import { Injectable } from '@angular/core';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root',
})
export class UtilMomentService {
  /**
   * Used to add a location.
   * @param inp? moment.MomentInput
   * @param format?: moment.MomentFormatSpecification
   * @param language?: string
   * @param strict?: boolean
   * @returns moment.Moment
   */
  static new(
    inp?: moment.MomentInput,
    format?: moment.MomentFormatSpecification,
    language?: string,
    strict?: boolean
  ): moment.Moment {
    return moment(inp, format, language, strict);
  }

  /**
   * Used to convert utc iso date string to local.
   * @param isoUTCDateString The isoDateString formatted as 'YYYY-MM-DDTHH:mm:ss.SS[Z]'
   * @returns Date object
   */
  static utcToLocal(isoUTCDateString: string): Date {
    return this.new(isoUTCDateString, 'YYYY-MM-DDTHH:mm:ss.SS[Z]')
      .utc()
      .local()
      .toDate();
  }
}
