import { Injectable } from '@angular/core';
import { HttpBackend, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { BaseApi } from '../environments/base-api';
import { UtilLocalStorageService } from '@xenon/utils';
import { BaseResponse, ChangePasswordRequest, ChangePasswordFromEmailRequest, User } from '@xenon/api';
import { LoggedInUser, RefreshTokenRequest } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class ApiAuthenticationService extends BaseApi {
  private http: HttpClient;

  constructor(private handler: HttpBackend, private localStorageService: UtilLocalStorageService) {
    super();

    /**
     * Note: We are using http backend here to skip http interceptors for these calls.
     * None of the calls in this service need the 'accessToken' header added so we're skipping http interceptors so it doesn't get added.
     * If a call needs to have the token added, it should be placed in a different service that uses HttpClient directly.
     */
    this.http = new HttpClient(this.handler);
  }

  public login(email: string, password: string, locationID: number): Observable<any> {
    const url = `${this.baseUrl}/applogin`;
    const request = {
      Email: email,
      Key: password,
      LocationID: locationID,
    };
    return this.http.post<any>(url, request).pipe(
      map((response: any) => {
        localStorage.setItem('token', response.Token);
        return response;
      })
    );
  }

  public logout(): Observable<BaseResponse> {
    const currentUser = this.localStorageService.getItem('user').CurrentUser;
    const url = `${this.baseUrl}/logout`;
    const request = {
      Email: currentUser.Email,
    };
    return this.http.post<BaseResponse>(url, request).pipe(
      map((response: BaseResponse) => {
        this.localStorageService.removeItem('user');
        this.localStorageService.removeItem('token');
        return response;
      })
    );
  }

  public authorized(): Observable<BaseResponse> {
    const url = `${this.baseUrl}/authorized`;
    return this.http.get<BaseResponse>(url).pipe(map((response: BaseResponse) => response));
  }

  public resetPassword(email: string): Promise<BaseResponse> {
    const url = `${this.baseUrl}/resetkey/${email}`;
    return this.http.get<BaseResponse>(url).toPromise();
  }

  public changePassword(request: ChangePasswordRequest): Promise<BaseResponse> {
    const url = `${this.baseUrl}/changepassword`;
    return this.http.post<BaseResponse>(url, request).toPromise();
  }

  public changePasswordFromEmail(request: ChangePasswordFromEmailRequest): Promise<BaseResponse> {
    const url = `${this.baseUrl}/changepasswordfromemail`;
    return this.http.post<BaseResponse>(url, request).toPromise();
  }

  public refreshToken(request: RefreshTokenRequest): Promise<RefreshTokenRequest> {
    const url = `${this.baseUrl}/token/refresh`;
    return this.http.post<RefreshTokenRequest>(url, request).toPromise();
  }

  public acceptUserTerms(userID: number): Promise<BaseResponse> {
    const url = `${this.baseUrl}/acceptuserterms/${userID}`;
    return this.http.get<BaseResponse>(url).toPromise();
  }
}
