import { ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { HttpErrorResponse, HttpEventType } from '@angular/common/http';
import { catchError, map, switchMap } from 'rxjs/operators';
import { forkJoin, Observable, of } from 'rxjs';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService, UtilStringService } from '@xenon/utils';

export interface TargetFile {
  data: any;
  inProgress: boolean;
  progress: number;
  url?: string;
}

@Component({
  selector: 'xenon-task-form-field-file',
  styleUrls: ['./task-form-field-file.component.scss'],
  template: `
    <div>
      <div>{{ formItem.Label }}</div>

      <button mat-button (click)="onClick()">
        <mat-icon>file_upload</mat-icon>
        Upload
      </button>

      <ul>
        <li *ngFor="let fileUrl of uploadedFiles">
          <a href="{{ fileUrl }}" target="_blank"> {{ fileUrl }} </a>
        </li>
      </ul>

      <ul>
        <li *ngFor="let file of files" [class.display-none]="file.progress === 100">
          <mat-progress-bar [value]="file.progress"></mat-progress-bar>
          <span id="file-label">{{ file.data.name }} {{ file.progress }}</span>
        </li>
      </ul>

      <input type="file" #fileUpload id="fileUpload" name="fileUpload" multiple="multiple" style="display:none;" />
    </div>
  `,
})
export class TaskFormFieldFileComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  @ViewChild('fileUpload', { static: false }) fileUpload: ElementRef;

  uploadedFiles: string[] = [];
  files: TargetFile[] = [];

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    const files = UtilStringService.isJson(this.formItem.Response) ? JSON.parse(this.formItem.Response) : undefined;
    this.uploadedFiles = files ? files : [];
  }

  onClick() {
    this.files = [];
    const fileUpload = this.fileUpload.nativeElement;
    fileUpload.onchange = () => {
      for (let index = 0; index < fileUpload.files.length; index++) {
        const file = fileUpload.files[index];
        this.files.push({ data: file, inProgress: false, progress: 0 });
      }
      this.uploadFiles();
    };
    fileUpload.click();
  }

  uploadFile(file: TargetFile): Observable<string | { body: { Uploaded: string } }> {
    const formData = new FormData();
    formData.append('formItemFile', file.data);
    file.inProgress = true;
    return this.formService.upload(this.formItem.FormID, formData).pipe(
      map((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          file.progress = Math.round((event.loaded * 100) / event.total);
          this.cd.detectChanges();
        }
        return event;
      }),
      catchError((error: HttpErrorResponse) => {
        file.inProgress = false;
        return of(`${file.data.name} upload failed.`);
      })
    );
  }

  private uploadFiles() {
    this.cd.detectChanges();
    this.fileUpload.nativeElement.value = '';
    const queue = [];

    this.files.forEach((file) => {
      queue.push(
        this.uploadFile(file).pipe(
          map((response: string | { body: { Uploaded: string } }) => {
            if (typeof response !== 'string' && response.body && response.body.Uploaded) {
              file.url = response.body.Uploaded;
              this.cd.detectChanges();
            }
            return response;
          })
        )
      );
    });

    forkJoin(queue)
      .pipe(
        switchMap((response) => {
          const filesList = [];
          response.forEach((res: string | { body: { Uploaded: string } }) => {
            if (typeof res !== 'string') {
              filesList.push(res.body.Uploaded);
            }
          });
          return this.submitPatch(JSON.stringify(filesList), this.formItem);
        })
      )
      .subscribe((response: { Message: string; formItem: FormItem }) => {
        const files = JSON.parse(response.formItem.Response);
        this.uploadedFiles = files ? files : [];
        this.formItem = response.formItem;
        this.cd.detectChanges();
      });
  }
}
