import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService, UtilMomentService, UtilStringService } from '@xenon/utils';

@Component({
  selector: 'xenon-task-form-field-date-range',
  styleUrls: ['./task-form-field-date-range.component.scss'],
  template: `
    <form [formGroup]="form">
      <mat-form-field appearance="fill">
        <mat-label>{{ formItem.Label }}</mat-label>
        <mat-date-range-input [rangePicker]="picker">
          <input matStartDate placeholder="Start date" formControlName="startDate" />
          <input matEndDate placeholder="End date" formControlName="endDate" />
        </mat-date-range-input>
        <mat-datepicker-toggle matSuffix [for]="picker"></mat-datepicker-toggle>
        <mat-date-range-picker #picker></mat-date-range-picker>
      </mat-form-field>
    </form>
  `,
})
export class TaskFormFieldDateRangeComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    const value =
      this.formItem.Response && UtilStringService.isJson(this.formItem.Response) ? JSON.parse(this.formItem.Response) : { start: undefined, end: undefined };
    const start = value.start ? UtilMomentService.new(value.start, 'YYYY-MM-DD').toDate() : undefined;
    const end = value.end ? UtilMomentService.new(value.end, 'YYYY-MM-DD').toDate() : undefined;
    this.form = new FormGroup({
      startDate: new FormControl(start, Validators.required),
      endDate: new FormControl(end, Validators.required),
    });

    this.form.valueChanges.subscribe((v) => {
      if (v && v.startDate && v.endDate && v.endDate > v.startDate) {
        const range: { start: string; end: string } = {
          start: null,
          end: null,
        };
        range.start = UtilMomentService.new(v.startDate).format('YYYY-MM-DD');
        range.end = UtilMomentService.new(v.endDate).format('YYYY-MM-DD');
        this.submitPatch(JSON.stringify(range), this.formItem).subscribe((response: { Message: string; formItem: FormItem }) => {
          this.formItem = response.formItem;
          this.cd.detectChanges();
        });
      }
    });
  }
}
