import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { SubTask, UpdateSubTaskNameRequest, UpdateSubTaskStatusRequest, ApiTaskTemplateService } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { take } from 'rxjs/operators';
import { MatSnackBar } from '@angular/material/snack-bar';
import { CdkDragDrop } from '@angular/cdk/drag-drop';

@Component({
  selector: 'xenon-complete-checklist-template-subtask-list',
  styleUrls: ['./complete-checklist-template-subtask-list.component.scss'],
  template: ` <div cdkDropList (cdkDropListDropped)="drop($event)">
    <div *ngFor="let subtask of sortedSubtaskList" fxLayout="row" fxLayoutAlign="start center" cdkDrag>
      <mat-icon class="grab mr-1" cdkDragHandle>drag_handle</mat-icon>
      <mat-form-field class="mat-form-field-alternate w-100">
        <input matInput [(ngModel)]="subtask.SubTaskDesc" (keyup.enter)="updateSubtaskName(subtask)" (blur)="updateSubtaskName(subtask)" />
      </mat-form-field>
      <button mat-icon-button [matMenuTriggerFor]="menu" aria-label="More options menu" class="ml-auto">
        <mat-icon>more_vert</mat-icon>
      </button>
      <mat-menu #menu="matMenu">
        <button mat-menu-item (click)="deleteSubTask(subtask)">
          <mat-icon>delete</mat-icon>
          <span>Delete</span>
        </button>
      </mat-menu>

      <mat-divider></mat-divider>
    </div>
    <div fxLayout="column">
      <div fxLayoutAlign="space-between center" *ngIf="showAddNewSubTask">
        <mat-form-field class="w-100" floatLabel="never">
          <input matInput #newSubTaskEl (keyup.enter)="addSubTask(newSubTaskEl, true)" (blur)="addSubTask(newSubTaskEl, false)" placeholder="Enter Subtask" />
        </mat-form-field>
        <mat-icon class="mx-3 clickable">done</mat-icon>
      </div>

      <button mat-button color="primary" class="p-0 text-left mt-12px" (click)="clickedShowAddNewSubTask()">
        <mat-icon class="mr-1">add_circle_outline</mat-icon>
        Add Subtask
      </button>
    </div>
  </div>`,
})
export class CompleteChecklistTemplateSubtaskListComponent {
  @Input() set subtaskList(subtaskList: SubTask[]) {
    this.sortedSubtaskList = subtaskList.sort((a, b) => {
      return a.DisplayOrder - b.DisplayOrder;
    });
  }
  @Input() taskID: number;
  @Output() refresh = new EventEmitter();
  @Output() newSubTaskList = new EventEmitter<SubTask[]>();
  @ViewChild('newSubTaskEl') newSubTaskEl: HTMLInputElement;
  @ViewChild('newSubTaskEl') newSubTaskElRef: ElementRef;
  public showAddNewSubTask = false;
  public currentUserID = this.localStorage.getItem('user').ID;
  public sortedSubtaskList: SubTask[];

  constructor(private taskTemplateService: ApiTaskTemplateService, private localStorage: UtilLocalStorageService, private snackBar: MatSnackBar) {}

  public updateSubtaskName(subtask: SubTask) {
    const request: UpdateSubTaskNameRequest = {
      ID: subtask.ID,
      SubTaskDesc: subtask.SubTaskDesc,
    };

    this.taskTemplateService
      .updateTaskTemplateSubtask(request)
      .pipe(take(1))
      .subscribe((result) => {
        if (result.Status === 'success') {
          this.snackBar.open('Subtask Updated', 'Close', { duration: 5000 });
        } else {
          this.showChecklistError(result.Message);
        }
      });
  }

  drop(event: CdkDragDrop<string[]>) {
    this.sortedSubtaskList.splice(event.currentIndex, 0, this.sortedSubtaskList.splice(event.previousIndex, 1)[0]);
    this.saveDisplayOrder();
  }

  emitList(): void {
    this.newSubTaskList.emit(this.sortedSubtaskList);
  }

  public saveDisplayOrder() {
    this.taskTemplateService
      .updateTaskTemplateSubtaskDisplayOrder(this.sortedSubtaskList)
      .pipe(take(1))
      .subscribe((result) => {
        console.log(result);
      });
  }

  public deleteSubTask(subtask: SubTask) {
    this.taskTemplateService
      .deleteTaskTemplateSubtask(subtask.ID)
      .pipe(take(1))
      .subscribe((result) => {
        if (result.Status === 'success') {
          this.sortedSubtaskList = this.sortedSubtaskList.filter((task) => task.ID !== subtask.ID);
          this.snackBar.open('Subtask Deleted', 'Close', { duration: 5000 });
          this.saveDisplayOrder();
        } else {
          this.showChecklistError(result.Message);
        }
      });
  }

  public addSubTask(newSubTaskEl, showAddNewSubTask: boolean) {
    if (!newSubTaskEl.value) {
      this.showAddNewSubTask = false;
      return;
    }

    const subTask: UpdateSubTaskStatusRequest = {
      TaskTemplateID: this.taskID,
      SubTaskDesc: newSubTaskEl.value,
      CreatedBy: this.currentUserID,
      DisplayOrder: this.sortedSubtaskList.length,
    };

    this.taskTemplateService
      .createTaskTemplateSubtask(subTask)
      .pipe(take(1))
      .subscribe((result) => {
        newSubTaskEl.value = ''; // reset input
        this.showAddNewSubTask = showAddNewSubTask; // hide 'add new' input

        const newSubTaskObject = {
          ID: result.NewTaskTemplateSubTask,
          TaskID: subTask.TaskID,
          SubTaskDesc: subTask.SubTaskDesc,
          CompletedAt: null,
          CreatedBy: this.currentUserID,
          DisplayOrder: this.sortedSubtaskList.length,
        };
        this.sortedSubtaskList ? this.sortedSubtaskList.push(newSubTaskObject) : (this.sortedSubtaskList = [newSubTaskObject]);
        this.emitList();
      });
  }

  public clickedShowAddNewSubTask(): void {
    this.showAddNewSubTask = true;
    setTimeout(() => this.newSubTaskElRef.nativeElement.focus(), 200);
  }

  private showChecklistError(message: string): void {
    this.snackBar.open(message, 'Error', { duration: 5000 });
  }
}
