import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ApiLocationService, ApiRoleService, Role, Location, RoleLocations, User, ApiUserService } from '@xenon/api';
import { UiListSelectDialogService } from '../../ui-list-select/ui-list-select.service';
import { BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';
import { UtilLocalStorageService } from '@xenon/utils';
@Component({
  selector: 'xenon-roles',
  styleUrls: ['./roles.component.scss'],
  template: `<h6>Roles</h6>

    <div *ngIf="roleLocationObjects | async as roleLocationObjects" class="mt-2">
      <div class="list-row" fxLayoutAlign="space-between center" *ngFor="let roleLocationObj of roleLocationObjects">
        <div fxLayoutAlign="start center">
          <mat-icon>person_outline</mat-icon>
          <p class="mb-0 text-body1 ml-3">{{ roleLocationObj.RoleName }}</p>
        </div>
        <span fxLayoutAlign="start center">
          <button mat-button (click)="openLocationDialog(roleLocationObj)">{{ getLocationString(roleLocationObj.LocationList) }}</button>
          <button class="remove-button" (click)="removeRole(roleLocationObj.RoleID)" mat-icon-button>
            <mat-icon>cancel</mat-icon>
          </button>
        </span>
      </div>
    </div>
    <button mat-button (click)="openRoleDialog()" class="text-left color-primary mt-3 p-0">
      <mat-icon class="mr-3">add_circle_outline</mat-icon>
      Add Role
    </button>`,
})
export class RolesComponent implements OnInit {
  @Input() user: User;
  @Input() currentOrg: number;

  public roleLocationObjects: BehaviorSubject<RoleLocations[]> = new BehaviorSubject(null);
  private locationList: Location[];
  private roleList: Role[];

  constructor(
    private uiListSelectService: UiListSelectDialogService<Role | Location>,
    private apiLocationService: ApiLocationService,
    private apiRoleService: ApiRoleService,
    private apiUserService: ApiUserService,
    private localStorage: UtilLocalStorageService
  ) {}

  public ngOnInit(): void {
    this.refreshData();
    this.apiRoleService
      .getRolesByOrg(this.currentOrg)
      .pipe(take(1))
      .subscribe((response) => {
        this.roleList = response.OrgRoleList;
      });
    this.apiLocationService
      .getLocationsByOrgId(this.currentOrg)
      .pipe(take(1))
      .subscribe((locations) => {
        this.locationList = locations;
      });
  }

  public openRoleDialog(): void {
    let oldSelectedRoles;
    if (this.roleLocationObjects.value) {
      oldSelectedRoles = this.roleList.filter((role) => {
        return this.roleLocationObjects.value.some((obj) => obj.RoleID === role.ID);
      });
    }
    this.uiListSelectService.open({
      inputList: this.roleList,
      currentSelectedList: oldSelectedRoles,
      primaryValueSelector1: 'Name',
      compareValueBy: 'ID',
      iconClass: 'person_outline',
    });

    this.uiListSelectService.closed().subscribe((selectedRoles: Role[]) => {
      if (!selectedRoles) return;

      let newSelectedRoleLocations;
      let oldSelectedRoleLocations = [];

      let newRoles;
      if (this.roleLocationObjects.value) {
        newRoles = selectedRoles.filter((role) => {
          return !this.roleLocationObjects.value.some((obj) => obj.RoleID === role.ID);
        });
      } else {
        newRoles = selectedRoles;
      }
      newSelectedRoleLocations = this.formatRoleIntoRoleLocation(newRoles);

      if (this.roleLocationObjects.value) {
        oldSelectedRoleLocations = this.roleLocationObjects.value.filter((obj) => selectedRoles.some((role) => role.ID === obj.RoleID));
      }

      this.roleLocationObjects.next([...oldSelectedRoleLocations, ...newSelectedRoleLocations]);

      if (newSelectedRoleLocations.length > 0) {
        this.apiLocationService.getLocationsByOrgId(this.currentOrg).subscribe((locations) => {
          this.uiListSelectService.open({
            inputList: locations,
            primaryValueSelector1: 'Name',
            compareValueBy: 'ID',
            iconClass: 'domain',
          });

          this.uiListSelectService.closed().subscribe((selectedLocations: Location[]) => {
            if (!selectedLocations) return;

            newSelectedRoleLocations.forEach((role) => {
              this.addRemoveLocations(role.RoleID, selectedLocations);
            });
          });
        });
      }
    });
  }

  public openLocationDialog(roleLocationObj: RoleLocations): void {
    const oldSelectedLocations = this.locationList.filter((location) => {
      return roleLocationObj.LocationList.some((locRole) => locRole.ID === location.ID);
    });
    this.uiListSelectService.open({
      inputList: this.locationList,
      currentSelectedList: oldSelectedLocations,
      primaryValueSelector1: 'Name',
      compareValueBy: 'ID',
      iconClass: 'domain',
    });

    this.uiListSelectService.closed().subscribe((selectedLocations: Location[]) => {
      this.addRemoveLocations(roleLocationObj.RoleID, selectedLocations);
    });
  }

  public getLocationString(locations: Location[]): string {
    if (!locations) return 'Add Location';
    return locations.length === 1 ? locations[0].Name : `${locations.length} Locations`;
  }

  public updateOrgUser() {
    const loggedInUser = this.localStorage.getItem('user');
    if (loggedInUser.CurrentUser.ID === this.user.ID) {
      const newUser = {
        ...loggedInUser.CurrentUser,
        Orgs: loggedInUser.CurrentUser.Orgs.map((org) =>
          org.OrgID === loggedInUser.CurrentOrg.ID ? { ...org, RoleLocations: this.roleLocationObjects.value } : org
        ),
      };
      const newLoggedInUser = { ...loggedInUser, CurrentUser: newUser };
      this.localStorage.setItem('user', newLoggedInUser);
    }
    this.apiUserService
      .updateOrgUser({
        ID: 0, //This needs to be 0 to trigger using the UserID and OrgID on the backend.
        UserID: this.user.ID,
        OrgID: this.currentOrg,
        JobTitle: this.user.JobTitle,
        RoleLocations: this.formatForUpdateOrgUser(this.roleLocationObjects.value),
      })
      .pipe(take(1))
      .subscribe((_) => {
        // Open snackbar letting them know it updated
      });
  }

  public removeRole(roleID: number): void {
    this.roleLocationObjects.next(this.roleLocationObjects.value.filter((obj) => obj.RoleID !== roleID));
    this.updateOrgUser();
  }

  private formatRoleIntoRoleLocation(roles: Role[]): RoleLocations[] {
    let newArray = [];
    roles.forEach((roleItem) => {
      if (newArray.length === 0) {
        return (newArray = [
          {
            OrgID: roleItem.OrgID,
            XenUserID: roleItem.CreatedBy,
            RoleID: roleItem.ID,
            RoleName: roleItem.Name,
            LocationList: [],
          },
        ]);
      } else {
        newArray.push({
          OrgID: roleItem.OrgID,
          XenUserID: roleItem.CreatedBy,
          RoleID: roleItem.ID,
          RoleName: roleItem.Name,
          LocationList: [],
        });
      }
    });
    return newArray;
  }

  private formatForUpdateOrgUser(roleLocations: RoleLocations[]): [{ RoleID: number; LocationID: number }] {
    let newFormattedArray = null;
    roleLocations.forEach((roleLocation) => {
      roleLocation.LocationList.forEach((location) => {
        if (newFormattedArray) {
          newFormattedArray.push({ RoleID: roleLocation.RoleID, LocationID: location.ID });
        } else {
          newFormattedArray = [{ RoleID: roleLocation.RoleID, LocationID: location.ID }];
        }
      });
    });
    return newFormattedArray;
  }

  private addRemoveLocations(roleID: number, selectedLocations: Location[]): void {
    const newRoleLocationObjects = this.roleLocationObjects.value.map((obj) => {
      if (selectedLocations.length <= 0) return { ...obj, LocationList: [] };
      if (obj.RoleID === roleID) {
        return { ...obj, LocationList: selectedLocations };
      } else {
        return obj;
      }
    });
    this.roleLocationObjects.next(newRoleLocationObjects);
    this.updateOrgUser();
  }

  private refreshData(): void {
    this.apiUserService
      .getOrgUserLocation(this.currentOrg, this.user.ID)
      .pipe(take(1))
      .subscribe((roleLocationList) => {
        this.roleLocationObjects.next(roleLocationList.UserLocationRoles);
      });
  }
}
