import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TasksByDate, Task } from '@xenon/api';

@Component({
  selector: 'xenon-task-list-by-date',
  styleUrls: ['./task-list-by-date.component.scss'],
  template: `
    <div *ngIf="filteredEmptyTasksByDate.length > 0" class="h-100">
      <div class="task-list-by-date" fxLayout="row" *ngFor="let mTask of filteredEmptyTasksByDate">
        <div fxFlex="82px" class="date text-center" [id]="mTask.dateString" [class.active]="mTask.active" [class.date-is-today]="mTask.dateIsToday">
          <div class="title">
            {{
              mTask.dateString.split('-')[2] === '01'
                ? (mTask.dateString | amParse: 'YYYY-MM-DD' | amDateFormat: 'MMM')
                : (mTask.dateString | amParse: 'YYYY-MM-DD' | amDateFormat: 'ddd')
            }}
          </div>
          <div class="day clickable" *ngIf="mTask" (click)="clickedDay(mTask.dateString)">
            {{ mTask.dateString.split('-')[2] }}
          </div>
        </div>
        <div fxFlex class="tasks clickable">
          <div *ngFor="let task of mTask.tasks" class="clickable">
            <mat-card class="m-3 p-0" [ngClass]="{ 'task-selected': task.ID === selectedTaskID }"
              ><xenon-task-by-date [task]="task" (clickedItem)="clickedItem(task)"> </xenon-task-by-date
            ></mat-card>
          </div>
        </div>
      </div>
    </div>
    <div *ngIf="filteredEmptyTasksByDate.length === 0" class="h-100">No tasks. Select a different day.</div>
  `,
})
export class TaskListByDateComponent {
  @Input() set tasksByDate(tasksByDate: TasksByDate[]) {
    this.filteredEmptyTasksByDate = tasksByDate.filter((taskObject) => taskObject.tasks.length > 0);
  }
  @Input() set setTaskID(setTaskID: number) {
    this.selectedTaskID = setTaskID;
  }
  @Output() dayClicked = new EventEmitter<string>();
  @Output() itemClicked = new EventEmitter<Task>();

  filteredEmptyTasksByDate: TasksByDate[];
  selectedTaskID: number;

  clickedDay(dateString: string) {
    this.dayClicked.emit(dateString);
  }

  clickedItem(item: Task) {
    item.ID === this.selectedTaskID ? (this.selectedTaskID = null) : (this.selectedTaskID = item.ID);
    this.itemClicked.emit(item);
  }
}
