import { Component, Input } from '@angular/core';
import { User } from '@xenon/api';

@Component({
  selector: 'xenon-team-list',
  styles: [
    '.profile-initials { height: 30px; width: 30px; border-radius: 50%; background: #e6f5fe; padding: 10px; margin-right: 10px; }',
    '.mat-cell { height: 56px; border: none; }',
    '.mt--32 { margin-top: -32px; }',
  ],
  template: `
    <table mat-table [dataSource]="users" class="mat-elevation-z0">
      <ng-container matColumnDef="Name">
        <th mat-header-cell *matHeaderCellDef>Name</th>
        <td mat-cell *matCellDef="let element" fxLayoutAlign="start center">
          <xenon-ui-user-photo class="mt--32" [userArray]="[element]"></xenon-ui-user-photo>
          <p class="ml-3 mb-0">{{ element.FirstName }} {{ element.LastName }}</p>
        </td>
      </ng-container>

      <ng-container matColumnDef="JobTitle">
        <th mat-header-cell *matHeaderCellDef>Title</th>
        <td mat-cell *matCellDef="let element">
          {{ element.JobTitle?.trim().length ? element.JobTitle : 'Unknown' }}
        </td>
      </ng-container>

      <ng-container matColumnDef="Organization">
        <th mat-header-cell *matHeaderCellDef>Organization</th>
        <td mat-cell *matCellDef="let element">{{ element.Org?.Name }}</td>
      </ng-container>

      <ng-container matColumnDef="Email">
        <th mat-header-cell *matHeaderCellDef>Email</th>
        <td mat-cell *matCellDef="let element">{{ element.Email }}</td>
      </ng-container>

      <ng-container matColumnDef="Phone">
        <th mat-header-cell *matHeaderCellDef>Phone</th>
        <td mat-cell *matCellDef="let element">{{ element.Phone }}</td>
      </ng-container>

      <tr mat-header-row *matHeaderRowDef="displayedColumns"></tr>
      <tr mat-row *matRowDef="let row; columns: displayedColumns"></tr>
    </table>
  `,
})
export class TeamListComponent {
  displayedColumns: string[] = ['Name', 'JobTitle', 'Organization', 'Email', 'Phone'];

  @Input() users: User[];
}
