import { Component, OnDestroy, OnInit } from '@angular/core';
import { ApiFormService, ApiFormTemplateService, Form } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { AppBase } from '../../../app-base';
import { FormTemplate } from '@xenon/api';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { TaskFormComponent } from '../../../shared/modules/task-form/task-form.component';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'xenon-forms',
  styleUrls: ['./forms.component.scss'],
  template: `
    <div class="page-header" fxLayout="row" fxLayoutAlign="space-between center">
      <h5 class="title">Forms</h5>

      <form>
        <mat-form-field appearance="fill">
          <mat-label>Form Templates</mat-label>
          <mat-select (selectionChange)="updateFormList()" [(ngModel)]="selectedTemplate" name="template">
            <mat-option *ngFor="let template of formTemplates" [value]="template">
              {{ template.Name }}
            </mat-option>
          </mat-select>
        </mat-form-field>
      </form>
    </div>

    <div class="page-body">
      <xenon-forms-list [forms]="forms$ | async" (openEditForm)="openTaskFormComponent($event)"> </xenon-forms-list>

      <button
        [disabled]="!selectedTemplate"
        mat-fab
        mat-flat-button
        class="add-button"
        color="primary"
        aria-label="Create new file or folder"
        (click)="addNewForm()"
      >
        <mat-icon>add</mat-icon>
      </button>

      <div class="error" [hidden]="hideError">
        <p>{{ errorMessage }}</p>
      </div>
    </div>
  `,
})
export class FormsComponent extends AppBase implements OnInit, OnDestroy {
  public selectedTemplate: FormTemplate;
  public formTemplates: FormTemplate[];
  public forms$: BehaviorSubject<Form[]> = new BehaviorSubject(null);
  public hideError = true;
  public errorMessage: string;

  constructor(
    private localStorage: UtilLocalStorageService,
    private formTemplateService: ApiFormTemplateService,
    private bottomSheet: MatBottomSheet,
    private formService: ApiFormService
  ) {
    super(localStorage);
  }

  ngOnInit() {
    this.getFormTemplates();
  }

  ngOnDestroy() {
    this.subscriptions.forEach((s) => s.unsubscribe());
  }

  public addNewForm() {
    this.formService.createFormFromTemplateID(this.selectedTemplate?.ID).subscribe(
      (form: Form) => {
        this.openTaskFormComponent(form);
      },
      (err) => {
        this.showError(`An error occurred adding a new form`, 5000);
      }
    );
  }

  public updateFormList() {
    const sub = this.formService.getFormsByTemplate(this.selectedTemplate?.ID).subscribe(
      (formArray: Form[]) => {
        this.forms$.next(formArray);
      },
      (err) => {
        this.showError(`An error occurred retrieving the form list`, 5000);
      }
    );

    this.subscriptions.push(sub);
  }

  public openTaskFormComponent(form: Form) {
    const bottomSheet = this.bottomSheet
      .open(TaskFormComponent, {
        data: {
          form: form,
        },
        backdropClass: 'task-form-backdrop',
        panelClass: 'task-form-panel',
      })
      .afterDismissed()
      .subscribe((res) => {
        this.updateFormList();
      });

    this.subscriptions.push(bottomSheet);
  }

  private getFormTemplates(): void {
    this.formTemplateService.getFormTemplatesByOrg(this.organizationId).subscribe(
      (response: FormTemplate[]) => {
        this.formTemplates = response;
      },
      (err) => {
        this.showError(`An error occurred retrieving form templates.`);
      }
    );
  }

  private showError(message: string, milliseconds?: number): void {
    this.hideError = false;
    this.errorMessage = message;

    if (milliseconds) {
      setTimeout(() => {
        this.hideError = true;
      }, milliseconds);
    }
  }
}
