import { Component, Input, OnInit } from '@angular/core';
import { User } from '@xenon/api';

@Component({
  selector: 'xenon-ui-user-photo',
  styleUrls: ['./ui-user-photo.component.scss'],
  template: `
    <span *ngIf="userArray.length > 0" class="first-picture" [ngClass]="borderPrimary ? 'border-primary' : 'border-gray'">
      <img *ngIf="showPhoto" src="{{ userArray[0].PhotoURL }}" alt="user" (error)="hidePhoto()" class="profile-pic" />
      <span *ngIf="!showPhoto" class="profile-initials color-primary-dark background-primary-light text-center "
        >{{ userArray[0].FirstName[0] }}{{ userArray[0].LastName[0] }}</span
      >
    </span>
    <span *ngIf="userArray.length > 1" class="middle-picture" [ngClass]="borderPrimary ? 'border-primary' : 'border-gray'">
      <img *ngIf="userArray[1]?.PhotoURL.trim().length" src="{{ userArray[1].PhotoURL }}" alt="user" class="profile-pic" />
      <span *ngIf="!userArray[1]?.PhotoURL.trim().length" class="profile-initials color-primary-dark background-primary-light text-center"
        >{{ userArray[1].FirstName[0] }}{{ userArray[1].LastName[0] }}</span
      >
    </span>
    <span *ngIf="userArray.length > 2" class="last-picture" [ngClass]="borderPrimary ? 'border-primary' : 'border-gray'">
      <img *ngIf="userArray[2]?.PhotoURL.trim().length" src="{{ userArray[2].PhotoURL }}" alt="user" class="profile-pic" />
      <span *ngIf="!userArray[2]?.PhotoURL.trim().length" class="profile-initials color-primary-dark background-primary-light text-center"
        >{{ userArray[2].FirstName[0] }}{{ userArray[2].LastName[0] }}</span
      >
    </span>
  `,
})
export class UiUserPhotoComponent implements OnInit {
  @Input() userArray: User[];
  @Input() borderPrimary = true;
  public showPhoto: boolean;

  ngOnInit() {
    this.showPhoto = this.userArray[0]?.PhotoURL?.trim()?.length > 0;
  }

  public hidePhoto = () => (this.showPhoto = false);
}
