import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BaseResponse, UpdateSubTaskStatusResponse, CreateSubTaskResponse, SubTask } from '@xenon/api';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { BaseApi } from '../environments/base-api';
import {
  CreateTaskTemplateRequest,
  CreateTaskTemplateResponse,
  GetTaskTemplateByOrgResponse,
  GetTaskTemplateByTemplateIDResponse,
  TaskTemplate,
  UpdateTaskTemplateRequest,
} from '../models/task-template';

@Injectable({
  providedIn: 'root',
})
export class ApiTaskTemplateService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  /**
   * Gets task templates for a given organization.
   * @param orgID The organization's ID to get the task templates for.
   * @returns An observable containing an array of task templates.
   */
  public getTaskTemplatesByOrgID(orgID: number): Observable<TaskTemplate[]> {
    const url = `${this.baseUrl}/tasktemplatelist/${orgID}`;
    return this.http.get<GetTaskTemplateByOrgResponse>(url).pipe(map((response: GetTaskTemplateByOrgResponse) => response.TaskTemplateList));
  }

  /**
   * Gets a single task template.
   * @param taskTemplateID The ID of the task template to retrieve.
   * @returns An observable containing the requested task template.
   */
  public getTaskTemplateByTemplateID(taskTemplateID: number): Observable<TaskTemplate> {
    const url = `${this.baseUrl}/tasktemplate/${taskTemplateID}`;
    return this.http.get<GetTaskTemplateByTemplateIDResponse>(url).pipe(map((response: GetTaskTemplateByTemplateIDResponse) => response.TaskTemplate));
  }

  /**
   * Creates a new task template with the information passed in.
   * @param newTaskTemplate The information used to create a new task template.
   * @returns An observable containing the create task template response.
   */
  public createTaskTemplate(newTaskTemplate: CreateTaskTemplateRequest): Observable<CreateTaskTemplateResponse> {
    const url = `${this.baseUrl}/tasktemplate`;
    return this.http.post<CreateTaskTemplateResponse>(url, newTaskTemplate);
  }

  /**
   * Updates an existing task template by the ID passed in.
   * @param updatedTaskTemplate The ID of the task template and the information to update.
   * @returns An observable containing the base response model.
   */
  public updateTaskTemplate(updatedTaskTemplate: UpdateTaskTemplateRequest): Observable<BaseResponse> {
    const taskTemplateID = updatedTaskTemplate?.ID;
    const url = `${this.baseUrl}/tasktemplate/${taskTemplateID}`;
    return this.http.patch<BaseResponse>(url, updatedTaskTemplate);
  }

  /**
   * Updates an existing task template by the ID passed in.
   * @param updatedTaskTemplate The ID of the task template and the information to update.
   * @returns An observable containing the base response model.
   */
  public updateTaskTemplateBatch(updatedTaskTemplate: UpdateTaskTemplateRequest): Observable<BaseResponse> {
    const taskTemplateID = updatedTaskTemplate?.ID;
    const url = `${this.baseUrl}/tasktemplatebatch/${taskTemplateID}`;
    return this.http.patch<BaseResponse>(url, updatedTaskTemplate);
  }

  /**
   * Deletes an existing task template by the ID passed in.
   * @param taskTemplateID
   * @returns An observable containing the base response model.
   */
  public deleteTaskTemplate(taskTemplateID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/tasktemplate/${taskTemplateID}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * Creates a task template subtask.
   * @param taskTemplateID
   * @returns An observable containing the base response model.
   */
  public createTaskTemplateSubtask(subTask: any): Observable<CreateSubTaskResponse> {
    const url = `${this.baseUrl}/tasktemplatesubtask`;
    return this.http.post<CreateSubTaskResponse>(url, subTask);
  }

  /**
   * Updates an existing task template subtask.
   * @param taskTemplateSubtaskID
   * @returns An observable containing the base response model.
   */
  public updateTaskTemplateSubtask(subTask: any): Observable<BaseResponse> {
    const url = `${this.baseUrl}/tasktemplatesubtask/${subTask.ID}`;
    return this.http.patch<BaseResponse>(url, subTask);
  }

  /**
   * Deletes an existing task template subtask by the ID passed in.
   * @param taskTemplateSubtaskID
   * @returns An observable containing the base response model.
   */
  public deleteTaskTemplateSubtask(taskTemplateSubtaskID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/tasktemplatesubtask/${taskTemplateSubtaskID}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * Update an existing task template subtasks display order by the ID passed in.
   * @param taskTemplateSubtaskID
   * @returns An observable containing the base response model.
   */
  public updateTaskTemplateSubtaskDisplayOrder(subtasks: SubTask[]): Observable<BaseResponse> {
    const url = `${this.baseUrl}/tasktemplatesubtaskdisplayorder`;
    return this.http.post<BaseResponse>(url, subtasks);
  }
}
