import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiOrganizationService, Organization } from '@xenon/api';
import { NotificationType, UiNotificationService } from '@xenon/ui';
import { take } from 'rxjs/operators';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-edit-organization-name-dialog',
  styleUrls: ['./edit-organization-name-dialog.component.scss'],
  template: `<h6>Organization Name</h6>
    <mat-form-field floatLabel="never">
      <input matInput [(ngModel)]="data.Name" />
    </mat-form-field>
    <div fxLayoutAlign="end center">
      <button mat-button color="primary" (click)="close()">Cancel</button>
      <button mat-button color="primary" (click)="update()">Update</button>
    </div> `,
})
export class EditOrganizationNameDialogComponent {
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: Organization,
    public dialogRef: MatDialogRef<EditOrganizationNameDialogComponent>,
    private apiOrganizationService: ApiOrganizationService,
    private notificationService: UiNotificationService,
    private localStorage: UtilLocalStorageService
  ) {}

  public close(): void {
    this.dialogRef.close();
  }

  public update(): void {
    this.apiOrganizationService
      .updateOrg(this.data.OrgID, { ID: this.data.OrgID, Name: this.data.Name })
      .pipe(take(1))
      .subscribe((result) => {
        if (result.Status === 'success') {
          const currentUser = this.localStorage.getItem('user').CurrentUser;
          const newLocalItem = {
            ...currentUser,
            Orgs: currentUser.Orgs.map((userOrg) => {
              if (userOrg.OrgID === this.data.OrgID) {
                return { ...userOrg, Org: { ...userOrg.Org, Name: this.data.Name } };
              } else {
                return userOrg;
              }
            }),
          };
          let newLoggedInUser = this.localStorage.getItem('user');
          newLoggedInUser = { ...newLoggedInUser, CurrentUser: newLocalItem };
          this.localStorage.setItem('user', newLoggedInUser);
          this.dialogRef.close();
        } else {
          this.notificationService.show(NotificationType.error, 'ERROR', result.Message, 2500);
        }
      });
  }
}
