import { Component, Inject, OnDestroy, OnInit, ChangeDetectorRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import {
  ApiUserService,
  BaseResponse,
  ChangePasswordRequest,
  Role,
  Location,
  UpdateUserRequest,
  UploadPhotoResponse,
  User,
  ApiAuthenticationService,
  LoggedInUser,
} from '@xenon/api';
import { UtilCloneService, UtilLocalStorageService } from '@xenon/utils';
import { USER_FORM_FIELDS } from './user-form.data';
import { BehaviorSubject, of, Subscription } from 'rxjs';
import { flatMap, take } from 'rxjs/operators';
import { FormGroup } from '@angular/forms';
import { UiFormFieldTableComponent } from '../../ui-form/components/ui-form-field-table/ui-form-field-table.component';
import { UiFormFieldInput } from '../../ui-form/models/ui-form-field-input';
import { UiFormFieldSelect } from '../../ui-form/models/ui-form-field-select';
import { UiFormFieldTextArea } from '../../ui-form/models/ui-form-field-text-area';
import { UiPromptService } from '../../modules/ui-prompt/services/ui-prompt.service';
import { NotificationType, UiNotificationService } from '../../services/ui-notification.service';
import { UiFormService } from '../../ui-form/services/ui-form.service';
import { ChangePasswordComponent, ChangePasswordData } from './change-password/change-password.component';
import { UiConfirmationComponent } from '../ui-confirmation/ui-confirmation.component';
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'xenon-ui-user-form',
  styleUrls: ['./ui-user-form.component.scss'],
  templateUrl: './ui-user-form.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class UIUserFormComponent implements OnInit, OnDestroy {
  @ViewChild(UiFormFieldTableComponent) uiFormFieldTableComponent: UiFormFieldTableComponent;
  public user = ([] as unknown) as User;
  public accessLevels = ['Administrator', 'Editor', 'Viewer', 'Owner'];
  public tableHeaders = {
    LocationName: 'Location',
    RoleName: 'Role',
    Delete: 'Delete',
  };
  public addRoleError = false;
  public deleteRoleError = false;
  public currentTab: BehaviorSubject<string> = new BehaviorSubject('account');
  public form: FormGroup;
  public fieldsAsObject: object;
  public fields: (UiFormFieldInput | UiFormFieldSelect | UiFormFieldTextArea)[];
  public subscriptions = new Subscription();
  public loggedInUser: LoggedInUser;
  public showPhoto: boolean;
  orgLocations: any[];

  public layoutOptions = {
    sideNavMenuOptions: [
      {
        selector: 'account',
        title: 'Account',
        icon: 'admin_panel_settings',
      },
      {
        selector: 'profile',
        title: 'Profile',
        icon: 'account_circle',
      },
      {
        selector: 'permissions',
        title: 'Permissions',
        icon: 'assignment_ind',
      },
    ],
    pageTitle: 'Edit Location',
  };

  constructor(
    @Inject(MAT_DIALOG_DATA)
    public data: { user: User; refresh: () => {}; roles: Role[]; locations: Location[] },
    public uiPromptService: UiPromptService,
    public notificationService: UiNotificationService,
    private dialogRef: MatDialogRef<UIUserFormComponent>,
    private apiUserService: ApiUserService,
    private authService: ApiAuthenticationService,
    private utilLocalStorageService: UtilLocalStorageService,
    private cdr: ChangeDetectorRef,
    public dialog: MatDialog,
    public snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.fields = UtilCloneService.deepClone(USER_FORM_FIELDS);
    this.loggedInUser = this.utilLocalStorageService.getItem('user');
    this.showPhoto = this.user?.PhotoURL?.trim()?.length > 0;
    this.orgLocations = this.data.locations;

    this.setForm();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  public hidePhoto = () => (this.showPhoto = false);

  public close(): void {
    if (this.form.pristine) {
      this.dialogRef.close();
      return;
    }
    if (this.form.valid) this.updateUser();
    else this.openPrompt();
  }

  public openChangePassword() {
    const dialogRef = this.dialog.open(ChangePasswordComponent, {
      width: '250px',
      data: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: '',
      },
    });

    dialogRef.afterClosed().subscribe((data: ChangePasswordData) => {
      if (data) {
        const request: ChangePasswordRequest = {
          email: this.user.Email,
          password: data.currentPassword,
          newpassword: data.newPassword,
          newpassword2: data.confirmPassword,
        };

        this.authService
          .changePassword(request)
          .then((response: BaseResponse) => {
            this.notificationService.show(NotificationType.success, 'Action done', 'Password Updated', 5000);
          })
          .catch((err) => {
            this.notificationService.show(NotificationType.error, 'Error', `${err?.error?.Error}`, 5000);
          });
      }
    });
  }

  public onSelectProfilePic(event): void {
    if (event.target.files && event.target.files[0]) {
      const photoToSave: File = event.target.files[0];

      this.apiUserService.uploadPhoto(photoToSave, this.user.ID.toString()).subscribe((response: UploadPhotoResponse) => {
        this.user.PhotoURL = response.Uploaded;
        this.form.patchValue({ PhotoURL: response.Uploaded });
        this.cdr.detectChanges();
        this.data.refresh();
      });
    }
  }

  public deleteUser() {
    const confirmationDialog = this.dialog.open(UiConfirmationComponent, {
      data: {
        title: 'Delete User?',
        description: 'This will permanently delete the user.',
        acceptMessage: 'Delete',
        declineMessage: 'Cancel',
      },
    });

    confirmationDialog
      .afterClosed()
      .pipe(take(1))
      .subscribe((result) => {
        if (result) {
          this.subscriptions.add(
            this.apiUserService.deleteUser(this.loggedInUser.CurrentOrg.ID, this.user.ID).subscribe((response: BaseResponse) => {
              if (response.Status === 'success') {
                this.snackBar.open('User deleted', 'Close', {
                  duration: 5000,
                });
                this.data.refresh();
              } else {
                this.snackBar.open(response.Message, 'Close', { duration: 5000 });
              }
            })
          );
        }
      });
  }

  public openUnsavedChangesPrompt(dialogRef: MatDialogRef<any>, prompt: string, stayMessage: string, showValidationErrors = false) {
    const sub = this.uiPromptService
      .prompt(prompt)
      .afterClosed()
      .pipe(
        flatMap((r: { ok: boolean }) => {
          if (r.ok) {
            if (showValidationErrors) this.showValidationErrors();
            return of({ Message: stayMessage });
          } else {
            dialogRef.close();
            return of({ Message: 'Changes not saved.' });
          }
        })
      )
      .subscribe((m) => {
        this.notificationService.show(NotificationType.success, '', m.Message, 2500);
      });

    this.subscriptions.add(sub);
  }

  private updateUser(): void {
    const request: UpdateUserRequest = {
      ID: this.user.ID,
    };

    this.fields.forEach((field) => {
      const formField = this.form.controls[field.name];
      if (formField.touched && !formField.pristine && formField.valid && field.name !== 'PhotoUpload') {
        request[field.name] = formField.value;
      }
    });
    if (this.form.touched && !this.form.pristine) {
      if (this.user.ID === this.loggedInUser.CurrentUser.ID) {
        let newLoggedInUser = this.utilLocalStorageService.getItem('user');
        newLoggedInUser = { ...newLoggedInUser, CurrentUser: { ...this.loggedInUser.CurrentUser, ...request } };
        this.utilLocalStorageService.setItem('user', newLoggedInUser);
      }
      const sub = this.apiUserService.updateUser(request).subscribe(
        (response: BaseResponse) => {
          if (response.Status === 'error') {
            const prompt = `An error occurred updating user. Error: ${response.Message}. Would you like to stay on the page?`;
            const stayMessage = response.Message;
            this.openUnsavedChangesPrompt(this.dialogRef, prompt, stayMessage);
          } else {
            this.dialogRef.close();
            this.data.refresh();
          }
        },
        (err) => {
          const prompt = `An error occurred updating user. Error: ${err?.error?.Error}. Would you like to stay on the page?`;
          const stayMessage = err;
          this.openUnsavedChangesPrompt(this.dialogRef, prompt, stayMessage);
        }
      );

      this.subscriptions.add(sub);
    } else {
      this.dialogRef.close();
    }
  }

  private setForm() {
    this.user = this.data.user;
    this.fields.forEach((field) => (field.value = this.user[field.name]));

    this.fieldsAsObject = UiFormService.buildFieldsAsObject(this.fields);
    this.fieldsAsObject['LocationNames'].options = this.orgLocations
      ? this.orgLocations.map((location: any) => {
          return { value: location.ID, label: location.Name };
        })
      : [];

    this.fieldsAsObject['Roles'].options = this.data.roles
      ? this.data.roles.map((role) => {
          return { value: role.ID, label: role.Name };
        })
      : [];

    this.form = UiFormService.buildForm(this.fields);

    // Password should only be required when creating a new user.
    this.form.get('Password').clearValidators();
    this.form.get('Password').updateValueAndValidity();
  }

  private openPrompt() {
    const prompt = `You have unsaved changes. Would you like to stay on the page and fix errors?`;
    const stayMessage = 'Fix errors.';
    this.openUnsavedChangesPrompt(this.dialogRef, prompt, stayMessage, true);
  }

  private showValidationErrors(): void {
    this.form.markAllAsTouched();
    Object.keys(this.form.controls).forEach((key) => {
      this.form.get(key).updateValueAndValidity();
    });
  }
}
