import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'xenon-ui-date-range-picker',
  styleUrls: ['./ui-date-range-picker.component.scss'],
  template: `
    <mat-form-field appearance="fill">
      <mat-label>Date range</mat-label>
      <mat-date-range-input [formGroup]="range" [rangePicker]="picker">
        <input matStartDate formControlName="start" placeholder="Start date" />
        <input matEndDate formControlName="end" placeholder="End date" />
      </mat-date-range-input>
      <mat-datepicker-toggle matSuffix [for]="picker"></mat-datepicker-toggle>
      <mat-date-range-picker #picker></mat-date-range-picker>
    </mat-form-field>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class UiDateRangePickerComponent implements OnInit {
  @Input() dateRange: { start: Date; end: Date };
  @Output() rangeChange = new EventEmitter<{ start: Date; end: Date }>();

  range = new FormGroup({
    start: new FormControl(),
    end: new FormControl(),
  });

  constructor() {}

  ngOnInit(): void {
    this.range.controls.start.setValue(this.dateRange.start);
    this.range.controls.end.setValue(this.dateRange.end);
    this.range.valueChanges.subscribe((x: { start: Date; end: Date }) => {
      if (x.start && x.end) {
        this.rangeChange.emit(x);
      }
    });
  }
}
