import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { switchMap } from 'rxjs/operators';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-task-form-field-radio',
  styleUrls: ['./task-form-field-radio.component.scss'],
  template: `
    <form [formGroup]="form">
      <label>{{ formItem.Label }}</label>
      <div>
        <mat-radio-group aria-label="Select an option" formControlName="inputItem">
          <mat-radio-button class="mr-3" name="inputItem" [value]="option.Label" *ngFor="let option of formItem.FormItemOptions">
            {{ option.Label }}
          </mat-radio-button>
        </mat-radio-group>
      </div>
    </form>
  `,
})
export class TaskFormFieldRadioComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    this.form = new FormGroup({
      inputItem: new FormControl(this.formItem.Response, Validators.required),
    });

    this.form.valueChanges
      .pipe(switchMap((v) => this.submitPatch(v.inputItem, this.formItem)))
      .subscribe((response: { Message: string; formItem: FormItem }) => {
        this.formItem = response.formItem;
        this.cd.detectChanges();
      });
  }
}
