import { Component, Input, Output, EventEmitter } from '@angular/core';
import { DriveItem, BaseResponse, ApiDriveService } from '@xenon/api';
import { MatDialog } from '@angular/material/dialog';
import { TextFileComponent } from '../text-file/text-file.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { PermissionsHelperService } from '@xenon/auth';
import { RenameDriveItemModal } from '../../../features/client/drive/drive-explorer/rename-drive-item.modal';

export type DriveMenuItems = 'edit' | 'copy' | 'rename' | 'starred' | 'permissions' | 'download' | 'delete';

@Component({
  selector: 'xenon-drive-vert-menu',
  template: `
    <button
      mat-menu-item
      *ngIf="item.details.ContentType === 2 && !hideItems.includes('edit') && permissionsHelperService.hasPermissionByName('editFiles')"
      (click)="editTextFile(item)"
    >
      <mat-icon fontSet="material-icons-outlined">insert_drive_file</mat-icon>
      <span>Edit File</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('copy')" (click)="makeCopy(item)" disabled>
      <mat-icon>content_copy</mat-icon>
      <span>Make a copy</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('rename') && permissionsHelperService.hasPermissionByName('editFiles')" (click)="rename(item)">
      <mat-icon>create</mat-icon>
      <span>Rename</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('starred')" (click)="addToStarred(item)" disabled>
      <mat-icon>star</mat-icon>
      <span>Add to Starred</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('permissions')" (click)="managePermissions(item)" disabled>
      <mat-icon>people_alt</mat-icon>
      <span>Manage Permissions</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('download')" (click)="downloadFile(item)" [hidden]="item?.details?.ContentType == 1">
      <mat-icon>get_app</mat-icon>
      <span>Download</span>
    </button>
    <button mat-menu-item *ngIf="!hideItems.includes('delete') && permissionsHelperService.hasPermissionByName('deleteFiles')" (click)="deleteDriveItem(item)">
      <mat-icon>delete_outline</mat-icon>
      <span>Delete</span>
    </button>
  `,
  styleUrls: ['./drive-vert-menu.component.scss'],
})
export class DriveVertMenuComponent {
  @Input() item: DriveItem;
  @Input() hideItems: DriveMenuItems[] = [];
  @Output() refresh = new EventEmitter<string>(); //getdriveitems

  constructor(
    private driveService: ApiDriveService,
    private dialog: MatDialog,
    private snackbar: MatSnackBar,
    public permissionsHelperService: PermissionsHelperService
  ) {}

  public editTextFile(item: DriveItem) {
    const dialogRef = this.dialog.open(TextFileComponent, {
      data: { driveItem: item, editFile: true },
      panelClass: 'full-page-dialog',
    });

    dialogRef.afterClosed().subscribe(() => {
      this.refresh.emit('editClosed');
    });
  }

  public rename(item: DriveItem) {
    const dialogRef = this.dialog.open(RenameDriveItemModal, {
      data: { newName: '' },
    });

    dialogRef.afterClosed().subscribe((newName) => {
      // note: new name will be undefined if the user doesn't click 'SAVE'
      if (newName) {
        this.renameDriveItem({ id: item.id, name: newName });
      }
    });
  }

  public deleteDriveItem(item: DriveItem) {
    this.driveService
      .deleteDriveItem(item.id)
      .then((_) => {
        this.refresh.emit('delete');
      })
      .catch((_) => {
        this.snackbar.open('Error deleting drive item', 'Close', { duration: 5000 });
      });
  }

  public downloadFile(item: DriveItem) {
    this.driveService
      .downloadFile(item.details.ContentPath)
      .then((response: Blob) => {
        const blob: any = new Blob([response], {
          type: 'text/json; charset=utf-8',
        });
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.setAttribute('target', '_self');
        link.setAttribute('href', url);
        link.setAttribute('download', item.name);
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((_) => {
        this.snackbar.open('Error downloading file', 'Close', { duration: 5000 });
      });
  }

  private renameDriveItem(info: { id: number; name: string }) {
    this.driveService
      .renameDriveItem(info.id, info.name)
      .then((_) => {
        this.refresh.emit('rename');
      })
      .catch((_) => {
        this.snackbar.open('Error renaming drive item', 'Close', { duration: 5000 });
      });
  }
}
