import { Injectable } from '@angular/core';
import { Task } from '@xenon/api';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class TaskByDateService {
  taskSelected: BehaviorSubject<Task> = new BehaviorSubject(null);
  nextTask(task: Task) {
    this.taskSelected.next(task);
  }
}
