import { Pipe, PipeTransform } from '@angular/core';
import { FormItemType, ItemTypes } from '@xenon/layouts';

@Pipe({
  name: 'formItemTypeDisplay',
})
export class FormItemTypeDisplayPipe implements PipeTransform {
  transform(value: unknown, ...args: unknown[]): unknown {
    let formItemTypeValue = '';
    switch (value) {
      case 1:
      case 2:
        formItemTypeValue = ItemTypes.text.key;
        break;

      case 5:
        formItemTypeValue = FormItemType.number.key;
        break;

      case 6:
      case 7:
      case 9:
      case 10:
      case 11:
      case 12:
        formItemTypeValue = FormItemType.dateTime.key;
        break;

      case 3:
      case 4:
        formItemTypeValue = ItemTypes.multipleChoice.key;
        break;

      case 8:
        formItemTypeValue = ItemTypes.multimedia.key;
        break;

      default:
        break;
    }
    return formItemTypeValue;
  }
}
