import { Component, OnDestroy, ViewEncapsulation, Output, EventEmitter, Input } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { BehaviorSubject, Subscription } from 'rxjs';

import {
  ApiLocationService,
  ApiOrganizationService,
  ApiUserService,
  BaseResponse,
  OrganizationList,
  UpdateUserPrimarySettingsRequest,
  User,
  LoggedInUser,
} from '@xenon/api';
import { NotificationType, UiNotificationService, UiConfirmationComponent } from '@xenon/ui';
import { UtilLocalStorageService } from '@xenon/utils';
import { take } from 'rxjs/operators';
import * as moment from 'moment';
import 'moment-timezone';
import { MatSnackBar } from '@angular/material/snack-bar';
import { EditOrganizationNameDialogComponent } from './edit-organization-name-dialog/edit-organization-name-dialog.component';
import { EditOrganizationLogoDialogComponent } from './edit-organization-logo-dialog/edit-organization-logo-dialog.component';

@Component({
  selector: 'xenon-switch-organization',
  templateUrl: './switch-organization.component.html',
  styleUrls: ['./switch-organization.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SwitchOrganizationComponent implements OnDestroy {
  @Input() set loadData(loadData: boolean) {
    if (loadData) {
      this.setOrgObjects();
    }
  }
  @Output() updateName = new EventEmitter<string>();
  public loggedInUser: LoggedInUser;
  public otherOrg: BehaviorSubject<OrganizationList[]> = new BehaviorSubject(null);
  public currentOrg: BehaviorSubject<OrganizationList> = new BehaviorSubject(null);
  public loginSubscription: Subscription;

  constructor(
    private localStorage: UtilLocalStorageService,
    private userService: ApiUserService,
    public notificationService: UiNotificationService,
    public dialog: MatDialog,
    private apiOrganizationService: ApiOrganizationService,
    private apiLocationService: ApiLocationService,
    private snackBar: MatSnackBar
  ) {}

  ngOnDestroy(): void {
    if (this.loginSubscription) {
      this.loginSubscription.unsubscribe();
    }
  }

  switchLocation(organizationID: number, locationID: number): void {
    this.apiLocationService
      .getLocation(locationID)
      .pipe(take(1))
      .subscribe((locationObj) => {
        if (locationObj && locationObj.Timezone) moment.tz.setDefault(locationObj.Timezone);
      });

    const request: UpdateUserPrimarySettingsRequest = {
      ID: this.loggedInUser.CurrentUser.ID,
      OrgID: organizationID,
      LocationID: locationID,
    };

    this.userService
      .updateUserPrimarySettings(request)
      .then((_) => {})
      .catch((err) => {
        this.notificationService.show(NotificationType.error, 'Error', 'Cannot set primary location.', 2500);
      });

    location.reload();
    // this.apiLocationService
    //   .switchLocation(locationID)
    //   .pipe(take(1))
    //   .subscribe((loggedInUser) => {
    //     this.localStorage.setItem('user', loggedInUser);
    //     location.reload();
    //   });
  }

  handleEvent(type: string, org: OrganizationList): void {
    switch (type) {
      case 'starred':
        this.starOrg(org);
        break;
      case 'name':
        this.editOrgName(org);
        break;
      case 'logo':
        this.editOrgLogo(org);
        break;
      case 'delete':
        this.deleteOrg(org);
        break;
      default:
        this.snackBar.open('Action not recognized', 'Close', {
          duration: 4000,
        });
        break;
    }
  }

  starOrg(org: OrganizationList): void {}

  editOrgName(org: OrganizationList): void {
    const editRef = this.dialog.open(EditOrganizationNameDialogComponent, {
      data: org,
    });

    editRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((_) => {
        this.updateName.emit(this.currentOrg.value.Org.Name);
        this.setOrgObjects();
      });
  }

  editOrgLogo(org: OrganizationList): void {
    const editRef = this.dialog.open(EditOrganizationLogoDialogComponent, {
      data: org,
    });
    editRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((newURL) => {
        this.setOrgObjects();
      });
  }

  deleteOrg(org: OrganizationList): void {
    const confirmationDialog = this.dialog.open(UiConfirmationComponent, {
      data: {
        title: 'Delete Organization?',
        description: 'This will permanently delete the organization.',
        acceptMessage: 'Delete',
        declineMessage: 'Cancel',
      },
    });

    confirmationDialog
      .afterClosed()
      .pipe(take(1))
      .subscribe((response) => {
        if (response) {
          this.apiOrganizationService
            .deleteOrg(org.OrgID)
            .pipe(take(1))
            .subscribe((_) => {
              this.snackBar.open('Organization deleted', 'Close', {
                duration: 4000,
              });
            });
        }
      });
  }

  /**
   * Get the list containing the currently selected org  - OR -
   * get the list containing all the other orgs (not currently selected).
   * @param getCurrentlySelected
   */
  private getOrgs(getCurrentlySelected: boolean, orgID: number): OrganizationList[] {
    if (getCurrentlySelected) return this.loggedInUser.CurrentUser.Orgs.filter((org) => org.OrgID === orgID);
    else return this.loggedInUser.CurrentUser.Orgs.filter((org) => org.OrgID !== orgID);
  }

  private setOrgObjects(): void {
    this.loggedInUser = this.localStorage.getItem('user');
    if (!this.loggedInUser.CurrentUser?.Orgs) return;
    const nonSelectedOrgs: OrganizationList[] = this.getOrgs(false, this.loggedInUser.CurrentOrg.ID);
    const selectedOrg: OrganizationList = this.getOrgs(true, this.loggedInUser.CurrentOrg.ID)[0];
    this.otherOrg.next(nonSelectedOrgs);
    this.currentOrg.next(selectedOrg);
  }
}
