import { FormGroup } from '@angular/forms';
import { Observable, of } from 'rxjs';
import { ApiFormService, Form, FormItem, User } from '@xenon/api';
import { UtilLocalStorageService, UtilMomentService } from '@xenon/utils';

export class TaskFormBase {
  currentUser: User;
  form: FormGroup;

  protected formService: ApiFormService;
  private localStorageService: UtilLocalStorageService;

  constructor(formService, localStorageService) {
    this.localStorageService = localStorageService;
    this.formService = formService;
    this.currentUser = this.localStorageService.getItem('user').CurrentUser;
  }

  protected submitPatch(value, formItem): Observable<any> {
    const targetItem = this.buildPatch(value, formItem);
    return this.formService.update(targetItem.ID, targetItem);
  }

  protected buildPatch(value: string, formItem: FormItem): FormItem {
    formItem.Response = value;
    formItem.Completed = !!value;

    formItem.CompletedBy = this.currentUser.ID;
    formItem.CompletedAt = UtilMomentService.new().utc().toISOString();

    return formItem;
  }
}
