import { Component, OnInit } from '@angular/core';
import { UtilLocalStorageService } from '@xenon/utils';
import { AppList, AppNavigationService, PermissionsHelperService } from '@xenon/auth';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'xenon-root',
  template: ` <router-outlet></router-outlet> `,
})
export class AppComponent implements OnInit {
  constructor(
    private appNavigationService: AppNavigationService,
    private localStorage: UtilLocalStorageService,
    private permissionsHelperService: PermissionsHelperService,
    private router: Router
  ) {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        if (event.url.indexOf('/admin') > -1 || event.urlAfterRedirects.indexOf('/admin') > -1) {
          this.appNavigationService.setApp(AppList.Admin);
        }
        if (event.url.indexOf('/client') > -1 || event.urlAfterRedirects.indexOf('/client') > -1) {
          this.appNavigationService.setApp(AppList.Client);
        }
      }
    });
  }

  ngOnInit(): void {
    this.appNavigationService.setApp(AppList.Client);
  }
}
