import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface ConfirmationDialogOptions {
  title: string;
  description?: string;
  acceptMessage?: string;
  declineMessage?: string;
  showDecline?: boolean;
}

@Component({
  selector: 'xenon-ui-confirmation',
  styleUrls: ['./ui-confirmation.component.scss'],
  template: `
    <p class="text-subtitle1">{{ dialogData.title }}</p>
    <p class="text-body1">{{ dialogData.description }}</p>
    <div fxLayoutAlign="space-between" class="pt-4">
      <button *ngIf="dialogData.showDecline" mat-flat-button (click)="dialogRef.close(false)">{{ dialogData.declineMessage }}</button>
      <button mat-flat-button color="primary" (click)="dialogRef.close(true)">{{ dialogData.acceptMessage }}</button>
    </div>
  `,
})
export class UiConfirmationComponent {
  constructor(
    public dialogRef: MatDialogRef<UiConfirmationComponent>,
    @Inject(MAT_DIALOG_DATA)
    public dialogData: ConfirmationDialogOptions = {
      title: 'Untitled',
      description: '',
      acceptMessage: 'Save',
      declineMessage: 'Close',
      showDecline: true,
    }
  ) {}
}
