import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ApiRoleService, Role, Task, User, LoggedInUser } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { BehaviorSubject } from 'rxjs';
import { UiListSelectDialogService } from '../../ui-list-select/ui-list-select.service';

@Component({
  selector: 'xenon-ui-fields-permissions',
  styleUrls: ['./ui-fields-permissions.component.scss'],
  template: ` <div fxLayoutAlign="space-between">
      <span>
        <h6 class="text-subtitle1 mb-1" id="permissions">Permissions</h6>
        <p class="text-caption2">Select roles that are allowed to complete this task.</p>
      </span>
      <button mat-icon-button (click)="fieldExpanded = !fieldExpanded">
        <mat-icon>{{ fieldExpanded ? 'expand_more' : 'expand_less' }}</mat-icon>
      </button>
    </div>
    <div [ngClass]="fieldExpanded ? 'display-block' : 'display-none'">
      <div *ngIf="selectedRoles | async as selectedRoles">
        <div class="list-row" fxLayoutAlign="space-between center" *ngFor="let role of selectedRoles">
          <div fxLayoutAlign="start center">
            <mat-icon>person_outline</mat-icon>
            <p class="mb-0 text-body1 ml-3">{{ role.Name }}</p>
          </div>
          <button (click)="removeRole(role)" mat-icon-button>
            <mat-icon>cancel</mat-icon>
          </button>
        </div>
      </div>
      <button mat-button [disabled]="!taskRoles && !taskRoles.length" (click)="addRole()" class="text-left color-primary mt-3 p-0">
        <mat-icon class="mr-3">add_circle_outline</mat-icon>
        Add Role
      </button>
    </div>`,
})
export class UiFieldsPermissionsComponent {
  @Input() set selectedTask(tasksByDate: Task) {
    this.selectedRoles.next(tasksByDate.AssignedRoles);
  }
  @Output() newRoleList = new EventEmitter<Role[]>();
  selectedRoles: BehaviorSubject<Role[]> = new BehaviorSubject(null);
  taskRoles: Role[] = [];
  loggedInUser: LoggedInUser;
  showInput = false;

  fieldExpanded = true;

  constructor(
    private apiRoleService: ApiRoleService,
    private utilLocalStorageService: UtilLocalStorageService,
    private uiListSelectService: UiListSelectDialogService<Role>
  ) {
    this.loggedInUser = this.utilLocalStorageService.getItem('user');
  }

  emitList(): void {
    this.newRoleList.emit(this.selectedRoles.value);
  }

  addRole() {
    this.apiRoleService.getRolesByOrg(this.loggedInUser.CurrentOrg.ID).subscribe((response) => {
      this.uiListSelectService.open({
        inputList: response.OrgRoleList,
        currentSelectedList: this.selectedRoles.value,
        primaryValueSelector1: 'Name',
        compareValueBy: 'ID',
        iconClass: 'person_outline',
      });

      this.uiListSelectService.closed().subscribe((selectedUsers) => {
        if (!selectedUsers) return;
        this.selectedRoles.next(selectedUsers);
        this.emitList();
      });
    });
  }

  removeRole(role: Role) {
    this.selectedRoles.next(this.selectedRoles.value.filter((selectedRole) => selectedRole.ID !== role.ID));
    this.emitList();
  }
}
