import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import {
  CreateUserRequest,
  CreateUserResponse,
  UpdateUserRequest,
  User,
  UploadPhotoResponse,
  UpdateUserPrimarySettingsRequest,
  GetOrgUserLocationResponse,
  UpdateOrgUserRequest,
} from '../models/user';
import { map, take } from 'rxjs/operators';
import { BaseApi } from '../environments/base-api';
import { BaseResponse } from '../models/base-response';

@Injectable({
  providedIn: 'root',
})
export class ApiUserService extends BaseApi {
  public cachedUsersSource: BehaviorSubject<User[]> = new BehaviorSubject<User[]>([]);
  cachedUsers = this.cachedUsersSource.asObservable();

  constructor(private http: HttpClient) {
    super();
  }

  /**
   * Create a new user.
   * @param user The information used to create a new user.
   * @returns An observable with the CreateUserResponse.
   */
  public createOrgUser(user: CreateUserRequest): Observable<CreateUserResponse> {
    const url = `${this.baseUrl}/orguser`;
    return this.http.post<CreateUserResponse>(url, user);
  }

  /**
   * Updates an existing OrgUser.
   * @param updateInfo The information used to update a user.
   * @returns An observable with the UpdatedUserResponse.
   */
  public updateOrgUser(updateInfo: UpdateOrgUserRequest): Observable<BaseResponse> {
    const url = `${this.baseUrl}/orguser`;
    return this.http.patch<CreateUserResponse>(url, updateInfo);
  }

  /**
   * Update an existing user.
   * @param updateInfo The user info to update.
   * @returns An observable with the BaseResponse.
   */
  public updateUser(updateInfo: UpdateUserRequest): Observable<BaseResponse> {
    const userID = updateInfo.ID;
    const url = `${this.baseUrl}/user/${userID}`;
    return this.http.patch<BaseResponse>(url, updateInfo);
  }

  /**
   * Delete an existing user.
   * @param userID The ID of the user to delete.
   * @returns A promise containing the BaseResponse.
   */
  public getUser(userID: number): Promise<{ Status: string; XenUser: User }> {
    const url = `${this.baseUrl}/user/${userID}`;
    return this.http.get<{ Status: string; XenUser: User }>(url).toPromise();
  }

  /**
   * get existing org user locations.
   * @param orgID The ID of the organization.
   * @param userID The ID of the user.
   * @returns An observable with the BaseResponse.
   */
  public getOrgUserLocation(orgID: number, userID): Observable<GetOrgUserLocationResponse> {
    const url = `${this.baseUrl}/orguserrole/${orgID}/${userID}`;
    return this.http.get<GetOrgUserLocationResponse>(url);
  }

  /**
   * Delete an existing user.
   * @param userID The ID of the user to delete.
   * @returns An observable with the BaseResponse.
   */
  public deleteUser(orgID: number, userID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/user/${orgID}/${userID}`;
    return this.http.delete<BaseResponse>(url);
  }

  public getUsersByLocation(locationId: number): Observable<User[]> {
    const url = `${this.baseUrl}/team/location/${locationId}`;
    return this.http.get<{ Users: User[] }>(url).pipe(map((response: { Users: User[] }) => response.Users));
  }

  public getUsersByOrganization(organizationId: number): Observable<User[]> {
    const url = `${this.baseUrl}/userbyorg/${organizationId}`;
    return this.http.get<{ OrgUserList: User[] }>(url).pipe(map((response: { OrgUserList: User[] }) => (response.OrgUserList ? response.OrgUserList : [])));
  }

  public getUserByID(organizationId: number, userId: number): Observable<User> {
    const url = `${this.baseUrl}/userorg/${organizationId}/${userId}`;
    return this.http.get<{ UserOrg: User }>(url).pipe(
      take(1),
      map((response: { UserOrg: User }) => response.UserOrg)
    );
  }

  public uploadPhoto(file: File, userID: string): Observable<UploadPhotoResponse> {
    const url = `${this.baseUrl}/uploaduserphoto/${userID}`;

    const formData = new FormData();
    formData.append('photoFile', file, file.name);

    return this.http.post<UploadPhotoResponse>(url, formData);
  }

  public setCachedUsers(users: User[]) {
    this.cachedUsersSource.next(users);
  }

  /**
   * When the user changes their organization / location in the drop down,
   * this call updates what their last choice was so we can load that again next time.
   * Updates the "PrimaryLocationID" & "PrimaryOrgID" properties on the User object.
   * @param request The update user primary settings request.
   * @returns The base response.
   */
  public updateUserPrimarySettings(request: UpdateUserPrimarySettingsRequest): Promise<BaseResponse> {
    const url = `${this.baseUrl}/userprimarysetting`;
    return this.http.post<BaseResponse>(url, request).toPromise();
  }
}
