import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSidenav } from '@angular/material/sidenav';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiTaskService, ApiTaskTemplateService, ApiUserService, BaseResponse, SubTask, Task, TaskTemplate, UpdateTaskRequest, User } from '@xenon/api';
import { EditTaskTemplateComponent, NotificationType, UiListSelectDialogService, UiNotificationService } from '@xenon/ui';
import * as moment from 'moment';
import { TaskByDateService } from '../task-by-date/task-by-date.service';

@Component({
  selector: 'xenon-checklist-panel',
  styleUrls: ['./checklist-panel.component.scss'],
  templateUrl: './checklist-panel.component.html',
})
export class ChecklistPanelComponent implements OnChanges {
  public checklist = {
    name: '',
    taskDue: null,
    taskStart: null,
    taskEnd: null,
    taskStatusPanelOpen: false,
    description: '',
    selectedChecklistTask: [] as SubTask[],
    id: null,
    currentStatus: '',
    itemsComplete: null,
    itemsTotal: null,
    assignedRoles: null,
  };

  public checklistTemplate: TaskTemplate;

  @ViewChild('drawer') sideNav: MatSidenav;
  @Input() currentUserID: number;
  @Input() checkListData: Task;
  @Input() set closeChecklist(closeChecklist: boolean) {
    if (closeChecklist !== null && !this.sideNav.opened) {
      this.sideNav.toggle();
    }
  }
  @Output() refresh = new EventEmitter<void>();
  @Output() hideItem = new EventEmitter<Task>();
  @Output() closed = new EventEmitter<void>();

  public taskDatePickerFormGroup: FormGroup;

  constructor(
    private notificationService: UiNotificationService,
    private taskService: ApiTaskService,
    private taskByDateService: TaskByDateService,
    private _snackBar: MatSnackBar,
    private apiUserService: ApiUserService,
    private uiListSelectService: UiListSelectDialogService<User>,
    private apiTaskTemplateService: ApiTaskTemplateService,
    public dialog: MatDialog
  ) {}

  ngOnChanges(changes: SimpleChanges) {
    if (this.checkListData) {
      const taskNotAlreadyOpen = !this.sideNav.opened || changes?.checkListData?.currentValue?.ID !== changes?.checkListData?.previousValue?.ID;

      if (taskNotAlreadyOpen) {
        this.openChecklistSidePanel();
      } else {
        this.sideNav.toggle();
      }
    }
  }

  public closeChecklistPanel(emptyData: boolean) {
    this.closed.emit();
    if (emptyData) {
      this.taskByDateService.nextTask(null);
    }
    this.sideNav.toggle();
  }

  public updateTask() {
    const taskDue = this.taskDatePickerFormGroup.value.taskDue ? moment(this.taskDatePickerFormGroup.value.taskDue).format() : this.checklist.taskDue;
    const taskStart = this.taskDatePickerFormGroup.value.taskStart ? moment(this.taskDatePickerFormGroup.value.taskStart).format() : this.checklist.taskStart;
    const taskEnd = this.taskDatePickerFormGroup.value.taskEnd ? moment(this.taskDatePickerFormGroup.value.taskEnd).format() : this.checklist.taskEnd;
    const request: UpdateTaskRequest = {
      ID: this.checklist.id,
      Description: this.checklist.description.replace(/\s/g, '').length > 0 ? this.checklist.description : 'Description',
      Name: this.checklist.name.replace(/\s/g, '').length > 0 ? this.checklist.name : 'Untitled Task',
      DueDate: taskDue.substring(0, 10),
      DueTime: taskDue.substring(11, 16) === ' :00:' ? '' : taskDue.substring(11, 16),
      AvailableDate: taskStart.substring(0, 10),
      AvailableTime: taskStart.substring(11, 16) === ' :00:' ? '' : taskStart.substring(11, 16),
      ExpireDate: taskEnd.substring(0, 10),
      ExpireTime: taskEnd.substring(11, 16) === ' :00:' ? '' : taskEnd.substring(11, 16),
    };

    this.notificationService.show(NotificationType.info, 'Okay', 'Updating', 1000);
    this.taskService
      .updateTask(request)
      .then((response: BaseResponse) => {
        this.refresh.next();
      })
      .catch((err) => {
        this.showChecklistError(`An error occurred updating task.`);
      });
  }

  public getTaskCompletionPercentage(): string {
    return Math.floor(this.checklist.itemsComplete / this.checklist.itemsTotal).toString();
  }

  public addAssignedUser(): void {
    this.apiUserService.getUsersByOrganization(this.checkListData.OrgID).subscribe((userList: User[]) => {
      this.uiListSelectService.open({
        inputList: userList,
        currentSelectedList: this.checkListData.AssignedUsers,
        primaryValueSelector1: 'FirstName',
        primaryValueSelector2: 'LastName',
        secondaryValueSelector: 'JobTitle',
        compareValueBy: 'ID',
        displayUserImage: true,
      });

      this.uiListSelectService.closed().subscribe((selectedUsers) => {
        if (!selectedUsers) return;

        const assignUsers = selectedUsers.filter((selectedUser) => {
          return !this.checkListData.AssignedUsers.includes(selectedUser);
        });
        const removeUsers = this.checkListData.AssignedUsers.filter((assignedUser) => {
          return !selectedUsers.includes(assignedUser);
        });

        assignUsers.forEach((user) => {
          this.taskService.assignTaskUser({ taskId: this.checkListData.ID, userID: user.ID }).then((response: BaseResponse) => {});
        });

        removeUsers.forEach((user) => {
          this.taskService.removeTaskUser({ taskId: this.checkListData.ID, userID: user.ID }).then((response: BaseResponse) => {});
        });
        this.checkListData.AssignedUsers = selectedUsers;
        this.refresh.next();
      });
    });
  }

  public markTaskComplete() {
    const taskID = this.checklist.id;
    const complete = true;

    this.taskService
      .updateTaskStatus(taskID, this.currentUserID, complete)
      .then((response: BaseResponse) => {
        this.closeChecklistPanel(false);
        const snackBarRef = this.notificationService.show(NotificationType.success, 'UNDO', `${this.checklist.name} completed`, 5000);
        snackBarRef.onAction().subscribe(() => {
          // User clicked 'UNDO'.
          this.updateTaskStatus(taskID, !complete);
        });

        // if user doesn't click 'UNDO' still refresh data.
        setTimeout(() => {
          this.refresh.next();
        }, 5000);
      })
      .catch((err) => {
        this.showChecklistError(`An error occurred updating task status.`);
      });
  }

  public editTemplate(): void {
    const task = this.checklistTemplate ? this.checklistTemplate : this.createTemplateObject();
    const dialogRef = this.dialog.open(EditTaskTemplateComponent, {
      data: {
        task: task,
      },
      panelClass: 'full-page-dialog',
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (!result) return;
      this.apiTaskTemplateService.updateTaskTemplateBatch(result).subscribe((_) => {
        this.notificationService.show(NotificationType.success, 'Action done', 'Task saved', 2500);
      });
    });
  }

  public deleteTask(): void {
    this.closeChecklistPanel(false);
    this.hideItem.emit(this.checkListData);
    const snackBarRef = this._snackBar.open('Task Deleted', 'Undo', {
      duration: 4000,
    });
    snackBarRef.afterDismissed().subscribe((info) => {
      if (!info.dismissedByAction) {
        this.taskService.deleteTask(this.checkListData.ID).subscribe(() => {
          this.refresh.next();
        });
      }
    });
    snackBarRef.onAction().subscribe(() => {
      this.hideItem.emit(null);
    });
  }

  public createTemplateObject(): TaskTemplate {
    const { AssignedRoles, AssignedUsers, CreatedBy, Description, ID, LocationID, Name, OrgID, SubTaskList, TaskType, TaskTypeID } = this.checkListData;
    return {
      AssignedRoles: AssignedRoles,
      AssignedUsers: AssignedUsers,
      CreatedBy: CreatedBy,
      Description: Description,
      ID: ID,
      LocationID: LocationID,
      Name: Name,
      OrgID: OrgID,
      SubTaskList: SubTaskList,
      TaskType: TaskType,
      TaskTypeID: TaskTypeID,
    };
  }

  private showChecklistError(message: string): void {
    this.notificationService.show(NotificationType.error, 'Error', message, 5000);
  }

  private updateTaskStatus(taskID: number, checked: boolean) {
    this.taskService
      .updateTaskStatus(taskID, this.currentUserID, checked)
      .then((response: BaseResponse) => {
        this.refresh.next();
      })
      .catch((err) => {
        this.showChecklistError(`An error occurred updating the task status.`);
      });
  }

  private openChecklistSidePanel() {
    this.taskByDateService.nextTask(this.checkListData);
    this.checklist = {
      name: this.checkListData.Name === 'Untitled Task' ? '' : this.checkListData.Name,
      taskDue: moment(this.checkListData.DueDate + 'T' + this.checkListData.DueTime + ':00').toDate(),
      taskStart: moment(this.checkListData.AvailableDate + 'T' + this.checkListData.AvailableTime + ':00').toDate(),
      taskEnd: moment(this.checkListData.ExpireDate + 'T' + this.checkListData.ExpireTime + ':00').toDate(),
      taskStatusPanelOpen: false,
      description: this.checkListData.Description === 'Description' ? '' : this.checkListData.Description,
      selectedChecklistTask: this.checkListData.SubTaskList || [],
      id: this.checkListData.ID,
      currentStatus: this.checkListData.CurrentStatus,
      itemsComplete: this.checkListData.ItemsComplete,
      itemsTotal: this.checkListData.ItemsTotal,
      assignedRoles: this.checkListData.AssignedRoles,
    };

    this.taskDatePickerFormGroup = new FormGroup({
      taskDue: new FormControl(this.checklist.taskDue),
      taskStart: new FormControl(this.checklist.taskStart),
      taskEnd: new FormControl(this.checklist.taskEnd),
    });

    this.apiTaskTemplateService.getTaskTemplateByTemplateID(this.checkListData.TaskTemplateID).subscribe((fullTemplate) => {
      this.checklistTemplate = fullTemplate;
    });

    if (!this.sideNav.opened) this.sideNav.toggle();
  }
}
