import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { UiFormFieldInput } from '../models/ui-form-field-input';
import { UiFormFieldSelect } from '../models/ui-form-field-select';
import { UiFormFieldTextArea } from '../models/ui-form-field-text-area';
import { UiFormFieldValidator } from '../enums/ui-form-field-validator.enum';
import * as _ from 'lodash-es';

@Injectable({
  providedIn: 'root',
})
export class UiFormService {
  static buildForm(
    fields: (UiFormFieldInput | UiFormFieldSelect | UiFormFieldTextArea)[]
  ): FormGroup {
    const formGroupBuild = new FormGroup({});
    fields.forEach(
      (field: UiFormFieldInput | UiFormFieldSelect | UiFormFieldTextArea) => {
        const arrayOfValidators = [];
        field.validators.forEach((v) => {
          switch (v.validator) {
            case UiFormFieldValidator.Required:
              arrayOfValidators.push(Validators.required);
              break;
            case UiFormFieldValidator.Email:
              arrayOfValidators.push(Validators.email);
              break;
            case UiFormFieldValidator.Max:
              arrayOfValidators.push(Validators.max(v.value));
              break;
            case UiFormFieldValidator.MaxLength:
              arrayOfValidators.push(Validators.maxLength(v.value));
              break;
            case UiFormFieldValidator.Min:
              arrayOfValidators.push(Validators.min(v.value));
              break;
            case UiFormFieldValidator.MinLength:
              arrayOfValidators.push(Validators.minLength(v.value));
              break;
            case UiFormFieldValidator.Pattern:
              arrayOfValidators.push(Validators.pattern(v.value));
              break;
          }
        });
        formGroupBuild.addControl(
          field.name,
          new FormControl(field.value, arrayOfValidators)
        );
      }
    );
    return formGroupBuild;
  }

  static buildFieldsAsObject(
    fields: (UiFormFieldInput | UiFormFieldSelect | UiFormFieldTextArea)[],
    property: string = 'name'
  ): object {
    return _.keyBy(fields, property);
  }
}
