import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiTaskService, ApiUserService, BaseResponse, Task, UpdateTaskRequest, User } from '@xenon/api';
import { UiListSelectDialogService } from '@xenon/ui';
import { UtilDateToolsService, UtilMomentService } from '@xenon/utils';
import * as moment from 'moment';
import { BehaviorSubject } from 'rxjs';
import { TaskByDateService } from './task-by-date.service';

@Component({
  selector: 'xenon-task-by-date',
  styleUrls: ['./task-by-date.component.scss'],
  template: `
    <div *ngIf="!hideTask" class="task-by-date" fxLayout="row">
      <div fxLayoutAlign="start center" fxLayout="column" class="w-100" (click)="selectedTask()">
        <p class="text-body2 m-0 w-100 pl-3 pt-2">{{ task.Name }}</p>
        <div class="w-100 pl-3 py-1" fxLayout="row" fxLayoutAlign="space-between">
          <div fxLayoutAlign="start center">
            <div *ngIf="task.CurrentStatus !== ''" class="status text-center" [ngClass]="task.CurrentStatus.toLowerCase()">
              <span *ngIf="task.CurrentStatus.toLowerCase() === 'scheduled'">Scheduled</span>
              <span *ngIf="task.CurrentStatus.toLowerCase() === 'complete'">Complete</span>
              <span *ngIf="task.CurrentStatus.toLowerCase() === 'open'">Open</span>
              <span *ngIf="task.CurrentStatus.toLowerCase() === 'overdue'">Overdue</span>
              <span *ngIf="task.CurrentStatus.toLowerCase() === 'expired'">Expired</span>
            </div>
            <div class="status text-center ml-2" *ngIf="getTaskCompletionPercentage() != ''" [ngClass]="task.CurrentStatus.toLowerCase()">
              <span>{{ getTaskCompletionPercentage() }}%</span>
            </div>
            <div class="status text-center ml-2" [ngClass]="task.CurrentStatus.toLowerCase()">
              <span>{{ getAvailableTime() }}</span>
            </div>
          </div>
        </div>
      </div>
      <span fxLayoutAlign="start center">
        <span (click)="addAssignedUser()" *ngIf="task.AssignedUsers && task.AssignedUsers.length > 0; else empty">
          <xenon-ui-user-photo [userArray]="task.AssignedUsers"></xenon-ui-user-photo>
        </span>
        <ng-template #empty><mat-icon (click)="addAssignedUser()" class="color-gray">person_add_alt</mat-icon></ng-template>
        <mat-icon [matMenuTriggerFor]="menu" class="mx-3 color-gray">more_vert</mat-icon>
        <mat-menu #menu="matMenu">
          <button mat-menu-item (click)="deleteTask()">Delete Task</button>
        </mat-menu>
      </span>
    </div>
  `,
})
export class TaskByDateComponent implements OnChanges {
  @Output() clickedItem = new EventEmitter();
  @Input() task: Task;
  today = moment().toDate();
  dueDate: BehaviorSubject<Date> = new BehaviorSubject<Date>(null);
  hideTask = false;

  constructor(
    private taskByDateService: TaskByDateService,
    private apiTaskService: ApiTaskService,
    private _snackBar: MatSnackBar,
    private apiUserService: ApiUserService,
    private uiListSelectService: UiListSelectDialogService<User>
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.task.currentValue) {
      const task = changes.task.currentValue;
      this.dueDate.next(UtilMomentService.utcToLocal(task.DueDate));
    }
  }

  addAssignedUser(): void {
    this.apiUserService.getUsersByOrganization(this.task.OrgID).subscribe((userList: User[]) => {
      this.uiListSelectService.open({
        inputList: userList,
        currentSelectedList: this.task.AssignedUsers,
        primaryValueSelector1: 'FirstName',
        primaryValueSelector2: 'LastName',
        secondaryValueSelector: 'JobTitle',
        compareValueBy: 'ID',
        displayUserImage: true,
      });

      this.uiListSelectService.closed().subscribe((selectedUsers) => {
        if (!selectedUsers) return;
        const assignUsers = selectedUsers.filter((selectedUser) => {
          return !this.task.AssignedUsers.includes(selectedUser);
        });
        const removeUsers = this.task.AssignedUsers.filter((assignedUser) => {
          return !selectedUsers.includes(assignedUser);
        });

        assignUsers.forEach((user) => {
          this.apiTaskService.assignTaskUser({ taskId: this.task.ID, userID: user.ID }).then((response: BaseResponse) => {});
        });

        removeUsers.forEach((user) => {
          this.apiTaskService.removeTaskUser({ taskId: this.task.ID, userID: user.ID }).then((response: BaseResponse) => {});
        });
        this.task.AssignedUsers = selectedUsers;
      });
    });
  }

  deleteTask(): void {
    this.hideTask = true;
    const snackBarRef = this._snackBar.open('Task Deleted', 'Undo', {
      duration: 4000,
    });
    snackBarRef.afterDismissed().subscribe((info) => {
      if (!info.dismissedByAction) {
        this.apiTaskService.deleteTask(this.task.ID).subscribe(() => {});
      }
    });
    snackBarRef.onAction().subscribe(() => {
      this.hideTask = false;
    });
  }

  changeTaskVisibility(newVal: boolean): void {
    this.hideTask = newVal;
  }

  getTaskCompletionPercentage(): string {
    const calculation = Math.floor((this.task.ItemsComplete / this.task.ItemsTotal) * 100);
    return calculation >= 0 ? calculation.toString() : '';
  }

  getAvailableTime(): string {
    const hours = parseInt(this.task.AvailableTime.substr(0, this.task.AvailableTime.indexOf(':')), 10);
    const minutes = parseInt(this.task.AvailableTime.substr(this.task.AvailableTime.indexOf(':') + 1, this.task.AvailableTime.indexOf(':')), 10);
    return `${hours % 12 || 12}:${minutes < 10 ? minutes + '0' : minutes} ${hours >= 12 ? 'PM' : 'AM'}`;
  }

  isPast(): boolean {
    const yesterday = moment(
      moment()
        .toDate()
        .setDate(moment().toDate().getDate() - 1)
    ).toDate();
    return yesterday < UtilDateToolsService.utcToLocal(this.task.AvailableDate);
  }

  selectedTask(): void {
    this.taskByDateService.nextTask(this.task);
    this.clickedItem.emit();
  }
}
