import { Injectable } from '@angular/core';
import { ApiAuthenticationService, ApiUserService, User, LoggedInUser, ApiLocationService } from '@xenon/api';
import { UtilLocalStorageService, UtilWindowRefService } from '@xenon/utils';
import { Observable, BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class CurrentUserStorageService {
  constructor(
    private apiUserService: ApiUserService,
    private utilLocalStorageService: UtilLocalStorageService,
    private authenticationService: ApiAuthenticationService,
    private utilWindowRefService: UtilWindowRefService,
    private apiLocationService: ApiLocationService
  ) {}

  public setCurrentUser(): Observable<boolean> {
    const loggedInUser = this.utilLocalStorageService.getItem('user') as LoggedInUser;
    const currentOrgID = loggedInUser.CurrentOrg.ID;
    const currentLocationID = loggedInUser.CurrentLocation.ID;
    const success: BehaviorSubject<boolean> = new BehaviorSubject(false);
    this.apiUserService
      .getUser(loggedInUser.CurrentUser.ID)
      .then((result: { Status: string; XenUser: User }) => {
        const xenUser = result.XenUser;
        //Check if user is logged into a location they are no longer a part of
        if (result.XenUser.Orgs.some((org) => org.OrgID === currentOrgID)) {
          const currentOrg = result.XenUser.Orgs.find((org) => org.OrgID === currentOrgID);
          if (!currentOrg.RoleLocations.some((roleLocation) => roleLocation.LocationList.some((location) => location.ID === currentLocationID))) {
            //switch location within org
            this.apiLocationService
              .switchLocation(xenUser.PrimaryLocationID)
              .pipe(take(1))
              .subscribe((newLoggedInUser) => {
                this.utilLocalStorageService.setItem('user', newLoggedInUser);
                success.next(true);
              });
          }
        } else {
          //switch org and location
          this.apiLocationService
            .switchLocation(xenUser.PrimaryLocationID)
            .pipe(take(1))
            .subscribe((newLoggedInUser) => {
              this.utilLocalStorageService.setItem('user', newLoggedInUser);
              success.next(true);
            });
        }
        if (!success.value) {
          const newLoggedInUser = { ...loggedInUser, CurrentUser: xenUser };
          this.utilLocalStorageService.setItem('user', newLoggedInUser);
          success.next(true);
        }
      })
      .catch((err) => {
        // log user out if their token has expired.
        this.authenticationService.logout();
        const origin = this.utilWindowRefService.nativeWindow.location.origin;
        this.utilWindowRefService.nativeWindow.location.replace(`${origin}/#/login`);
      });
    return success;
  }
}
