import { Component, OnDestroy, OnInit } from '@angular/core';
import { AppList, AppNavigationService } from '@xenon/auth';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, BehaviorSubject } from 'rxjs';
import { takeUntil, map, mergeMap, take, filter } from 'rxjs/operators';
import { ApiDriveService, ApiFormService, ApiFormTemplateService, FormTemplate, Role, User, LoggedInUser } from '@xenon/api';
import { TabTypes } from './enums/tab-types';
import { FormBuilderService } from './services/form-builder.service';
import { NotificationType, UiNotificationService } from '@xenon/ui';
import { CurrentUserStorageService } from '../services/current-user-storage.service';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-sub-layout',
  templateUrl: './sub-layout.component.html',
  styleUrls: ['./sub-layout.component.scss'],
})
export class SubLayoutComponent implements OnInit, OnDestroy {
  currentForm: FormTemplate;
  tabTypes = TabTypes;
  tabTypesList = [TabTypes.layout, TabTypes.tasks, TabTypes.settings];
  selectedTabName: string = this.tabTypes.layout;
  locationPath = 'layout';

  user: LoggedInUser; //TODO move this to formBuilderService.
  locationId: number;
  formId: number;

  unsubscribe = new Subject<{}>();

  public loadedCurrentUser: BehaviorSubject<boolean> = new BehaviorSubject(false);

  constructor(
    private appNavigationService: AppNavigationService,
    private formBuilderService: FormBuilderService,
    private route: ActivatedRoute,
    private router: Router,
    private apiFormTemplateService: ApiFormTemplateService,
    private apiFormService: ApiFormService,
    private apiDriveService: ApiDriveService,
    public notificationService: UiNotificationService,
    public currentUserStorage: CurrentUserStorageService,
    private localStorage: UtilLocalStorageService
  ) {
    currentUserStorage
      .setCurrentUser()
      .pipe(
        filter((result) => result),
        take(1)
      )
      .subscribe((_) => {
        this.loadedCurrentUser.next(true);
      });
  }

  ngOnInit(): void {
    this.setRoutePath(this.router.url);
    this.getCurrentUser();
    this.getOrCreateForm();
    this.getCurrentForm();

    // this.formBuilderService.lastSavedForm
    //   .pipe(takeUntil(this.unsubscribe))
    //   .subscribe((form) => {
    //     this.lastSavedForm = form;
    //   });
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  getOrCreateForm() {
    this.route.queryParams.pipe(takeUntil(this.unsubscribe)).subscribe((params) => {
      this.formId = params.id;

      if (params.action === 'create') {
        this.createNewFormAndRedirect(1); //TODO Find where FormCategoryID comes from.
      } else if (this.formId) {
        this.fetchCurrentForm();
      }
    });
  }

  fetchCurrentForm() {
    this.apiFormTemplateService.getFormTemplateById(this.formId).subscribe((response) => {
      if (response.Status === 'success') {
        this.formBuilderService.setFormTemplate(response.FormTemplate);
      } else if (response.Status === 'error') {
        this.notificationService.show(NotificationType.success, 'ERROR', response.Message, 5000);
      }
    });
  }

  getCurrentForm() {
    this.formBuilderService.currentForm
      .pipe(
        takeUntil(this.unsubscribe),
        map((form) => {
          this.currentForm = form;
          this.locationId = form.LocationID;
          const orgID = form.OrgID;
          return orgID;
        }),
        mergeMap((orgID) => this.apiDriveService.getDriveFilesByOrg(orgID))
      )
      .subscribe((response) => {
        this.formBuilderService.setCurrentDriveList(response);
      });
  }

  getCurrentUser() {
    const savedUser = this.localStorage.getItem('user');
    this.user = savedUser;
    this.formBuilderService.setCurrentUser(savedUser.CurrentUser);
  }

  createNewFormAndRedirect(formCategoryId: number) {
    const formTemplate = {
      OrgID: this.user.CurrentOrg.ID,
      LocationID: this.locationId,
      Name: 'New Form',
      Description: '',
      FormCategoryID: formCategoryId,
      CreatedBy: this.user.CurrentUser.ID,
    };

    this.apiFormTemplateService.createFormTemplate(formTemplate).subscribe((response) => {
      if (response.Status === 'success') {
        this.router.navigate(['/layout'], {
          queryParams: { id: response.NewFormTemplate.ID },
          replaceUrl: true,
        });
      } else if (response.Status === 'error') {
        this.notificationService.show(NotificationType.error, 'ERROR', response.Message, 5000);
      }
    });
  }

  selectTab(tabName: string): void {
    this.selectedTabName = tabName;
  }

  navigateToAdmin() {
    this.appNavigationService.navigateToApp(AppList.Admin, `form-templates`);
  }

  // TODO Add a success error notification
  saveForm() {
    const formData = {
      ID: this.currentForm.ID,
      Name: this.currentForm.Name,
      NamePlural: this.currentForm.NamePlural,
      Description: this.currentForm.Description,
      DriveID: this.currentForm.DriveID ? this.currentForm.DriveID : 0,
      FormCategoryID: this.currentForm.FormCategoryID,
    };

    if (this.currentForm.FormTemplateItemList) {
      this.currentForm.FormTemplateItemList.forEach((item) => {
        const newItem = { ...item, DriveID: item.DriveID ? item.DriveID : 0 };

        //Save form fields
        this.apiFormService.updateItem(newItem.ID, newItem).subscribe((_) => {});
      });

      //Save form field options
      this.currentForm.FormTemplateItemList.forEach((item) => {
        item.FormTemplateItemOptions.forEach((itemOption) => {
          this.apiFormService.updateItemOption(itemOption).subscribe((_) => {});
        });
      });
    }

    //Delete old roles and save new ones
    //We have to do this because there is no update method for roles.
    if (this.currentForm.AssignedRoles && this.currentForm.AssignedRoles.length > 0) {
      this.apiFormTemplateService.getFormTemplateById(this.formId).subscribe((response) => {
        const savedRoles = response['FormTemplate'].AssignedRoles;

        //delete existing roles.
        if (savedRoles && savedRoles.length > 0) {
          savedRoles.forEach((role) => {
            this.deleteRole(role);
          });
        }

        this.saveNewRoles(this.currentForm.AssignedRoles);
      });
    }

    //Save form
    this.apiFormTemplateService
      .updateFormTemplate(formData)
      .pipe(take(1))
      .subscribe((_) => {
        this.notificationService.show(NotificationType.success, 'Action done', 'Form template saved', 2500);
      });
  }

  deleteRole(role: Role) {
    this.apiFormService
      .deleteFormTemplateRole(this.currentForm.ID, role.ID)
      .pipe(take(1))
      .subscribe((_) => {});
  }

  saveNewRoles(roles: Role[]) {
    roles.forEach((role) => {
      this.apiFormService
        .addFormTemplateRole(this.currentForm.ID, role.ID)
        .pipe(take(1))
        .subscribe((_) => {});
    });
  }

  setRoutePath(locationPath: any) {
    const pathMatch = this.tabTypesList.findIndex((t) => {
      const index = locationPath.indexOf(t);
      return index > -1 ? true : false;
    });

    if (pathMatch) {
      this.selectTab(this.tabTypesList[pathMatch]);
    } else {
      this.selectTab(TabTypes.layout);
    }
  }
}
