/**
 * Note that these interfaces will change once we know what data
 * we get from Fattmerchant.
 */

export interface Invoice {
  InvoiceNumber: string;
  Date: string;
  Description: string;
  BilledAmount: number;
  Status: string;
}

export type PaymentMethodType = 'Card' | 'ACH';
export interface PaymentMethod {
  Type: PaymentMethodType;
  AccountNumberLastFour: number;
  NameOnAccount: string;
  Bank?: string; // for ACH only
}

export interface Address {
  Name: string;
  Street: string;
  City: string;
  State: string;
  Zip: string;
  Email: string;
  PhoneNumber: string;
}
