import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiAuthenticationService, BaseResponse, ChangePasswordFromEmailRequest } from '@xenon/api';
import { NotificationType, UiNotificationService, ChangePasswordComponent, ChangePasswordData } from '@xenon/ui';

@Component({
  selector: 'xenon-password-reset-from-email',
  template: '',
})
export class PasswordResetFromEmailComponent implements OnInit {
  constructor(
    public notificationService: UiNotificationService,
    private dialog: MatDialog,
    private authService: ApiAuthenticationService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  public ngOnInit() {
    const routeParams = {
      email: this.route.snapshot.paramMap.get('e'),
      orgID: this.route.snapshot.paramMap.get('o'),
      uUid: this.route.snapshot.paramMap.get('uu'),
      acceptInvitation: this.route.snapshot.paramMap.get('ai'),
    };

    const dialogRef = this.dialog.open(ChangePasswordComponent, {
      width: '250px',
      data: {
        hideCurrentPassword: true,
        newPassword: '',
        confirmPassword: '',
      },
    });

    dialogRef.afterClosed().subscribe((data: ChangePasswordData) => {
      if (data) {
        const request: ChangePasswordFromEmailRequest = {
          Email: routeParams.email,
          UUID: routeParams.uUid,
          OrgId: +routeParams.orgID,
          NewPassword: data.newPassword,
          NewPassword2: data.confirmPassword,
          AI: +routeParams.acceptInvitation,
        };

        this.authService
          .changePasswordFromEmail(request)
          .then((response: BaseResponse) => {
            this.router.navigate(['login']);
            this.notificationService.show(NotificationType.success, 'Action done', 'Password Updated', 5000);
          })
          .catch((err) => {
            this.notificationService.show(NotificationType.error, 'Error', `${err?.error?.Error}`, 5000);
            this.ngOnInit();
          });
      }
    });
  }
}
