import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { UiPromptComponent } from '../components/ui-prompt/ui-prompt.component';

@Injectable({
  providedIn: 'root',
})
export class UiPromptService {
  constructor(public dialog: MatDialog) {}

  prompt(message): MatDialogRef<UiPromptComponent> {
    return this.dialog.open(UiPromptComponent, {
      width: '250px',
      height: '220px',
      data: {
        question: message,
        ok: false,
      },
    });
  }
}
