import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AddTaskRecurrenceRequest, AddTaskRecurrenceResponse, GetTaskRecurrenceResponse, TaskRecurrence } from '@xenon/api';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/operators';
import { BaseApi } from '../environments/base-api';
import { BaseResponse } from '../models/base-response';

@Injectable({
  providedIn: 'root',
})
export class ApiTaskRecurrenceService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  getTaskRecurrenceByTaskTemplateId(taskTemplateId: number): Observable<GetTaskRecurrenceResponse> {
    const url = `${this.baseUrl}/taskrecurrencebytasktemplateid/${taskTemplateId}`;
    return this.http.get<GetTaskRecurrenceResponse>(url);
  }

  getTaskRecurrence(id: number): Observable<TaskRecurrence> {
    const url = `${this.baseUrl}/gettaskrecurrence/${id}`;
    return this.http.get<any>(url).pipe(map((response: any) => response));
  }

  addTaskRecurrence(taskRecurrence: AddTaskRecurrenceRequest): Promise<AddTaskRecurrenceResponse> {
    const url = `${this.baseUrl}/taskrecurrence`;
    return this.http.post<AddTaskRecurrenceResponse>(url, taskRecurrence).toPromise();
  }

  updateTaskRecurrence(taskRecurrence: TaskRecurrence): Observable<TaskRecurrence> {
    const url = `${this.baseUrl}/taskrecurrence`;
    return this.http.patch<TaskRecurrence>(url, taskRecurrence).pipe(map((response: any) => response));
  }

  deleteTaskRecurrence(id: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/taskrecurrence/${id}`;
    return this.http.delete<BaseResponse>(url).pipe(map((response: any) => response));
  }

  // getOverdueTasksByOrganization(organizationId: number): Observable<TaskRecurrence[]> {
  //   const url = `${this.baseUrl}/tasks/overdue/org/${organizationId}`;
  //   return this.http
  //     .get<{ Tasks: Task[] }>(url)
  //     .pipe(map((response: { Tasks: Task[] }) => response.Tasks));
  // }

  //   v1.GET(“/gettaskrecurrence/:id”, getTaskRecurrenceReq)
  // 		v1.POST(“/taskrecurrence”, createTaskRecurrenceReq)
  // 		v1.PATCH(“/taskrecurrence”, updateTaskRecurrence)
  // 		v1.DELETE(“/taskrecurrence/:id”, deleteTaskRecurrence)
}
