import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { debounceTime, switchMap } from 'rxjs/operators';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-task-form-field-textbox',
  styleUrls: ['./task-form-field-textbox.component.scss'],
  template: `
    <form [formGroup]="form">
      <mat-form-field appearance="fill">
        <mat-label>{{ formItem.Label }}</mat-label>
        <textarea matInput formControlName="inputItem"></textarea>
      </mat-form-field>
    </form>
  `,
})
export class TaskFormFieldTextboxComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    this.form = new FormGroup({
      inputItem: new FormControl(this.formItem.Response, Validators.required),
    });

    this.form.valueChanges
      .pipe(
        debounceTime(500),
        switchMap((v) => this.submitPatch(v.inputItem, this.formItem))
      )
      .subscribe((response: { Message: string; formItem: FormItem }) => {
        this.formItem = response.formItem;
        this.cd.detectChanges();
      });
  }
}
