import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { BaseApi } from '../environments/base-api';
import { BaseResponse } from '../models/base-response';

import {
  CreateFormTemplateRequest,
  CreateFormTemplateResponse,
  FormTemplate,
  GetFormTemplateResponse,
  UpdateFormTemplateRequest,
  GetFormTemplateByIDResponse,
} from '../models/form-template';

@Injectable({
  providedIn: 'root',
})
export class ApiFormTemplateService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  /**
   * Used to get form templates by an organization ID.
   * @param orgID The organization ID used to pull form templates.
   * @returns An observable with an array of form templates.
   */
  public getFormTemplatesByOrg(orgID: number): Observable<FormTemplate[]> {
    const url = `${this.baseUrl}/formtemplatebyorg/${orgID}`;
    return this.http.get<GetFormTemplateResponse>(url).pipe(map((response: GetFormTemplateResponse) => response.FormTemplateList));
  }

  /**
   * Used to get form templates by a location ID.
   * @param locationID The location ID used to pull form templates.
   * @returns An observable with an array of form templates.
   */
  public getFormTemplatesByLocation(locationID: number): Observable<FormTemplate[]> {
    const url = `${this.baseUrl}/formtemplatebylocation/${locationID}`;
    return this.http.get<GetFormTemplateResponse>(url).pipe(map((response: GetFormTemplateResponse) => response.FormTemplateList));
  }

  /**
   * Used to create a new form template.
   * @param template The template to create.
   * @returns An observable with the CreateFormTemplateResponse.
   */
  public createFormTemplate(template: CreateFormTemplateRequest): Observable<CreateFormTemplateResponse> {
    const url = `${this.baseUrl}/formtemplate`;
    return this.http.post<CreateFormTemplateResponse>(url, template);
  }

  /**
   * Used to get an existing form template.
   * @param id The form template ID.
   * @returns An observable with the UpdateFormTemplateReponse.
   */
  public getFormTemplateById(id: number): Observable<GetFormTemplateByIDResponse> {
    const url = `${this.baseUrl}/formtemplate/${id}`;
    return this.http.get<GetFormTemplateByIDResponse>(url);
  }

  /**
   * Used to update an existing form template.
   * @param templateInfo The template info to update.
   * @returns An observable with the UpdateFormTemplateReponse.
   */
  public updateFormTemplate(templateInfo: UpdateFormTemplateRequest): Observable<BaseResponse> {
    const templateID = templateInfo.ID;
    const url = `${this.baseUrl}/formtemplate/${templateID}`;
    return this.http.patch<BaseResponse>(url, templateInfo);
  }

  /**
   * Used to delete an existing form template.
   * @param templateID The template to delete.
   * @returns An observable with the BaseResponse.
   */
  public deleteFormTemplate(templateID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/formtemplate/${templateID}`;
    return this.http.delete<BaseResponse>(url);
  }
}
