import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ApiRoleService, ApiUserService, Role, User, LoggedInUser } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';

export interface TaskFilterItem {
  active: boolean;
  label: string;
  value: string;
}

@Component({
  selector: 'xenon-tasks-filter',
  styleUrls: ['./tasks-filter.component.scss'],
  templateUrl: './tasks-filter.component.html',
})
export class TasksFilterComponent {
  @Input() items: TaskFilterItem[];
  @Output() filterChange = new EventEmitter<TaskFilterItem[]>();
  public showPhoto: boolean;
  generalForm: FormGroup;
  teamForm: FormGroup;
  roleForm: FormGroup;
  loggedInUser: LoggedInUser;
  allTeam: User[];
  allRoles: Role[];
  organizationId: number;
  locationId: number;
  localFilterArray: TaskFilterItem[];

  constructor(
    public formBuilder: FormBuilder,
    private userService: ApiUserService,
    public localStorage: UtilLocalStorageService,
    private apiRoleService: ApiRoleService
  ) {
    this.localFilterArray = this.localStorage.getItem('tasksFilterItems');
    this.loggedInUser = this.localStorage.getItem('user');
    this.showPhoto = this.loggedInUser.CurrentUser?.PhotoURL?.trim()?.length > 0;
    this.createTeamForm();
    this.createRolesForm();
    this.createGeneralForm();
  }

  public hidePhoto() {
    this.showPhoto = false;
  }

  changeFilter(): void {
    const filter = { ...this.generalForm.value, ...this.teamForm.value, ...this.roleForm.value };
    this.items.forEach((item) => (item.active = filter[item.value] ? filter[item.value] : false));
    const newItems = Object.entries(filter).map(([k, v]) => ({ active: v, label: k, value: k })) as TaskFilterItem[];
    const newItemArray = this.items.concat(newItems.filter((newItem) => !this.items.some((item) => item.value === newItem.value)));
    this.filterChange.emit(newItemArray);
    this.localStorage.setItem('tasksFilterItems', newItemArray);
  }

  createGeneralForm(): void {
    this.generalForm = this.formBuilder.group({
      scheduled: [this.checkFilterStatus('scheduled', true)],
      open: [this.checkFilterStatus('open', true)],
      overdue: [this.checkFilterStatus('overdue', true)],
      complete: [this.checkFilterStatus('complete', true)],
      expired: [this.checkFilterStatus('expired', true)],
      'unassigned-tasks': [this.checkFilterStatus('unassigned-tasks', false)],
      'my-tasks': [this.checkFilterStatus('my-tasks', false)],
    });
    this.generalForm.valueChanges.subscribe(() => {
      this.changeFilter();
    });
  }

  createTeamForm(): void {
    this.loggedInUser = this.localStorage.getItem('user');
    this.organizationId = this.loggedInUser.CurrentOrg.ID;
    this.userService.getUsersByOrganization(this.organizationId).subscribe((value) => {
      this.allTeam = value;
      const teamGroup: any = {};
      value.forEach((user) => {
        teamGroup['_teamID' + user.ID] = new FormControl(this.checkFilterStatus('_teamID' + user.ID, false));
      });
      this.teamForm = new FormGroup(teamGroup);
      this.teamForm.valueChanges.subscribe(() => {
        this.changeFilter();
      });
    });
  }

  createRolesForm(): void {
    this.apiRoleService.getRolesByOrg(this.loggedInUser.CurrentOrg.ID).subscribe((response) => {
      this.allRoles = response.OrgRoleList;
      const roleGroup: any = {};
      roleGroup['none'] = new FormControl(false);
      if (response.OrgRoleList) {
        response.OrgRoleList.forEach((role) => {
          roleGroup['_roleID' + role.ID] = new FormControl(this.checkFilterStatus('_roleID' + role.ID, false));
        });
      }
      this.roleForm = new FormGroup(roleGroup);
      this.roleForm.valueChanges.subscribe(() => {
        this.changeFilter();
      });
    });
  }

  private checkFilterStatus(value: string, defaultValue: boolean): boolean {
    if (!this.localFilterArray) {
      return defaultValue;
    }
    return this.localFilterArray.find((localArray) => localArray.value === value).active;
  }
}
