import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'xenon-terms-and-services',
  styleUrls: ['./terms-and-services.component.scss'],
  template: `<mat-card>
    <div fxLayout="column" class="h-100">
      <div *ngIf="docType === 'none'; else doc" fxLayoutAlign="space-between" fxLayout="column" class="h-100">
        <span>
          <h5 class="text-center color-primary">Legal Documents</h5>
          <mat-action-list>
            <button mat-list-item (click)="docType = 'license'">
              <div class="w-100" fxLayoutAlign="space-between">License Agreement <mat-icon>chevron_right</mat-icon></div>
            </button>
            <button mat-list-item (click)="docType = 'terms'">
              <div class="w-100" fxLayoutAlign="space-between">Term and Conditions <mat-icon>chevron_right</mat-icon></div>
            </button>
            <button mat-list-item (click)="docType = 'privacy'">
              <div class="w-100" fxLayoutAlign="space-between">Privacy Policy <mat-icon>chevron_right</mat-icon></div>
            </button>
          </mat-action-list>
        </span>

        <div fxLayoutAlign="center">
          <button mat-stroked-button color="primary" class="py-2 mr-4" (click)="onDeny()">
            Log Out
          </button>
          <button mat-flat-button color="primary" class="py-2" (click)="onAccept()">
            Accept All
          </button>
        </div>
      </div>

      <ng-template #doc>
        <xenon-ui-user-terms-document [documentType]="docType" (back)="docType = 'none'"></xenon-ui-user-terms-document>
      </ng-template>
    </div>
  </mat-card>`,
})
export class TermsAndServicesComponent {
  public docType = 'none';

  constructor(public dialogRef: MatDialogRef<TermsAndServicesComponent>) {}

  public onDeny(): void {
    this.dialogRef.close(false);
  }

  public onAccept(): void {
    this.dialogRef.close(true);
  }
}
