import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { NgbCalendar, NgbDatepicker, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'xenon-ui-calendar-datepicker',
  styleUrls: ['./ui-calendar-datepicker.component.scss'],
  template: `
    <ngb-datepicker #dp [(ngModel)]="selectedDate" (dateSelect)="changeLocation()" [dayTemplate]="customDay"> </ngb-datepicker>
    <ng-template #customDay let-date let-currentMonth="currentMonth" let-today="today" let-selected="selected" let-disabled="disabled" let-focused="focused">
      <span
        class="custom-day"
        [class.focused]="focused"
        [class.selected]="selected"
        [class.today]="today"
        [class.current-month]="currentMonth"
        [class.not-current-month]="date.month !== currentMonth"
        [class.text-muted]="disabled"
      >
        {{ date.day }}
      </span>
    </ng-template>
  `,
})
export class UiCalendarDatepickerComponent implements OnChanges {
  @Input() selectedDate: NgbDateStruct;
  @Output() dateChange = new EventEmitter<NgbDateStruct>();

  @ViewChild('dp', { static: true }) datepicker: NgbDatepicker;

  changeLocation(): void {
    this.dateChange.emit(this.selectedDate);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes && changes.selectedDate && changes.selectedDate.currentValue) {
      const targetDate = changes.selectedDate.currentValue;
      this.datepicker.navigateTo({
        year: targetDate.year,
        month: targetDate.month,
        day: targetDate.day,
      });
    }
  }
}
