import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ApiUserService, Task, User } from '@xenon/api';
import { UiListSelectDialogService } from '../../ui-list-select/ui-list-select.service';
import { UtilLocalStorageService } from '@xenon/utils';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'xenon-ui-fields-assignments',
  styleUrls: ['./ui-fields-assignments.component.scss'],
  template: ` <div fxLayoutAlign="space-between">
      <span>
        <h6 class="text-subtitle1 mb-1" id="assignments">Assignments</h6>
        <p class="text-caption2">
          Select default assignments for a newly created task. The selected individual must be active in the task’s location at the time of creation to be
          assigned.
        </p>
      </span>
      <button mat-icon-button (click)="fieldExpanded = !fieldExpanded">
        <mat-icon>{{ fieldExpanded ? 'expand_more' : 'expand_less' }}</mat-icon>
      </button>
    </div>
    <div [ngClass]="fieldExpanded ? 'display-block' : 'display-none'">
      <div *ngIf="selectedUsers | async as selectedUsers">
        <div class="list-row" fxLayoutAlign="space-between center" *ngFor="let user of selectedUsers">
          <div fxLayoutAlign="start center">
            <xenon-ui-user-photo class="mt--30" [userArray]="[user]"></xenon-ui-user-photo>
            <p class="mb-0 text-body1 ml-3">{{ user.FirstName + ' ' + user.LastName }}</p>
          </div>
          <button (click)="removeUser(user)" mat-icon-button>
            <mat-icon>cancel</mat-icon>
          </button>
        </div>
      </div>
      <button mat-button (click)="addUser()" class="text-left color-primary mt-3 p-0">
        <mat-icon class="mr-3">add_circle_outline</mat-icon>
        Add Individual
      </button>
    </div>`,
})
export class UiFieldsAssignmentsComponent {
  @Input() set selectedTask(tasksByDate: Task) {
    this.selectedOrgID = tasksByDate.OrgID;
    this.selectedUsers.next(tasksByDate.AssignedUsers);
  }
  @Output() newUserList = new EventEmitter<User[]>();
  allUsersList: User[];
  selectedUsers: BehaviorSubject<User[]> = new BehaviorSubject([]);
  currentUser: User;
  showInput = false;
  selectedOrgID: number;

  fieldExpanded = true;

  constructor(
    private apiUserService: ApiUserService,
    private utilLocalStorageService: UtilLocalStorageService,
    private uiListSelectService: UiListSelectDialogService<User>
  ) {
    this.currentUser = this.utilLocalStorageService.getItem('user').CurrentUser;
  }

  emitList(): void {
    this.newUserList.emit(this.selectedUsers.value);
  }

  addUser() {
    this.apiUserService.getUsersByOrganization(this.selectedOrgID).subscribe((userList: User[]) => {
      this.uiListSelectService.open({
        inputList: userList,
        currentSelectedList: this.selectedUsers.value,
        primaryValueSelector1: 'FirstName',
        primaryValueSelector2: 'LastName',
        secondaryValueSelector: 'JobTitle',
        compareValueBy: 'ID',
        displayUserImage: true,
      });

      this.uiListSelectService.closed().subscribe((selectedUsers) => {
        if (!selectedUsers) return;
        this.selectedUsers.next(selectedUsers);
        this.emitList();
      });
    });
  }

  removeUser(user: User) {
    this.selectedUsers.next(this.selectedUsers.value.filter((selectedUser) => selectedUser.ID !== user.ID));
    this.emitList();
  }
}
