import { Injectable } from '@angular/core';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root',
})
export class UtilDateToolsService {
  constructor() {}

  // input
  // - dateString format = 2020-09-18T06:13:59Z
  // returns date
  static utcToLocal(dateString: string): Date {
    return moment(`${dateString.replace('T', ' ').replace('Z', '')} UTC`).toDate();
  }

  // returns YYYY-mm-dd
  static convertToDateString(date: Date): string {
    const year = date.getFullYear();
    const month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
    const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
    return `${year}-${month}-${day}`;
  }
}
