import { Component, Inject, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LoginOrgList } from '@xenon/api';

@Component({
  selector: 'xenon-pick-org-location',
  styleUrls: ['./pick-org-location.component.scss'],
  template: `<mat-card fxLayout="column" fxLayoutAlign="space-between">
    <div>
      <div *ngFor="let org of data">
        <span fxLayoutAlign="start center">
          <!-- <img [src]="logoLink(org.LogoURL)" class="logo mr-2" /> -->
          {{ org.OrgName }}
        </span>
        <mat-selection-list #otherOrg [multiple]="false">
          <mat-list-option *ngFor="let location of org.Locations" (click)="this.dialogRef.close(location.ID)">
            <div fxLayoutAlign="start center">
              <span class="text-body2">{{ location.Name }}</span>
            </div>
          </mat-list-option>
        </mat-selection-list>
      </div>
    </div>
    <div fxLayoutAlign="center">
      <button mat-stroked-button color="primary" class="py-2" (click)="back()">
        Back
      </button>
    </div>
  </mat-card>`,
})
export class PickOrgLocationComponent {
  constructor(@Inject(MAT_DIALOG_DATA) public data: LoginOrgList, public dialogRef: MatDialogRef<PickOrgLocationComponent>) {}

  public back(): void {
    this.dialogRef.close();
  }
}
