import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PermissionsAuthGuard } from '@xenon/auth';
import { AdminComponent } from './admin.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'locations',
    pathMatch: 'full',
  },
  {
    path: '',
    component: AdminComponent,
    children: [
      {
        path: 'locations',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./locations/locations.module').then((m) => m.LocationsModule),
      },
      {
        path: 'billing',
        loadChildren: () => import('./billing/billing.module').then((m) => m.BillingModule),
      },
      {
        path: 'users',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./users/users.module').then((m) => m.UsersModule),
      },
      {
        path: 'roles',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./roles/roles.module').then((m) => m.RolesModule),
      },
      {
        path: 'task-templates',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./task-templates/task-templates.module').then((m) => m.TaskTemplatesModule),
      },
      {
        path: 'form-templates',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./form-templates/form-templates.module').then((m) => m.FormTemplatesModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
