import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { NavigationEnd, Router } from '@angular/router';
import { UtilLocalStorageService } from '@xenon/utils';
import { AppList } from '@xenon/auth';

export interface SidebarItem {
  active: boolean;
  name: string;
  title: string;
  icon: string;
}

@Component({
  selector: 'xenon-sidebar',
  template: `
    <mat-list>
      <ng-container *ngFor="let item of items">
        <mat-list-item [class.active]="item.active" *ngIf="item.hasAccess">
          <a class="nav-item nav-link" (click)="setDocTitle(item.title)" routerLink="{{ item.name }}">
            <mat-icon color="iconic">{{ item.icon }}</mat-icon>
            {{ item.title }}
          </a>
        </mat-list-item>
      </ng-container>
    </mat-list>
  `,
})
export class SidebarComponent implements OnInit {
  items: SidebarItem[] = [];

  constructor(private titleService: Title, private router: Router, private localStorageService: UtilLocalStorageService) {
    // This is for the new web setup
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.initMenuItems();
      }
    });
  }

  ngOnInit(): void {
    this.initMenuItems();
  }

  // This is for the new web project setup.
  private initMenuItems(): void {
    this.items = this.localStorageService.getItem('menu');
    this.items.forEach((item) => {
      item.active = item.name === this.router.url.replace('/', '');
    });
  }

  setDocTitle(title: string): void {
    this.items.forEach((item) => {
      item.active = item.title === title;
    });
    this.titleService.setTitle('Xenon App | ' + title);
  }
}
