import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface RenameData {
  newName: string;
}

@Component({
  selector: 'xenon-rename-drive-item',
  template: `
    <h6 mat-dialog-title>Rename</h6>
    <div mat-dialog-content>
      <mat-form-field>
        <input matInput [(ngModel)]="data.newName" placeholder="New Name" />
      </mat-form-field>
    </div>
    <div mat-dialog-actions>
      <button mat-button (click)="onNoClick()">CANCEL</button>
      <button mat-button [mat-dialog-close]="data.newName" cdkFocusInitial>
        SAVE
      </button>
    </div>
  `,
})
export class RenameDriveItemModal {
  constructor(public dialogRef: MatDialogRef<RenameDriveItemModal>, @Inject(MAT_DIALOG_DATA) public data: RenameData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
}
