import { Component, EventEmitter, Input, Output } from '@angular/core';
import { OrganizationList, RoleLocations, Location } from '@xenon/api';

@Component({
  selector: 'xenon-switch-organization-row',
  styleUrls: ['./switch-organization-row.component.scss'],
  template: `
    <mat-expansion-panel hideToggle [expanded]="isExpanded">
      <mat-expansion-panel-header>
        <mat-panel-title class="text-subtitle2 mr-0" fxLayoutAlign="space-between center">
          <span fxLayoutAlign="start center">
            <img [src]="logoLink(organization.Org.LogoURL)" class="logo mr-2" />
            {{ organization.Org.Name }}
          </span>
          <mat-menu #menu="matMenu">
            <!-- <div mat-menu-item fxLayoutAlign="start center" (click)="menuClick.emit('starred')">
              <mat-icon class="color-gray icon-small material-icons-outlined">grade</mat-icon>
              <p class="mb-0">Add to Starred</p>
            </div> -->
            <div mat-menu-item fxLayoutAlign="start center" (click)="menuClick.emit('name')">
              <mat-icon class="color-gray icon-small material-icons-outlined">edit</mat-icon>
              <p class="mb-0">Edit Name</p>
            </div>
            <div mat-menu-item fxLayoutAlign="start center" (click)="menuClick.emit('logo')">
              <mat-icon class="color-gray icon-small material-icons-outlined">edit</mat-icon>
              <p class="mb-0">Edit Logo</p>
            </div>
            <mat-divider></mat-divider>
            <div mat-menu-item fxLayoutAlign="start center" (click)="menuClick.emit('delete')">
              <mat-icon class="color-gray icon-small material-icons-outlined">delete</mat-icon>
              <p class="mb-0">Delete</p>
            </div>
          </mat-menu>
          <button mat-icon-button (click)="stopProp($event)" [matMenuTriggerFor]="menu">
            <mat-icon class="color-gray icon-small">more_vert</mat-icon>
          </button>
        </mat-panel-title>
      </mat-expansion-panel-header>
      <mat-selection-list #otherOrg [multiple]="false">
        <mat-list-option
          *ngFor="let location of getLocations(organization.RoleLocations)"
          [selected]="checkCurrentLocation(location.ID)"
          (click)="switchLocation.emit(location.ID)"
        >
          <div fxLayoutAlign="start center">
            <mat-icon class="mr-3">{{ checkCurrentLocation(location.ID) ? 'check' : '' }}</mat-icon>
            <span class="text-body2">{{ location.Name }}</span>
          </div>
        </mat-list-option>
      </mat-selection-list>
    </mat-expansion-panel>
  `,
})
export class SwitchOrganizationComponentRow {
  @Input() isExpanded = false;
  @Input() organization: OrganizationList;
  @Input() currentLocation: number;
  @Output() menuClick = new EventEmitter<string>();
  @Output() switchLocation = new EventEmitter<number>();

  public orgObject: OrganizationList;

  public getLocations(roleLocations: RoleLocations[]): Location[] {
    let locationList = [];
    if (!roleLocations) return;
    roleLocations.forEach((roleLocation) => {
      if (!roleLocation.LocationList) return;
      roleLocation.LocationList.forEach((location) => {
        if (locationList.length === 0) {
          locationList = [location];
        }
        if (!locationList.some((list) => list.ID === location.ID)) {
          locationList.push(location);
        }
      });
    });
    return locationList;
  }

  public stopProp(event: Event): void {
    event.stopPropagation();
  }

  public checkCurrentLocation(locationID: number): boolean {
    return locationID === this.currentLocation;
  }

  public logoLink(url: string): string {
    return url.replace(/\s/g, '').length > 0 ? url : 'assets/logo-transparent.png';
  }
}
