import { Component, EventEmitter, Input, Output } from '@angular/core';

export interface DriveFilterItem {
  active: boolean;
  label: string;
  value: string;
}

@Component({
  selector: 'xenon-drive-filter',
  styleUrls: ['./drive-filter.component.scss'],
  template: `
    <div fxLayout="row" class="drive-filters">
      <div class="title-bar">
        <mat-chip-list aria-label="Filter selection">
          <mat-chip [selected]="item.active" *ngFor="let item of items" [class.active]="item.active" (click)="changeFilter(item.value)">
            {{ item.label }}
          </mat-chip>
        </mat-chip-list>
      </div>
    </div>
  `,
})
export class DriveFilterComponent {
  @Input() items: DriveFilterItem[];
  @Output() filterChange = new EventEmitter<DriveFilterItem[]>();

  changeFilter(filter: string): void {
    this.items.forEach((item) => {
      if (item.value === filter) {
        item.active = !item.active;
      }
    });
    this.filterChange.emit(this.items);
  }
}
