import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { UtilMomentService } from '@xenon/utils';
import { BaseApi } from '../environments/base-api';
import {
  CreateTaskRequest,
  Task,
  UpdateSubTaskNameRequest,
  UpdateSubTaskStatusRequest,
  UpdateSubTaskStatusResponse,
  UpdateTaskRequest,
  CreateTaskResponse,
} from '../models/task';
import { BaseResponse } from '../models/base-response';
import * as moment from 'moment';
import { TaskTemplate } from '../models/task-template';

export interface TasksByDate {
  active: boolean;
  dateString: string;
  tasks: Task[];
  dateIsToday: boolean;
}

@Injectable({
  providedIn: 'root',
})
export class ApiTaskService extends BaseApi {
  organizationId$ = new BehaviorSubject(1);
  locationId$ = new BehaviorSubject(1);
  stringDateRange$ = new BehaviorSubject({ start: null, end: null });

  constructor(private http: HttpClient) {
    super();
  }

  search$ = combineLatest([this.organizationId$, this.locationId$, this.stringDateRange$]).pipe(
    switchMap(([organizationId, locationId, stringDateRange]) => this.search(organizationId, locationId, stringDateRange))
  );

  getTasksListByDate$ = combineLatest([this.organizationId$, this.locationId$, this.stringDateRange$]).pipe(
    switchMap(([organizationId, locationId, stringDateRange]) => this.getTasksListByDate(organizationId, locationId, stringDateRange))
  );

  search(organizationId: number, locationId: number, stringDateRange: { start: string; end: string }): Observable<Task[]> {
    const url = `${this.baseUrl}/searchtasks`;
    const request = {
      OrgID: organizationId,
      LocationID: locationId,
      StartDate: stringDateRange.start,
      EndDate: stringDateRange.end,
    };
    return this.http.post<{ TaskList: Task[] }>(url, request).pipe(map((response: { TaskList: Task[] }) => response.TaskList));
  }

  getTasksListByDate(organizationId: number, locationId: number, stringDateRange: { start: string; end: string }): Observable<TasksByDate[]> {
    return this.search(organizationId, locationId, stringDateRange).pipe(
      map((response: Task[]) => {
        const tasksListByDate: TasksByDate[] = [];

        if (response) {
          const sortedTasks = response.slice().sort((a, b) => (b.AvailableDate < a.AvailableDate ? 1 : -1));

          let currentMonth = 0;
          sortedTasks.forEach((task) => {
            const localAvailableDate = UtilMomentService.new(task.AvailableDate).toDate();
            const dateString = UtilMomentService.new(localAvailableDate).format('YYYY-MM-DD');
            const dateIsToday = UtilMomentService.new(moment().toDate()).format('YYYY-MM-DD') === dateString;
            if (tasksListByDate.length) {
              let matchFound = false;
              let matchIndex = 0;
              tasksListByDate.forEach((mt, index) => {
                if (mt.dateString === dateString) {
                  matchFound = true;
                  matchIndex = index;
                }
              });

              if (matchFound) {
                tasksListByDate[matchIndex].tasks.push(task);
              } else {
                tasksListByDate.push({
                  dateString: dateString,
                  tasks: [task],
                  active: false,
                  dateIsToday: dateIsToday,
                });
              }
            } else {
              tasksListByDate.push({
                dateString: dateString,
                tasks: [task],
                active: false,
                dateIsToday: dateIsToday,
              });
            }
            currentMonth = localAvailableDate.getMonth();
          });
        }

        return tasksListByDate;
      })
    );
  }

  getOverdueTasksByLocation(locationId: number): Observable<Task[]> {
    const url = `${this.baseUrl}/tasks/overdue/location/${locationId}`;
    return this.http.get<{ Tasks: Task[] }>(url).pipe(map((response: { Tasks: Task[] }) => response.Tasks));
  }

  getOverdueTasksByOrganization(organizationId: number): Observable<Task[]> {
    const url = `${this.baseUrl}/tasks/overdue/org/${organizationId}`;
    return this.http.get<{ Tasks: Task[] }>(url).pipe(map((response: { Tasks: Task[] }) => response.Tasks));
  }

  getTasksByFormTemplate(formTemplateId: number): Observable<any> {
    const url = `${this.baseUrl}/tasktemplatesbyformtemplate/${formTemplateId}`;
    return this.http.get<any>(url).pipe(map((response: any) => response));
  }

  public addTaskTemplate(task: TaskTemplate): Observable<any> {
    const url = `${this.baseUrl}/tasktemplate`;
    return this.http.post<any>(url, task).pipe(
      map((response) => {
        return response;
      })
    );
  }

  public updateTaskTemplate(task: TaskTemplate): Observable<any> {
    const url = `${this.baseUrl}/tasktemplate/${task.ID}`;
    return this.http.patch<any>(url, task).pipe(
      map((response) => {
        return response;
      })
    );
  }

  public deleteTaskTemplate(taskTemplateId: number): Observable<any> {
    const url = `${this.baseUrl}/tasktemplate/${taskTemplateId}`;
    return this.http.delete<any>(url).pipe(
      map((response) => {
        return response;
      })
    );
  }

  /**
   * Update an existing task.
   * @param task The task to update.
   * @returns A promise containing the base response.
   */
  public updateTask(task: UpdateTaskRequest): Promise<BaseResponse> {
    const url = `${this.baseUrl}/task/${task.ID}`;
    return this.http.patch<BaseResponse>(url, task).toPromise();
  }

  public deleteTask(taskId: number): Observable<any> {
    const url = `${this.baseUrl}/task/${taskId}`;
    return this.http.delete<any>(url).pipe(
      map((response) => {
        return response;
      })
    );
  }

  /**
   * Used to update the status of a task (from the checklist panel).
   * This is NOT used for subtasks (only tasks).
   * Either marks a checklist task as "complete" or "incomplete".
   * @param taskID The task id.
   * @param userID The id of the user marking the task as complete.
   * @param markComplete Whether to mark the task as complete.
   * @returns A promise containing the base response.
   */
  public updateTaskStatus(taskID: number, userID: number, markComplete: boolean): Promise<BaseResponse> {
    if (markComplete) {
      const url = `${this.baseUrl}/checklisttaskcomplete/${taskID}/${userID}`;
      return this.http.get<BaseResponse>(url).toPromise();
    } else {
      const url = `${this.baseUrl}/checklisttaskincomplete/${taskID}/${userID}`;
      return this.http.get<BaseResponse>(url).toPromise();
    }
  }

  /**
   * Used to update the status of a subtask (from the checklist panel).
   * This is NOT used for tasks (only subtasks).
   * Either marks a checklist task as "complete" or "incomplete".
   * @param taskID The subtask id.
   * @param markComplete Whether to mark the subtask as complete.
   * @returns A promise containing the base response.
   */
  public updateSubTaskStatus(subTaskID: number, currentUserID: number, markComplete: boolean): Promise<BaseResponse> {
    if (markComplete) {
      const url = `${this.baseUrl}/subtaskmarkcomplete/${subTaskID}/${currentUserID}`;
      return this.http.get<BaseResponse>(url).toPromise();
    } else {
      const url = `${this.baseUrl}/subtaskmarkincomplete/${subTaskID}/${currentUserID}`;
      return this.http.get<BaseResponse>(url).toPromise();
    }
  }

  /**
   * Used to add a sub task to an existing task.
   * @param subTask The sub task to create.
   * @returns A promise containing the UpdateSubTaskResponse.
   */
  public addSubTask(subTask: UpdateSubTaskStatusRequest): Promise<UpdateSubTaskStatusResponse> {
    const url = `${this.baseUrl}/subtask`;
    return this.http.post<UpdateSubTaskStatusResponse>(url, subTask).toPromise();
  }

  /**
   * Used to add a task.
   * @param task The task to create.
   * @returns A promise containing the AddTaskResponse.
   */
  public addTask(task: CreateTaskRequest): Promise<CreateTaskResponse> {
    const url = `${this.baseUrl}/checklisttask`;
    return this.http.post<CreateTaskResponse>(url, task).toPromise();
  }

  /**
   * Update the name of a sub task.
   * @param request The information needed to update the sub task name.
   * @returns A promise containing the base response.
   */
  public updateSubTaskName(request: UpdateSubTaskNameRequest): Promise<BaseResponse> {
    const url = `${this.baseUrl}/subtask/${request.ID}`;
    return this.http.patch<BaseResponse>(url, request).toPromise();
  }

  /**
   * Delete an existing sub task.
   * @param id The id of the sub task to delete.
   * @returns A promise containing the base response.
   */
  public deleteSubTask(id: number): Promise<BaseResponse> {
    const url = `${this.baseUrl}/subtask/${id}`;
    return this.http.delete<BaseResponse>(url).toPromise();
  }

  /**
   * Assigns user to a task.
   * @param taskId The id of the task to assign to.
   * @param userID The id of the user to assign to.
   * @returns A promise containing the base response.
   */
  public assignTaskUser(body: { taskId: number; userID: number }): Promise<BaseResponse> {
    const url = `${this.baseUrl}/assigntaskuser`;
    return this.http.post<BaseResponse>(url, body).toPromise();
  }

  /**
   * Removes user to a task.
   * @param taskId The id of the task to assign to.
   * @param userID The id of the user to assign to.
   * @returns A promise containing the base response.
   */
  public removeTaskUser(body: { taskId: number; userID: number }): Promise<BaseResponse> {
    const url = `${this.baseUrl}/removetaskuser/${body.taskId}/${body.userID}`;
    return this.http.delete<BaseResponse>(url).toPromise();
  }
}
