import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'xenon-ui-form-field-table',
  styleUrls: ['./ui-form-field-table.component.scss'],
  template: `
    <mat-table #table [dataSource]="dataSource">
      <ng-container [matColumnDef]="tableData" *ngFor="let tableData of objectKeys(columnHeader)">
        <mat-header-cell *matHeaderCellDef>{{ columnHeader[tableData] }}</mat-header-cell>
        <mat-cell *matCellDef="let element; let i = index">
          <span>{{ element[tableData] }}</span>

          <span *ngIf="columnHeader[tableData] === 'Delete'" (click)="onDeleteRow(element, i)">
            <mat-icon color="primary">
              delete
            </mat-icon>
          </span>
        </mat-cell>
      </ng-container>

      <mat-header-row *matHeaderRowDef="objectKeys(columnHeader)"></mat-header-row>
      <mat-row *matRowDef="let row; columns: objectKeys(columnHeader)"></mat-row>
    </mat-table>
  `,
})
export class UiFormFieldTableComponent implements OnInit {
  @Input() tableData;
  @Input() columnHeader;
  @Output() deleteRow = new EventEmitter<any>();
  public objectKeys = Object.keys;
  public dataSource: MatTableDataSource<any>;

  constructor(private cdr: ChangeDetectorRef) {}

  ngOnInit() {
    this.dataSource = new MatTableDataSource(this.tableData);
  }

  public onDeleteRow(element, index) {
    const deleted = { data: element, index: index };
    this.deleteRow.emit(deleted);
  }

  public addElement(element) {
    this.tableData ? this.tableData.push(element) : (this.tableData = [element]);
    // this.tableData.push(element);
    this.dataSource = new MatTableDataSource(this.tableData);
  }

  public refreshTable(index) {
    this.tableData.splice(index, 1);
    this.dataSource = new MatTableDataSource(this.tableData);
    this.cdr.detectChanges();
  }
}
