import { Component, ElementRef, ViewChild, Input, AfterViewInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'xenon-ui-expanding-input',
  styleUrls: ['./ui-expanding-input.component.scss'],
  template: `
    <div #divToMeasure class="w-100">
      <mat-form-field [floatLabel]="floatLabel" [ngStyle]="{ 'width.px': width }" [ngClass]="formFieldClasses">
        <input
          autocomplete="off"
          #elasticInput
          matInput
          (input)="resize()"
          [disabled]="disabled"
          [placeholder]="placeholder"
          (blur)="fieldValue.emit(elasticInput.value)"
          [value]="startValue"
        />
      </mat-form-field>
      <span #hiddenText style="visibility: hidden; white-space: pre; position: absolute;">{{ elasticInput.value }}</span>
    </div>
  `,
})
export class UiExpandingInputComponent implements AfterViewInit {
  @ViewChild('hiddenText') textEl: ElementRef;
  @ViewChild('divToMeasure') divToMeasureElement: ElementRef;
  @Output() fieldValue = new EventEmitter<string>();
  @Input() startValue = '';
  @Input() minWidth = 100;
  @Input() formFieldClasses: string[];
  @Input() disabled = false;
  @Input() placeholder: string;
  @Input() floatLabel = 'always';

  public width: number;
  public maxWidth: number;

  public ngAfterViewInit(): void {
    this.maxWidth = this.divToMeasureElement.nativeElement.offsetWidth;
    this.resize();
  }

  public resize(): void {
    setTimeout(() => {
      const calcMaxWidth = Math.max(this.minWidth, this.textEl.nativeElement.offsetWidth);
      this.width = calcMaxWidth >= this.maxWidth ? this.maxWidth : calcMaxWidth;
    });
  }
}
