import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'xenon-leave-app-dialog',
  template: `
    <h6 mat-dialog-title>You will be leaving the app</h6>
    <div mat-dialog-actions>
      <button mat-button (click)="onNoClick()">No Thanks</button>
      <button mat-button (click)="onOkClick()">Ok</button>
    </div>
  `,
})
export class LeaveAppDialogComponent {
  constructor(public dialogRef: MatDialogRef<LeaveAppDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: { agreed: boolean }) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  onOkClick() {
    this.dialogRef.close({ agreed: true });
  }
}
