import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatCardModule } from '@angular/material/card';
import { MatButtonModule } from '@angular/material/button';
import { MatInputModule } from '@angular/material/input';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatIconModule } from '@angular/material/icon';

import { PageLoginComponent } from './pages/page-login/page-login.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { UtilsModule } from '@xenon/utils';
import { ResetPasswordComponent } from './pages/reset-password/reset-password.component';
import { CheckYourEmailComponent } from './pages/check-your-email-dialog/check-your-email.component';
import { NewAccountComponent } from './pages/new-account/new-account.component';
import { PasswordResetFromEmailComponent } from './pages/password-reset-from-email/password-reset-from-email.component';
import { TermsAndServicesComponent } from './pages/terms-and-services/terms-and-services.component';
import { MatListModule } from '@angular/material/list';
import { UiUserTermsDocumentModule } from '@xenon/ui';
import { PickOrgLocationComponent } from './pages/pick-org-location/pick-org-location.component';

const authComponents = [
  PageLoginComponent,
  PageNotFoundComponent,
  ResetPasswordComponent,
  PasswordResetFromEmailComponent,
  CheckYourEmailComponent,
  NewAccountComponent,
  TermsAndServicesComponent,
  PickOrgLocationComponent,
];

@NgModule({
  declarations: [...authComponents],
  providers: [],
  exports: [...authComponents],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    MatFormFieldModule,
    MatCardModule,
    MatButtonModule,
    MatInputModule,
    MatSnackBarModule,
    MatListModule,
    UtilsModule,
    FlexLayoutModule,
    MatIconModule,
    UiUserTermsDocumentModule,
  ],
})
export class AuthModule {
  static forRoot(environment: any): ModuleWithProviders<AuthModule> {
    return {
      ngModule: AuthModule,
      providers: [
        {
          provide: 'env',
          useValue: environment,
        },
      ],
    };
  }
}
