import { Component, OnDestroy, OnInit } from '@angular/core';
import { AppBase } from '../../../app-base';
import { BehaviorSubject } from 'rxjs';
import { UtilLocalStorageService } from '@xenon/utils';
import { AddContentRequest, AddContentResponse, ApiDriveService, BaseResponse, ContentType, DriveItem } from '@xenon/api';
import { DriveFilterItem } from './drive-filter/drive-filter.component';
import { AddFolderModalComponent } from './add-folder-modal.component';
import { NotificationType, UiNotificationService } from '@xenon/ui';
import { MatDialog } from '@angular/material/dialog';
import { TextFileComponent } from '../../../shared/components/text-file/text-file.component';
import { PermissionsHelperService } from '@xenon/auth';

@Component({
  selector: 'xenon-drive',
  styleUrls: ['./drive.component.scss'],
  template: `
    <div class="page-header" fxLayout="row" fxLayoutAlign="between center">
      <h5 class="title">Drive</h5>
      <mat-divider></mat-divider>

      <xenon-drive-filter [items]="filterItems$ | async" (filterChange)="filterChange($event)"></xenon-drive-filter>
    </div>

    <p class="error color-red" [hidden]="!errorMessage">{{ errorMessage }}</p>

    <div class="page-body">
      <xenon-drive-explorer
        [driveItems]="driveItems$ | async"
        (driveItemClicked)="driveItemClicked($event)"
        (showError)="handleError($event)"
        (moveDriveItem)="moveDriveItem($event)"
        (refreshDrive)="getDrives()"
      ></xenon-drive-explorer>

      <button
        *ngIf="permissionsHelperService.hasPermissionByName('createAddFiles')"
        mat-fab
        mat-flat-button
        class="add-button"
        color="primary"
        aria-label="Create new file or folder"
        (click)="toggleAddButtons()"
      >
        <mat-icon>add</mat-icon>
      </button>

      <div [hidden]="hideAddButtons">
        <input hidden id="fileInput" type="file" (change)="addFile($event.target.files[0])" />
        <button mat-fab mat-flat-button class="new-file" color="basic" aria-label="Create new file" (click)="clickToAddFile()">
          <mat-icon>publish</mat-icon>
        </button>

        <button mat-fab mat-flat-button class="new-folder" color="basic" aria-label="Create new folder" (click)="addFolder()">
          <mat-icon>folder_open</mat-icon>
        </button>

        <button mat-fab mat-flat-button class="new-text-file" color="basic" aria-label="Create new file" (click)="addTextFile()">
          <mat-icon fontSet="material-icons-outlined">insert_drive_file</mat-icon>
        </button>
      </div>
    </div>
  `,
})
export class DriveComponent extends AppBase implements OnInit, OnDestroy {
  public hideAddButtons = true;
  public errorMessage = undefined;

  driveItems$: BehaviorSubject<DriveItem[][]> = new BehaviorSubject<DriveItem[][]>([]);

  filterItems = [
    {
      active: false,
      label: 'Starred',
      value: 'starred',
    },
  ];

  filterItems$: BehaviorSubject<DriveFilterItem[]> = new BehaviorSubject(this.filterItems);

  private selectedFolderID: number;

  constructor(
    private notificationService: UiNotificationService,
    private localStorage: UtilLocalStorageService,
    private driveService: ApiDriveService,
    private dialog: MatDialog,
    public permissionsHelperService: PermissionsHelperService
  ) {
    super(localStorage);
  }

  ngOnInit(): void {
    this.getDrives();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((s) => s.unsubscribe());
  }

  public toggleAddButtons(): void {
    this.hideAddButtons = !this.hideAddButtons;
  }

  public clickToAddFile(): void {
    document.getElementById('fileInput').click();
    this.toggleAddButtons(); // hide buttons
  }

  public addFile(file: File) {
    console.log(file);
    if (file) {
      const contentType: ContentType = 3;

      /* tslint:disable-next-line */
      let requestInfo: AddContentRequest = {
        Name: file.name,
        ContentType: contentType,
        ParentID: this.selectedFolderID,
        OrgID: this.organizationId,
        Description: '',
        Content: '',
        StarredInd: false,
        ContentPath: '',
        PreviewPath: '',
      };

      this.driveService
        .addFile(requestInfo, file)
        .then((success: boolean) => {
          if (success) {
            let driveItem: DriveItem;
            const all = this.driveItems$.getValue();
            all.forEach((driveI) => {
              driveI.forEach((i) => {
                if (i.id === this.selectedFolderID) driveItem = i;
              });
            });

            this.getDrives(driveItem);
          } else {
            this.showUploadError(`${file?.name} upload failed.`);
          }
        })
        .catch((err) => {
          this.showUploadError(`${file?.name} upload failed.`);
        });
    }
  }

  public addFolder() {
    this.toggleAddButtons(); // hide buttons

    const folderInfo: AddContentRequest = {
      Name: '',
      ContentType: 1,
      OrgID: this.organizationId,
      Description: '',
      Content: '',
      StarredInd: false,
      ContentPath: '',
      PreviewPath: '',
      ParentID: this.selectedFolderID,
    };

    const dialogRef = this.dialog.open(AddFolderModalComponent, {
      width: '250px',
      data: { folderName: folderInfo.Name },
    });

    dialogRef.afterClosed().subscribe((folderName) => {
      if (folderName) {
        folderInfo.Name = folderName;
        this.saveFolder(folderInfo, this.selectedFolderID);
      }
    });
  }

  public addTextFile() {
    this.toggleAddButtons(); // hide buttons

    const fileInfo: AddContentRequest = {
      Name: '',
      ContentType: 2,
      OrgID: this.organizationId,
      Description: '',
      Content: '',
      StarredInd: false,
      ContentPath: '',
      PreviewPath: '',
      ParentID: this.selectedFolderID,
    };

    const dialogRef = this.dialog.open(TextFileComponent, {
      data: { addContentRequest: fileInfo, editFile: true },
      panelClass: 'full-page-dialog',
    });

    dialogRef.afterClosed().subscribe(() => {
      this.getDrives();
    });
  }

  public driveItemClicked(item: DriveItem) {
    // TODO: This is messy START ---------------------

    const driveItems = this.driveItems$.getValue();
    const newDriveItems = [];
    let itemLevel = 0;
    let itemFound = false;
    driveItems.forEach((items) => {
      items.forEach((i) => {
        if (i.id === item.id) {
          itemFound = true;
        }
      });
      if (!itemFound) {
        itemLevel++;
      }
    });
    driveItems.forEach((items, index) => {
      if (index <= itemLevel) {
        newDriveItems.push(items);
      }
    });

    this.driveItems$.next(newDriveItems);

    // TODO: This is messy END ---------------------

    if (item.icon === 'folder') {
      this.selectedFolderID = item.details.ID;
      if (item.details.ID) {
        const sub = this.driveService.getDriveFolderContent(item.details.ID, this.driveItems$.getValue()).subscribe((r) => this.driveItems$.next(r));
        this.subscriptions.push(sub);
      }
    } else this.selectedFolderID = null;
  }

  public moveDriveItem(info: { id: number; parentId: number; parentItem?: DriveItem }) {
    this.driveService
      .moveDriveItem(info.id, info.parentId)
      .then((response: BaseResponse) => {
        this.getDrives(info.parentItem);
      })
      .catch((err) => {
        this.handleError('An error occurred moving drive item.');
      });
  }

  public filterChange(event: DriveFilterItem[]): void {
    const starActive = event[0].active;
    const driveItems = this.localStorage.getItem('drives');
    if (starActive) {
      const newDriveItems: DriveItem[][] = [];
      driveItems.forEach((drives) => {
        const newDrives = [];
        drives.forEach((d) => {
          if (d.starred) {
            newDrives.push(d);
          }
        });
        newDriveItems.push(newDrives);
      });
      this.driveItems$.next(newDriveItems);
    } else {
      this.driveItems$.next(driveItems);
    }
    this.filterItems$.next(event);
  }

  public handleError(errorMessage: string): void {
    this.errorMessage = errorMessage;

    setTimeout(() => {
      this.errorMessage = null;
    }, 5000);
  }

  /**
   * @param driveToSelect After getting the latest drives, this will select a certain folder.
   */
  public getDrives(driveToSelect?: DriveItem): void {
    const driveServiceSub = this.driveService.getDrivesByOrg(this.organizationId).subscribe((r) => {
      this.localStorage.setItem('drives', r);
      this.driveItems$.next(r);
      if (driveToSelect) {
        this.driveItemClicked(driveToSelect);
      }
    });
    this.subscriptions.push(driveServiceSub);
  }

  private getDriveItem(request: AddContentRequest, isFolder: boolean, id: number): DriveItem {
    const driveItem: DriveItem = {
      id: id,
      name: request.Name,
      icon: isFolder ? 'folder' : 'file',
      starred: false,
      active: false,
      details: {
        ID: id,
        OrgID: request.OrgID,
        LocationID: 0,
        Name: request.Name,
        Description: request.Description,
        ContentType: request.ContentType,
        Content: request.Content,
        StarredInd: request.StarredInd,
        ContentPath: request.ContentPath,
        PreviewPath: request.PreviewPath,
        MetaList: null,
        ParentID: request.ParentID,
      },
    };

    return driveItem;
  }

  private saveFolder(folderInfo: AddContentRequest, currentFolderId: number) {
    this.driveService
      .saveContentInfo(folderInfo)
      .then((response: AddContentResponse) => {
        const driveItem = this.getDriveItem(folderInfo, true, currentFolderId);
        this.getDrives(driveItem);
      })
      .catch((err) => {
        this.showUploadError('Error: Folder Not Created.');
      });
  }

  private showUploadError(message: string) {
    this.notificationService.show(NotificationType.error, 'Error', message, 5000);
  }
}
