import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BehaviorSubject, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import * as _ from 'lodash-es';

export interface UiListSelectOptions<T> {
  inputList: T[];
  currentSelectedList?: T[];
  primaryValueSelector1: string;
  primaryValueSelector2?: string;
  secondaryValueSelector?: string;
  compareValueBy: string;
  displayUserImage?: boolean;
  iconClass?: string;
}

@Component({
  selector: 'xenon-ui-list-select',
  styleUrls: ['./ui-list-select.component.scss'],
  template: `
    <form class="expand-form-line-past-dialog-padding">
      <mat-form-field class="w-100 mt--17" floatLabel="never">
        <input type="text" placeholder="Search" matInput [formControl]="myControl" [matAutocomplete]="auto" />
        <mat-autocomplete #auto="matAutocomplete"> </mat-autocomplete>
      </mat-form-field>
    </form>
    <mat-dialog-content class="mt--17 h-100">
      <div class="scroll-area">
        <p class="text-caption2 mt-2 mb-0">Selected</p>
        <mat-action-list>
          <button mat-list-item (click)="removeItemFromList(item)" *ngFor="let item of selectedList | async" class="ml--23">
            <div fxLayout="row" class="w-100">
              <xenon-ui-user-photo class="mt--5" *ngIf="data.displayUserImage; else icon" [userArray]="[item]"></xenon-ui-user-photo>
              <div #icon *ngIf="data.iconClass" fxLayoutAlign="center center" class="icon background-primary">
                <mat-icon class="text-white">{{ data.iconClass }}</mat-icon>
              </div>
              <span fxLayoutAlign="start"
                ><p class="text-body1 ml-3 mb-0">{{ item[data.primaryValueSelector1] }}</p>
                <p class="text-body1 ml-1 mb-0" *ngIf="item[data.primaryValueSelector2]">{{ item[data.primaryValueSelector2] }}</p></span
              >
            </div>
            <p class="text-body1 no-wrap mb-0" *ngIf="item[data.secondaryValueSelector]">{{ item[data.secondaryValueSelector] }}</p>
          </button>
        </mat-action-list>

        <mat-divider *ngIf="(selectedList | async).length > 0" class="expand-past-dialog-padding"></mat-divider>

        <p class="text-caption2 mt-2 mb-0">Available</p>
        <mat-action-list>
          <button mat-list-item (click)="addItemToList(item)" *ngFor="let item of filteredList | async" class="ml--23">
            <div fxLayout="row" class="w-100">
              <xenon-ui-user-photo class="mt--5" *ngIf="data.displayUserImage" [borderPrimary]="false" [userArray]="[item]"></xenon-ui-user-photo>
              <div #icon *ngIf="data.iconClass" fxLayoutAlign="center center" class="icon background-border-light">
                <mat-icon class="color-gray">{{ data.iconClass }}</mat-icon>
              </div>
              <span fxLayoutAlign="start center"
                ><p class="text-body1 ml-3 mb-0">{{ item[data.primaryValueSelector1] }}</p>
                <p class="text-body1 ml-1 mb-0" *ngIf="item[data.primaryValueSelector2]">{{ item[data.primaryValueSelector2] }}</p></span
              >
            </div>
            <p class="text-body1 no-wrap mb-0" *ngIf="item[data.secondaryValueSelector]">{{ item[data.secondaryValueSelector] }}</p>
          </button>
        </mat-action-list>
      </div>
    </mat-dialog-content>
    <mat-dialog-actions fxLayoutAlign="space-between center" class="p-0 border-top expand-past-dialog-padding">
      <p class="text-caption2 mb-0 ml-3 color-gray">{{ (selectedList | async).length }} Assigned</p>
      <span>
        <button mat-button color="primary" (click)="dialogRef.close()">Cancel</button>
        <button mat-button (click)="dialogRef.close(this.selectedList.getValue())" color="primary">Select</button>
      </span>
    </mat-dialog-actions>
  `,
})
export class UiListSelectComponent<T> implements OnInit {
  @Output() selectedItems = new EventEmitter<T[]>();

  filteredList: Observable<T[]>;
  selectedList: BehaviorSubject<T[]> = new BehaviorSubject([]);
  myControl = new FormControl();

  constructor(public dialogRef: MatDialogRef<UiListSelectComponent<T>>, @Inject(MAT_DIALOG_DATA) public data: UiListSelectOptions<T>) {
    this.filteredList = this.myControl.valueChanges.pipe(
      startWith(''),
      map((value) => this._filter(value))
    );
  }

  ngOnInit() {
    if (!this.data.currentSelectedList) return;
    if (this.data.currentSelectedList.length > 0)
      this.data.currentSelectedList.forEach((element) => {
        this.addItemToList(element);
      });
  }

  addItemToList(item: T): void {
    if (this.selectedList.getValue().length === 0) {
      this.selectedList.next([item]);
    } else {
      this.selectedList.next([...this.selectedList.value, item]);
    }
    this.myControl.updateValueAndValidity({ onlySelf: false, emitEvent: true });
  }

  removeItemFromList(item: T): void {
    this.selectedList.next(
      this.selectedList.value.filter((selectedItem) => {
        if (selectedItem !== item) {
          return item;
        }
      })
    );
    this.myControl.updateValueAndValidity({ onlySelf: false, emitEvent: true });
  }

  private _filter(value: string): T[] {
    if (!value) {
      return this.data.inputList.filter((item) => {
        if (!this.selectedList.value.some((selectedItem) => selectedItem[this.data.compareValueBy] === item[this.data.compareValueBy])) {
          return item;
        }
      });
    }
    const filterValue = value.toLowerCase();

    return this.data.inputList.filter((item) => {
      if (
        (item[this.data.primaryValueSelector1].toLowerCase().includes(filterValue) ||
          (item[this.data.primaryValueSelector2] && item[this.data.primaryValueSelector2].toLowerCase().includes(filterValue))) &&
        !this.selectedList.value.some((selectedItem) => selectedItem[this.data.compareValueBy] === item[this.data.compareValueBy])
      ) {
        return item;
      }
    });
  }
}
