import { Inject, Injectable } from '@angular/core';
import { UtilLocalStorageService, UtilWindowRefService } from '@xenon/utils';
import { AppList } from '../enums/app-list';
import { PermissionsHelperService } from './permissions-helper.service';

@Injectable({
  providedIn: 'root',
})
export class AppNavigationService {
  currentApp: AppList;

  menuItems = {
    client: [
      {
        active: true,
        name: 'client/home',
        title: 'Home',
        icon: 'home',
        hasAccess: true,
      },
      {
        active: false,
        name: 'client/tasks',
        title: 'Tasks',
        icon: 'check_box',
        hasAccess: true,
      },
      {
        active: false,
        name: 'client/forms',
        title: 'Forms',
        icon: 'content_paste',
        hasAccess: true,
      },
      {
        active: false,
        name: 'client/drive',
        title: 'Drive',
        icon: 'folder',
        hasAccess: this.permissionsHelperService.hasPermissionByName('accessDrive'),
      },
      {
        active: false,
        name: 'client/team',
        title: 'Team',
        icon: 'people',
        hasAccess: this.permissionsHelperService.hasPermissionByName('viewTeam'),
      },
    ],
    admin: [
      {
        active: false,
        name: 'admin/locations',
        title: 'Locations',
        icon: 'location_on',
        hasAccess: this.permissionsHelperService.hasPermissionByName('manageOrganization'),
      },
      {
        active: false,
        name: 'admin/billing',
        title: 'Billing',
        icon: 'credit_card',
      },
      {
        active: false,
        name: 'admin/users',
        title: 'Users',
        icon: 'people',
        hasAccess: this.permissionsHelperService.hasPermissionByName('manageUsers'),
      },
      {
        active: false,
        name: 'admin/roles',
        title: 'Roles',
        icon: 'folder',
        hasAccess: this.permissionsHelperService.hasPermissionByName('manageUsers'),
      },
      {
        active: false,
        name: 'admin/task-templates',
        title: 'Tasks',
        icon: 'check_box',
        hasAccess: this.permissionsHelperService.hasPermissionByName('manageTaskTemplates'),
      },
      {
        active: false,
        name: 'admin/form-templates',
        title: 'Forms',
        icon: 'filter_none',
        hasAccess: this.permissionsHelperService.hasPermissionByName('manageFormTemplates'),
      },
    ],
    formBuilder: [],
  };

  constructor(
    private permissionsHelperService: PermissionsHelperService,
    private utilWindowRefService: UtilWindowRefService,
    private localStorage: UtilLocalStorageService,
    @Inject('env') private env
  ) {
    this.setApp(AppList.Client);
  }

  public setApp(app: AppList): void {
    this.currentApp = app;
    switch (this.currentApp) {
      case AppList.Client:
        this.localStorage.setItem('menu', this.menuItems.client);
        break;
      case AppList.Admin:
        this.localStorage.setItem('menu', this.menuItems.admin);
        break;
      case AppList.FormBuilder:
        this.localStorage.setItem('menu', this.menuItems.formBuilder);
        break;
    }
  }

  public isAdmin(): boolean {
    return this.currentApp === AppList.Admin;
  }

  public isClient(): boolean {
    return this.currentApp === AppList.Client;
  }

  public isFormBuilder(): boolean {
    return this.currentApp === AppList.FormBuilder;
  }

  public navigateToApp(app: AppList, append2Url?: string): void {
    this.setApp(app);
    const baseUrl = `${this.env['appUrls'][app]}`;
    const targetUrl = append2Url && append2Url.length ? `${baseUrl}${append2Url}` : `${baseUrl}`;
    this.utilWindowRefService.nativeWindow.location.replace(targetUrl);
  }
}
