import { Organization, OrganizationList, LoginOrgList } from './organization';
import { BaseResponse } from './base-response';
import { RoleLocations, Location } from './location';

export enum Permissions {
  Viewer = '0',
  Editor = '50',
  Administrator = '100',
  Owner = '200',
}

export interface LoggedInUser extends BaseResponse {
  AccessToken: string;
  RefreshToken: string;
  CurrentLocation: Location;
  CurrentOrg: Organization;
  CurrentUser: User;
  LocationRoles: [{ ID: number; Name: string }];
  AggregatePermissions: string[];

  Locations?: LoginOrgList; //This will come back if they don't have a default org location
}

export interface User {
  Email: string;
  FirstName: string;
  ID: number;
  LastName: string;
  Orgs: OrganizationList[];
  Password: string;
  Phone: string;
  PhotoURL: string;
  PrimaryLocationID: number; // This is their default location NOT their current
  PrimaryOrgID: number; // This is their default Organization NOT their current
  TermsAccepted: number; //0 means they haven't accepted
  // These properties are set on the front end based on the currently selected Organization / Location.
  RoleLocations?: RoleLocations[];
  JobTitle?: string;
  Org?: Organization;
}

export interface CreateUserRequest {
  FirstName: string;
  LastName: string;
  Email: string;
  OrgID: number;
  JobTitle: string;
  Permission: Permissions;
  CreatedBy: number;
  RoleLocations: [{ RoleID: number; LocationID: number }];
}

export interface UpdateUserRequest {
  ID: number;
  FirstName?: string;
  LastName?: string;
  Email?: string;
  Phone?: string;
  OrgID?: number;
  PrimaryLocationID?: number;
}

export interface UpdateOrgUserRequest {
  ID: number;
  UserID: number;
  OrgID: number;
  Permission?: string;
  JobTitle?: string;
  RoleLocations?: [{ RoleID: number; LocationID: number }];
}

export interface CreateUserResponse extends BaseResponse {
  NewUserID: number;
}

export interface GetOrgUserLocationResponse extends BaseResponse {
  UserLocationRoles: RoleLocations[];
}
export interface UpdateOrgUserLocationRequest {
  OrgID: number;
  LocationID: number;
  RoleID: number;
  XenUserID: number;
  LocationName: string;
  RoleName: string;
}

export interface UploadPhotoResponse extends BaseResponse {
  Uploaded: string; // the new URL
}

export interface UpdateUserPrimarySettingsRequest {
  ID: number; // This is the User ID
  OrgID: number;
  LocationID: number;
}

export interface ChangePasswordRequest {
  email: string;
  password: string;
  newpassword: string;
  newpassword2: string;
}

export interface ChangePasswordFromEmailRequest {
  Email: string;
  UUID: string;
  OrgId: number;
  NewPassword: string;
  NewPassword2: string;
  AI: number;
}

export interface RefreshTokenRequest {
  AccessToken: string;
  RefreshToken: string;
}
