import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import {
  Organization,
  CreateOrgRequest,
  CreateOrgResponse,
  UpdateOrgRequest,
  BaseResponse,
  GetOrgResponse,
  GetAllOrgResponse,
  ScaffoldOrgRequest,
  ScaffoldOrgResponse,
  UpdateOrgLogoResponse,
} from '@xenon/api';

import { BaseApi } from '../environments/base-api';

@Injectable({
  providedIn: 'root',
})
export class ApiOrganizationService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  /**
   * Used to get all organizations.
   * @returns An observable containing an array of organizations.
   */
  public getAllOrgs(): Observable<Organization[]> {
    const url = `${this.baseUrl}/org`;
    return this.http.get<GetAllOrgResponse>(url).pipe(map((response: GetAllOrgResponse) => response.OrgList));
  }

  /**
   * Used to get a single organization by ID.
   * @param id The ID of the organization to get.
   * @returns An observable containing an array of organizations.
   */
  public getOrg(id: number): Observable<Organization> {
    const url = `${this.baseUrl}/org/${id}`;

    return this.http.get<GetOrgResponse>(url).pipe(map((response: GetOrgResponse) => (response.Org ? response.Org : null)));
  }

  /**
   * Used to create a new organization in the system.
   * @param newOrgInfo The request info.
   * @returns An observable with the CreateOrgResponse.
   */
  public createOrg(newOrgInfo: CreateOrgRequest): Observable<CreateOrgResponse> {
    const url = `${this.baseUrl}/org`;
    return this.http.post<CreateOrgResponse>(url, newOrgInfo);
  }

  /**
   * @param id The organization's ID.
   * @param updateInfo The organization info to update.
   * @returns An observable with the BaseResponse.
   */
  public updateOrg(id: number, updateInfo: UpdateOrgRequest): Observable<BaseResponse> {
    const url = `${this.baseUrl}/org/${id}`;
    return this.http.patch<BaseResponse>(url, updateInfo);
  }

  /**
   * @param id The organization's ID.
   * @param logoFile The file of organizations logo.
   * @returns An observable with the BaseResponse.
   */
  public updateOrgLogo(id: number, logoFile: File): Observable<UpdateOrgLogoResponse> {
    const url = `${this.baseUrl}/uploadorglogo/${id}`;

    const formData = new FormData();
    formData.append('logoFile', logoFile);

    return this.http.post<UpdateOrgLogoResponse>(url, formData);
  }

  /**
   * @param id The organization's ID.
   * @returns An observable with the BaseResponse.
   */
  public deleteOrg(id: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/org/${id}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * Used to Scaffold a new organization in the system.
   * @param newOrgInfo The request info.
   * @returns An observable with the ScaffoldOrgResponse.
   */
  public scaffoldOrg(newOrgInfo: ScaffoldOrgRequest): Observable<ScaffoldOrgResponse> {
    const url = `${this.baseUrl}/org/scaffold`;
    return this.http.post<ScaffoldOrgResponse>(url, newOrgInfo);
  }
}
