import { Component, Input, OnInit, EventEmitter, Output, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiOrganizationService, Organization, Location } from '@xenon/api';

@Component({
  selector: 'xenon-locations-select',
  styleUrls: ['locations-select.component.scss'],
  template: `
    <mat-form-field appearance="fill">
      <mat-label>Locations</mat-label>
      <mat-select [(value)]="selected" (selectionChange)="changeLocation()" required>
        <mat-option [value]="option.ID" *ngFor="let option of locations$ | async">{{ option.Name }}</mat-option>
      </mat-select>
    </mat-form-field>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class LocationsSelectComponent implements OnInit {
  @Input() organizationId: number;
  @Input() locationId: number;

  @Output() locationChange = new EventEmitter<number>();

  selected: number;
  locations$: Observable<Location[]>;

  constructor(private organizationService: ApiOrganizationService) {}

  ngOnInit(): void {
    this.locations$ = this.organizationService.getOrg(this.organizationId).pipe(
      map((response: Organization) => {
        this.selected = response.LocationList && response.LocationList.length ? response.LocationList[0].ID : undefined;
        return response.LocationList;
      })
    );
  }

  changeLocation() {
    this.locationChange.emit(this.selected);
  }
}
