import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'xenon-search-pipe',
})
export class SearchPipe implements PipeTransform {
  /**
   * Used for on page search / filtering
   * @param dataToSearch The list of objects to search through
   * @param filterText The text to search by
   * @param columnsToFilterBy The parameters in the 'dataToSearch' that you'd like to search by
   */
  public transform(
    dataToSearch: any[],
    filterText: string,
    columnsToFilterBy: string[]
  ) {
    if (filterText !== '') {
      return dataToSearch
        ? dataToSearch.filter((item) => {
            let contains = false;

            for (let i = 0; i < columnsToFilterBy.length; i++) {
              const column = columnsToFilterBy[i];
              if (column === 'FullName')
                contains =
                  (item['FirstName'] + ' ' + item['LastName'])
                    .toString()
                    .search(new RegExp(filterText, 'i')) > -1;
              else
                contains =
                  item[column]?.toString().search(new RegExp(filterText, 'i')) >
                  -1;
              if (contains) break;
            }

            return contains;
          })
        : [];
    } else {
      return dataToSearch;
    }
  }
}
