import { BaseResponse } from './base-response';
import { Organization } from './organization';
import { Role } from './role';
import { User } from './user';

export type Status = 'success' | 'error' | 'empty';

export interface Task {
  ID: number;
  OrgID: number;
  LocationID: number;
  TaskType: number;
  TaskTypeID: number;
  Name: string;
  Description: string;
  SubTaskList: SubTask[];
  AvailableDate: string;
  AvailableTime: string;
  DueDate: string;
  DueTime: string;
  ExpireDate: string;
  ExpireTime: string;
  CompletedAt: string;
  Completed: boolean;
  CompletedBy: number; // user-id
  CompletedOnTime: boolean;
  CurrentStatus: string;
  Expired: boolean;
  AssignedRoles: Role[];
  AssignedUsers: User[];
  ItemsComplete: number;
  ItemsTotal: number;
  ItemsCompletedLate: number;
  ItemsRemaining: number;
  ItemsExpired: number;
  CreatedAt: string;
  UpdatedAt: string;
  DeletedAt: string;
  CreatedBy: number; // user-id
}

export interface SubTask {
  ID: number;
  TaskID: number;
  SubTaskDesc: string;
  CompletedAt: string; // date
  CreatedBy: number;
  DisplayOrder?: number;
}

export interface TaskRecurrence {
  ID?: number;
  AvailableTime: string; // 24 hour format, ie 15:30
  CreatedBy?: number;
  DaysOfMonth: string;
  DaysOfWeek: string; //0 is all, 1=Monday etc. list of days 1,2,3
  MonthRelativeDays: string;
  RepeatUnit: number; // 3-days, 4 - weeks, 5-months (1-minutes, 2-hours are not currently used on the front end)
  RepeatValue: number; //2,10,7
  TaskTemplateID: number;
  StartDate: string; // "YYYY-MM-DD" - this needs to be a date ISO string.
  EndDate: string; // "YYYY-MM-DD" - this is just a string.
}

export interface AddTaskRecurrenceRequest extends TaskRecurrence {
  DueTime: string; // 24 hour format, ie 15:30
  DueDays: number; // default 0, if it is due 2 days in the future it would be 2
  ExpireTime: string; // 24 hour format, ie 15:30
  ExpireDays: number; // default 0, if it is due 2 days in the future it would be 2
}

export interface AddTaskRecurrenceResponse {
  NewTask: number; // The ID of the new task
  Status: Status;
}

export interface GetTaskRecurrenceResponse extends BaseResponse {
  TaskRecurrenceList?: TaskRecurrence[];
}

export interface UpdateTaskRequest {
  ID: number;
  Name?: string;
  Description?: string;
  AssignedUsers?: User[];
  AvailableDate?: string;
  AvailableTime?: string;
  DueDate?: string;
  DueTime?: string;
  ExpireDate?: string;
  ExpireTime?: string;
  CompletedBy?: number;
  CreatedBy?: number;
  DriveID?: number;
  CompletedOnTime?: boolean;
  Expired?: boolean;
}

export interface CreateTaskRequest {
  OrgID: number;
  LocationID: number;
  TaskType?: number; //1 for FormTemplate
  TaskTypeID?: number;
  Name?: string;
  Description?: string;
  DriveID?: number;
  AvailableDate?: string;
  AvailableTime?: string;
  DueDate?: string;
  DueTime?: string;
  ExpireDate?: string;
  ExpireTime?: string;
}

export interface UpdateSubTaskStatusRequest {
  TaskID?: number;
  TaskTemplateID?: number;
  SubTaskDesc: string;
  CreatedBy: number;
  DisplayOrder?: number;
}

export interface UpdateSubTaskStatusResponse extends BaseResponse {
  ID: number;
}

export interface CreateSubTaskResponse extends BaseResponse {
  NewTaskTemplateSubTask: number;
}

export interface CreateTaskResponse extends BaseResponse {
  NewTask: Task;
}

export interface UpdateSubTaskNameRequest {
  ID: number;
  SubTaskDesc: string;
}

export enum CopiesType {
  locations = 'Locations',
  users = 'Users',
}

export enum TaskType {
  recurring = 'Recurring',
}

export interface Task {
  id?: string;
  name: string;
  type: TaskType;
  organizations: Organization[];
  recurrenceSettings: TaskRecurrence;
  TaskTemplateID: number;
  DaysOfWeek: string;
  weekdays: string[];
  recurrenceTime: RecurrenceTime;
  // recurrenceTimes: RecurrenceTime[];
  copies: CopiesType;
}

export interface RecurrenceDay {
  displayName: string;
  day: string;
  value: number | string;
  selected: boolean;
}

export interface TaskRecurrence {
  RepeatValue: number;
  RepeatUnit: number;
  StartDate: string;
  EndDate: string;
}

export interface TimeSelect {
  hour: number;
  minute: number;
  second: number;
}

export interface RecurrenceRepeatUnit {
  name: string;
  value: number;
}

export interface RecurrenceTime {
  startTime: TimeSelect;
  AvailableDate?: string;
  AvailableTime?: string;
  DueDurationUnit: number;
  DueDurationValue: number;
  ExpireDurationUnit: number;
  ExpireDurationValue: number;
}

export interface TimeUnit {
  name: string;
  value: number;
}

export enum AmPmType {
  am = 'AM',
  pm = 'PM',
}

export enum WeekdayType {
  sunday = 'sunday',
  monday = 'monday',
  tuesday = 'tuesday',
  wednesday = 'wednesday',
  thursday = 'thursday',
  friday = 'friday',
  saturday = 'saturday',
}
