import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

export interface ChangePasswordData {
  currentPassword?: string;
  hideCurrentPassword?: boolean;
  newPassword: string;
  confirmPassword: string;
}

@Component({
  selector: 'xenon-change-password',
  styleUrls: ['./change-password.component.scss'],
  template: `
    <h6 mat-dialog-title>Change Password</h6>
    <div mat-dialog-content [hidden]="data?.hideCurrentPassword">
      <mat-form-field>
        <mat-label>Current Password</mat-label>
        <input matInput [(ngModel)]="data.currentPassword" (change)="onInputChange()" />
      </mat-form-field>
    </div>
    <div mat-dialog-content>
      <mat-form-field>
        <mat-label>New Password</mat-label>
        <input matInput [(ngModel)]="data.newPassword" (change)="onInputChange()" />
      </mat-form-field>
    </div>
    <div mat-dialog-content>
      <mat-form-field>
        <mat-label>Confirm Password</mat-label>
        <input matInput [(ngModel)]="data.confirmPassword" (change)="onInputChange()" />
      </mat-form-field>
    </div>
    <p *ngIf="errors.currentSameAsNew" class="error">Your new password cannot be the same as your previous password.</p>
    <p *ngIf="errors.confirmDoesntMatch" class="error">Your confirmed password must match your new password</p>
    <div mat-dialog-actions fxLayout="row" fxLayoutAlign="center center">
      <button mat-flat-button color="primary" [disabled]="updateDisabled" (click)="onUpdateClick()">Update</button>
    </div>
  `,
})
export class ChangePasswordComponent {
  public updateDisabled = true;
  public errors = { currentSameAsNew: false, confirmDoesntMatch: false };

  constructor(public dialogRef: MatDialogRef<ChangePasswordComponent>, @Inject(MAT_DIALOG_DATA) public data: ChangePasswordData) {}

  public onNoClick(): void {
    this.dialogRef.close();
  }

  public onUpdateClick() {
    this.dialogRef.close(this.data);
  }

  public onInputChange() {
    const currentSameAsNew = this.data.currentPassword === this.data.newPassword && this.data.currentPassword !== '';
    const confirmDoesntMatch = this.data.newPassword !== this.data.confirmPassword && this.data.confirmPassword !== '';
    const missingRequiredField = this.data.currentPassword === '' || this.data.newPassword === '' || this.data.confirmPassword === '';

    this.errors.currentSameAsNew = currentSameAsNew;
    this.errors.confirmDoesntMatch = confirmDoesntMatch;

    this.updateDisabled = currentSameAsNew || confirmDoesntMatch || missingRequiredField;
  }
}
