import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { MainLayoutComponent, SubLayoutComponent } from '@xenon/layouts';
import { PageLoginComponent, AuthGuard, PageNotFoundComponent, AppList, PasswordResetFromEmailComponent, PermissionsAuthGuard } from '@xenon/auth';

const routes = [
  {
    path: '',
    redirectTo: 'client',
    pathMatch: 'full',
  },
  {
    path: '',
    component: MainLayoutComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'client',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./features/client/client.module').then((m) => m.ClientModule),
      },
      {
        path: 'admin',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./features/admin/admin.module').then((m) => m.AdminModule),
      },
    ],
  },
  {
    component: SubLayoutComponent,
    canActivate: [AuthGuard],
    data: {
      app: AppList.FormBuilder,
    },
    path: 'admin/form-builder',
    children: [
      {
        path: 'layout',
        data: {
          app: AppList.FormBuilder,
        },
        loadChildren: () => import('./features/admin/form-builder/layout/layout.module').then((m) => m.LayoutModule),
      },
      {
        path: 'tasks',
        data: {
          app: AppList.FormBuilder,
        },
        loadChildren: () => import('./features/admin/form-builder/tasks/tasks.module').then((m) => m.TasksModule),
      },
      {
        path: 'settings',
        data: {
          app: AppList.FormBuilder,
        },
        loadChildren: () => import('./features/admin/form-builder/settings/settings.module').then((m) => m.SettingsModule),
      },
    ],
  },
  {
    /** id = some id, e = email, o = orgID, uu = UUID, ai = accept invitation*/
    path: 'passwordreset/:id/:e/:o/:uu/:ai',
    component: PasswordResetFromEmailComponent,
  },
  {
    path: 'login',
    component: PageLoginComponent,
  },
  {
    path: '**',
    component: PageNotFoundComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [],
})
export class AppRoutingModule {}
