import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface NewAccountData {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
}

@Component({
  selector: 'xenon-new-account',
  styleUrls: ['./new-account.component.scss'],
  template: `<mat-card>
    <div>
      <h6 class="text-center">Create New Account</h6>
      <div fxLayoutAlign="space-between">
        <mat-form-field>
          <mat-label>First Name</mat-label>
          <input matInput placeholder="First Name" [(ngModel)]="data.firstName" />
        </mat-form-field>
        <mat-form-field>
          <mat-label>Last Name</mat-label>
          <input matInput placeholder="Last Name" [(ngModel)]="data.lastName" />
        </mat-form-field>
      </div>
      <mat-form-field class="w-100">
        <mat-label>Email</mat-label>
        <input matInput type="email" placeholder="Email" [(ngModel)]="data.email" />
      </mat-form-field>
      <mat-form-field class="w-100">
        <mat-label>Password</mat-label>
        <input matInput type="password" placeholder="Password" [(ngModel)]="data.password" />
      </mat-form-field>
      <p class="w-100 text-center">Already have an account? <span class="color-primary clickable" (click)="close()">Log in</span></p>
      <button
        mat-flat-button
        color="primary"
        (click)="onSubmit()"
        class="w-100 py-2"
        [disabled]="data.firstName === '' || data.lastName === '' || data.email === '' || data.password === ''"
      >
        CREATE ACCOUNT
      </button>
    </div>
  </mat-card>`,
})
export class NewAccountComponent {
  constructor(public dialogRef: MatDialogRef<NewAccountComponent>, @Inject(MAT_DIALOG_DATA) public data: NewAccountData) {}

  public onSubmit(): void {
    this.dialogRef.close(this.data);
  }

  public close(): void {
    this.dialogRef.close();
  }
}
