import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-task-form-field-checkbox',
  styleUrls: ['./task-form-field-checkbox.component.scss'],
  template: `
    <form [formGroup]="form">
      <label>{{ formItem.Label }}</label>
      <section>
        <ul>
          <li *ngFor="let option of formItem.FormItemOptions">
            <mat-checkbox formControlName="{{ buildPropertyName(option.Label) }}">
              {{ option.Label }}
            </mat-checkbox>
          </li>
        </ul>
      </section>
    </form>
  `,
})
export class TaskFormFieldCheckboxComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    this.form = new FormGroup({});
    this.formItem.FormItemOptions.forEach((option) => {
      const prop = this.buildPropertyName(option.Label);
      const initValue = this.formItem.Response.indexOf(option.Label) > -1;
      this.form.addControl(prop, new FormControl(initValue, Validators.required));
    });

    this.form.valueChanges.subscribe((v) => {
      const newValue = [];
      this.formItem.FormItemOptions.forEach((i) => {
        const prop = this.buildPropertyName(i.Label);
        if (v[prop] === true) {
          newValue.push(i.Label);
        }
      });
      this.submitPatch(newValue.join(','), this.formItem).subscribe((response: { Message: string; formItem: FormItem }) => {
        this.formItem = response.formItem;
        this.cd.detectChanges();
      });
    });
  }

  buildPropertyName(label: string): string {
    return label.toLowerCase().split(' ').join('_');
  }
}
