import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { UnsavedChangesModalComponent } from '../components/unsaved-changes-modal/unsaved-changes-modal.component';

@Injectable()
export class UnsavedChangesModalService {
  constructor(public dialog: MatDialog, private router: Router) {}

  private userClickedCancel = false;

  public handleUnsavedChangesModal(checkForUnsavedChanges: () => boolean, save: () => Promise<void>, nextState: string): boolean {
    const userCanLeavePage = true;
    const unsavedChanges: boolean = checkForUnsavedChanges();

    if (unsavedChanges && !this.userClickedCancel) {
      const dialogRef = this.dialog.open(UnsavedChangesModalComponent, { width: '300px' });

      dialogRef.afterClosed().subscribe((saveBeforeLeaving) => {
        if (saveBeforeLeaving) {
          save()
            .then(() => {
              this.router.navigate([nextState]);
              return userCanLeavePage;
            })
            .catch(() => {
              return !userCanLeavePage;
            });
        } else {
          this.userClickedCancel = true;
          this.router.navigate([nextState]);
          return userCanLeavePage;
        }
      });
    } else {
      this.userClickedCancel = false;
      return userCanLeavePage;
    }
  }
}
