import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { BaseApi } from '../environments/base-api';
import { BaseResponse } from '../models/base-response';
import {
  AddRoleToUserRequest,
  AddRoleToUserResponse,
  CreateRoleRequest,
  CreateRoleResponse,
  GetRolesByLocationResponse,
  GetRolesByOrgResponse,
  UpdateRoleRequest,
} from '@xenon/api';
import {
  GetRolePermissionsResponse,
  GetAllRolePermissionsResponse,
  FormTemplatePermissionResponse,
  TaskTemplatePermissionResponse,
  TaskTemplatePermission,
  FormTemplatePermission,
} from '../models/role';

@Injectable({
  providedIn: 'root',
})
export class ApiRoleService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  /**
   * Used to get roles by an organization ID.
   * @param orgId Get all roles by this organization's ID.
   * @returns An observable with the GetRolesByOrgResponse.
   */
  public getRolesByOrg(orgId: number): Observable<GetRolesByOrgResponse> {
    const url = `${this.baseUrl}/rolebyorg/${orgId}`;
    return this.http.get<GetRolesByOrgResponse>(url);
  }

  /**
   * Used to get roles by a location ID.
   * @param locationId Get all roles by this location's ID.
   * @returns An observable with the GetRolesByLocationResponse.
   */
  public getRolesByLocation(locationId: number): Observable<GetRolesByLocationResponse> {
    const url = `${this.baseUrl}/rolebylocation/${locationId}`;
    return this.http.get<GetRolesByLocationResponse>(url);
  }

  /**
   * Used to create a new role.
   * @param roleInfo The information used to create a new role.
   * @returns An observable with the CreateRoleResponse.
   */
  public createRole(roleInfo: CreateRoleRequest): Observable<CreateRoleResponse> {
    const url = `${this.baseUrl}/role`;
    return this.http.post<CreateRoleResponse>(url, roleInfo);
  }

  /**
   * Used to update an existing role.
   * @param role Information on the role to update.
   * @returns An observable with the BaseResponse.
   */
  public updateRole(role: UpdateRoleRequest): Observable<BaseResponse> {
    const roleID = role.ID;
    const url = `${this.baseUrl}/role/${roleID}`;
    return this.http.patch<BaseResponse>(url, role);
  }

  /**
   * Used to delete an existing role.
   * @param roleID The ID of the role to delete.
   * @returns An observable with the BaseResponse.
   */
  public deleteRole(roleID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/role/${roleID}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * Used to add a role to a user.
   * @param info Information on the role and the user to add it to.
   * @returns An observable with the AddRoleToUserResponse
   */
  public addRoleToUser(info: AddRoleToUserRequest): Observable<AddRoleToUserResponse> {
    const url = `${this.baseUrl}/userrole`;
    return this.http.post<AddRoleToUserResponse>(url, info);
  }

  /**
   * Used to remove an assigned role from a user.
   * @param roleID The ID of the role to remove.
   * @returns An observable with the BaseResponse.
   */
  public removeRoleFromUser(roleID: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/userrole/${roleID}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * Used to get permissions by a role ID.
   * @param roleID Get all permissions by this role's ID.
   * @returns An observable with the GetRolePermissionsResponse.
   */
  public getPermissionsByRole(roleID: number): Observable<GetRolePermissionsResponse> {
    const url = `${this.baseUrl}/permissions/get/byrole`;
    return this.http.post<GetRolePermissionsResponse>(url, { RoleID: roleID });
  }

  /**
   * Used to get all permissions.
   * @returns An observable with the GetAllRolePermissionsResponse.
   */
  public getAllPermissions(): Observable<GetAllRolePermissionsResponse> {
    const url = `${this.baseUrl}/permissions/get/all`;
    return this.http.get<GetAllRolePermissionsResponse>(url);
  }

  /**
   * Used to set permissions by a role ID.
   * @param roleID Set all permissions by this role's ID.
   * @param permissionIDs List of permissionIds to set.
   * @returns An observable with the BaseResponse.
   */
  public setPermissionsByRole(roleID: number, permissionIDs: number[]): Observable<BaseResponse> {
    const url = `${this.baseUrl}/role/permissions/set`;
    return this.http.post<BaseResponse>(url, { RoleID: roleID, PermissionIDs: permissionIDs });
  }

  /**
   * Used to get form template permissions by a role ID.
   * @param roleID Get permissions by this role's ID.
   * @returns An observable with the FormTemplatePermissionResponse.
   */
  public getFormTemplateRolePermissionsByRole(roleID: number): Observable<FormTemplatePermissionResponse> {
    const url = `${this.baseUrl}/form/template/role/permissions/get/byrole/${roleID}`;
    return this.http.get<FormTemplatePermissionResponse>(url);
  }

  /**
   * Used to get form template permissions by a role ID.
   * @param roleID Get permissions by this role's ID.
   * @returns An observable with the FormTemplatePermissionResponse.
   */
  public getFormTemplateRolePermissionsDefaultByRole(roleID: number): Observable<FormTemplatePermissionResponse> {
    const url = `${this.baseUrl}/form/template/role/permissions/get/default/byrole/${roleID}`;
    return this.http.get<FormTemplatePermissionResponse>(url);
  }

  /**
   * Used to get task template permissions by a role ID.
   * @param roleID Get permissions by this role's ID.
   * @returns An observable with the TaskTemplatePermissionResponse.
   */
  public getTaskTemplateRolePermissionsByRole(roleID: number): Observable<TaskTemplatePermissionResponse> {
    const url = `${this.baseUrl}/task/template/role/permissions/get/byrole/${roleID}`;
    return this.http.get<TaskTemplatePermissionResponse>(url);
  }

  /**
   * Used to get task template permissions by a role ID.
   * @param roleID Get permissions by this role's ID.
   * @returns An observable with the TaskTemplatePermissionResponse.
   */
  public getTaskTemplateRolePermissionsDefaultByRole(roleID: number): Observable<TaskTemplatePermissionResponse> {
    const url = `${this.baseUrl}/task/template/role/permissions/get/default/byrole/${roleID}`;
    return this.http.get<TaskTemplatePermissionResponse>(url);
  }

  /**
   * Used to set task template permissions.
   * @param templateRoles List of template role permissions to set.
   * @returns An observable with the BaseResponse.
   */
  public setTaskTemplateRolePermissionsByRole(templateRoles: TaskTemplatePermission[]): Observable<BaseResponse> {
    const url = `${this.baseUrl}/task/template/role/permissions/set`;
    return this.http.post<BaseResponse>(url, { TaskTemplateRoles: templateRoles });
  }

  /**
   * Used to set default task template permissions.
   * @param templateRoles List of template role permissions to set.
   * @returns An observable with the BaseResponse.
   */
  public setDefaultTaskTemplateRolePermissionsByRole(templateRoles: TaskTemplatePermission): Observable<BaseResponse> {
    const url = `${this.baseUrl}/task/template/role/permissions/default/set`;
    return this.http.post<BaseResponse>(url, templateRoles);
  }

  /**
   * Used to set form template permissions.
   * @param templateRoles List of template role permissions to set.
   * @returns An observable with the BaseResponse.
   */
  public setFormTemplateRolePermissionsByRole(templateRoles: FormTemplatePermission[]): Observable<BaseResponse> {
    const url = `${this.baseUrl}/form/template/role/permissions/set`;
    return this.http.post<BaseResponse>(url, { FormTemplateRoles: templateRoles });
  }

  /**
   * Used to set default form template permissions.
   * @param templateRoles List of template role permissions to set.
   * @returns An observable with the BaseResponse.
   */
  public setDefaultFormTemplateRolePermissionsByRole(templateRoles: FormTemplatePermission): Observable<BaseResponse> {
    const url = `${this.baseUrl}/form/template/role/permissions/default/set`;
    return this.http.post<BaseResponse>(url, templateRoles);
  }
}
