import { Component, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'xenon-tasks-search',
  template: ` <xenon-ui-string-search [label]="'Search'" (searchChange)="changeSearch($event)"></xenon-ui-string-search> `,
})
export class TasksSearchComponent {
  @Output() searchChange = new EventEmitter<string>();

  searchString: string;

  constructor() {}

  changeSearch($event): void {
    this.searchChange.emit($event);
  }
}
