import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'timeUnits',
})
export class TimeUnitsPipe implements PipeTransform {
  transform(value: unknown, ...args: unknown[]): unknown {
    let timeUnitValue = '';
    switch (value) {
      case 1:
        timeUnitValue = 'Minute(s)';
        break;

      case 2:
        timeUnitValue = 'Hour(s)';
        break;

      case 3:
        timeUnitValue = 'Day(s)';
        break;

      case 4:
        timeUnitValue = 'Week(s)';
        break;

      case 5:
        timeUnitValue = 'Months(s)';
        break;

      case 5:
        timeUnitValue = 'Year(s)';
        break;

      default:
        break;
    }
    return timeUnitValue;
  }
}
