import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { UiListSelectComponent, UiListSelectOptions } from './ui-list-select.component';

@Injectable()
export class UiListSelectDialogService<T> {
  constructor(private dialog: MatDialog) {}
  dialogRef: MatDialogRef<UiListSelectComponent<T>>;

  public open(options: UiListSelectOptions<T>) {
    this.dialogRef = this.dialog.open(UiListSelectComponent, {
      width: '711px',
      maxHeight: '610px',
      minHeight: '440px',
      panelClass: 'custom-dialog-container',
      data: {
        inputList: options.inputList,
        currentSelectedList: options.currentSelectedList,
        primaryValueSelector1: options.primaryValueSelector1,
        primaryValueSelector2: options.primaryValueSelector2,
        secondaryValueSelector: options.secondaryValueSelector,
        compareValueBy: options.compareValueBy,
        displayUserImage: options.displayUserImage,
        iconClass: options.iconClass,
      },
    }) as MatDialogRef<UiListSelectComponent<T>>;
  }

  public closed(): Observable<T[]> {
    return this.dialogRef.afterClosed().pipe(
      take(1),
      map((res) => {
        return res;
      })
    );
  }
}
