import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { UiFormFieldInput } from '../../models/ui-form-field-input';

@Component({
  selector: 'xenon-ui-form-field-input',
  styleUrls: ['./ui-form-field-input.component.scss'],
  template: `
    <mat-form-field [formGroup]="fieldFormGroup">
      <mat-label>{{ fieldOptions.label }}</mat-label>
      <input
        type="{{ inputType }}"
        matInput
        formControlName="{{ fieldOptions.name }}"
      />
    </mat-form-field>
  `,
})
export class UiFormFieldInputComponent {
  @Input() fieldFormGroup: FormGroup;
  @Input() fieldOptions: UiFormFieldInput;
  @Input() inputType = 'text';
}
