import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { ApiTaskService, BaseResponse, SubTask, UpdateSubTaskNameRequest, UpdateSubTaskStatusRequest, UpdateSubTaskStatusResponse } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { UiNotificationService, NotificationType } from '@xenon/ui';

@Component({
  selector: 'xenon-checklist-panel-subtask-list',
  styleUrls: ['./checklist-panel-subtask-list.component.scss'],
  template: `<div *ngFor="let subtask of subtaskList" fxLayout="row" fxLayoutAlign="start center">
      <mat-checkbox [checked]="subtask.CompletedAt" (change)="updateSubTaskStatus(subtask.ID, currentUserID, $event.checked)" class="mr-2 ml-3"></mat-checkbox>
      <mat-form-field class="mat-form-field-alternate w-100">
        <input class="ml-2" matInput [(ngModel)]="subtask.SubTaskDesc" (keyup.enter)="updateSubtaskName(subtask)" (blur)="updateSubtaskName(subtask)" />
      </mat-form-field>
      <button mat-icon-button [matMenuTriggerFor]="menu" aria-label="More options menu" class="ml-auto">
        <mat-icon>more_vert</mat-icon>
      </button>
      <mat-menu #menu="matMenu">
        <button mat-menu-item (click)="deleteSubTask(subtask)">
          <mat-icon>delete</mat-icon>
          <span>Delete</span>
        </button>
      </mat-menu>

      <mat-divider></mat-divider>
    </div>
    <div fxLayout="column">
      <div fxLayoutAlign="space-between center" *ngIf="showAddNewSubTask">
        <mat-checkbox class="mr-3 ml-3"></mat-checkbox>
        <mat-form-field class="w-100" floatLabel="never">
          <input matInput #newSubTaskEl (keyup.enter)="addSubTask(newSubTaskEl, true)" (blur)="addSubTask(newSubTaskEl, false)" placeholder="Enter Subtask" />
        </mat-form-field>
        <mat-icon class="mx-3 clickable">done</mat-icon>
      </div>

      <button mat-button color="primary" class="p-0 text-left mx-3 mt-12px" (click)="clickedShowAddNewSubTask()">
        <mat-icon class="mr-1">add_box</mat-icon>
        Add Subtask
      </button>
    </div>`,
})
export class ChecklistPanelSubtaskListComponent {
  @Input() subtaskList: SubTask[] = [];
  @Input() taskID: number;
  @Output() refresh = new EventEmitter();
  @Output() newSubTaskList = new EventEmitter<SubTask[]>();
  @ViewChild('newSubTaskEl') newSubTaskEl: HTMLInputElement;
  @ViewChild('newSubTaskEl') newSubTaskElRef: ElementRef;
  public showAddNewSubTask = false;
  public currentUserID = this.localStorage.getItem('user').CurrentUser.ID;

  constructor(private taskService: ApiTaskService, private notificationService: UiNotificationService, private localStorage: UtilLocalStorageService) {}
  /**
   * Updates a subtask to either 'complete' or 'incomplete'.
   */
  public updateSubTaskStatus(subTaskID: number, checked: boolean) {
    this.taskService
      .updateSubTaskStatus(subTaskID, this.currentUserID, checked)
      .then((_) => {
        this.refresh.emit();
      })
      .catch((_) => {
        this.showChecklistError(`An error occurred updating the subtask status.`);
      });
  }

  public updateSubtaskName(subtask: SubTask) {
    const request: UpdateSubTaskNameRequest = {
      ID: subtask.ID,
      SubTaskDesc: subtask.SubTaskDesc,
    };

    this.taskService
      .updateSubTaskName(request)
      .then((_) => {
        this.notificationService.show(NotificationType.success, 'Close', 'Updated Subtask', 5000);
      })
      .catch((_) => {
        this.showChecklistError(`An error occurred updating sub task name.`);
      });
  }

  emitList(): void {
    this.newSubTaskList.emit(this.subtaskList);
  }

  public deleteSubTask(subtask: SubTask) {
    this.taskService
      .deleteSubTask(subtask.ID)
      .then((response: BaseResponse) => {
        const indexToRemove = this.subtaskList.findIndex((i) => i.ID === subtask.ID);
        this.subtaskList.splice(indexToRemove, 1);
        this.emitList();
      })
      .catch((err) => {
        this.showChecklistError(`An error occurred deleting sub task.`);
      });
  }

  public addSubTask(newSubTaskEl, showAddNewSubTask: boolean) {
    if (!newSubTaskEl.value) {
      this.showAddNewSubTask = false;
      return;
    }

    const subTask: UpdateSubTaskStatusRequest = {
      TaskID: this.taskID,
      SubTaskDesc: newSubTaskEl.value,
      CreatedBy: this.currentUserID,
    };

    this.taskService
      .addSubTask(subTask)
      .then((response: UpdateSubTaskStatusResponse) => {
        newSubTaskEl.value = ''; // reset input
        this.showAddNewSubTask = showAddNewSubTask; // hide 'add new' input
        const newSubTaskObject = {
          ID: response.ID,
          TaskID: subTask.TaskID,
          SubTaskDesc: subTask.SubTaskDesc,
          CompletedAt: null,
          CreatedBy: this.currentUserID,
        };
        this.subtaskList ? this.subtaskList.push(newSubTaskObject) : (this.subtaskList = [newSubTaskObject]);
        this.emitList();
      })
      .catch((err) => {
        this.showChecklistError(`An error occurred adding task.`);
      });
  }

  public clickedShowAddNewSubTask(): void {
    this.showAddNewSubTask = true;
    setTimeout(() => this.newSubTaskElRef.nativeElement.focus(), 200);
  }

  private showChecklistError(message: string): void {
    this.notificationService.show(NotificationType.error, 'Error', message, 5000);
  }
}
