import { Component, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, Subject, throwError } from 'rxjs';
import { catchError, map, mergeMap, take, takeUntil } from 'rxjs/operators';
import { MatDialog } from '@angular/material/dialog';

import { ApiAuthenticationService, ApiLocationService, ApiRoleService, ApiUserService, GetRolesByOrgResponse, User, LoggedInUser } from '@xenon/api';
import { NotificationType, UiNotificationService, UIUserFormComponent } from '@xenon/ui';
import { UtilLocalStorageService, UtilWindowRefService } from '@xenon/utils';
import { AppNavigationService, AppList, PermissionsHelperService } from '@xenon/auth';
import { animate, state, style, transition, trigger } from '@angular/animations';
import * as moment from 'moment';

@Component({
  selector: 'xenon-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
  animations: [
    trigger('slideInOut', [
      state(
        'opened',
        style({
          transform: 'translate3d(0, 0, 0)',
        })
      ),
      state(
        'closed',
        style({
          transform: 'translate3d(-100%,0,0)',
        })
      ),
      transition('opened => closed', animate('300ms ease-in-out')),
      transition('closed => opened', animate('300ms ease-in-out')),
    ]),
  ],
})
export class HeaderComponent implements OnInit, OnDestroy {
  hideArrow = false;
  itemSettingsDisplayState: BehaviorSubject<boolean> = new BehaviorSubject(false);
  loggedInUser: LoggedInUser;
  public users$: BehaviorSubject<User[]> = new BehaviorSubject<User[]>([]);
  public showPhoto: boolean;
  unsubscribe = new Subject<{}>();
  userOrganizationName = '';
  userLocationName = '';
  orgLocations = [];
  public hasPermission: BehaviorSubject<boolean> = new BehaviorSubject(false);

  public isXClient = () => {
    return this.appNavigationService.isClient();
  };

  public isXAdmin = () => {
    return this.appNavigationService.isAdmin();
  };

  constructor(
    private authenticationService: ApiAuthenticationService,
    private notificationService: UiNotificationService,
    private utilLocalStorageService: UtilLocalStorageService,
    private utilWindowRefService: UtilWindowRefService,
    private appNavigationService: AppNavigationService,
    private apiRoleService: ApiRoleService,
    private dialog: MatDialog,
    private apiUserService: ApiUserService,
    private apiLocationService: ApiLocationService,
    private permissionsHelperService: PermissionsHelperService
  ) {
    this.hasPermission.next(
      this.permissionsHelperService.hasAnyPermissionInArray(['manageOrganization', 'manageUsers', 'manageFormTemplates', 'manageTaskTemplates'])
    );
    this.loggedInUser = this.utilLocalStorageService.getItem('user');
    this.apiLocationService
      .getLocationsByOrgId(this.loggedInUser.CurrentOrg.ID)
      .pipe(take(1))
      .subscribe((response: any) => {
        this.orgLocations = response;
        this.userLocationName = response.find((orgLocation) => orgLocation.ID === this.loggedInUser.CurrentLocation.ID).Name;
      });
  }

  ngOnInit(): void {
    this.showPhoto = this.loggedInUser.CurrentUser?.PhotoURL?.trim()?.length > 0;
    if (this.loggedInUser.CurrentUser.Orgs) {
      const primaryOrg = this.loggedInUser.CurrentUser.Orgs.find((org) => org.OrgID === this.loggedInUser.CurrentOrg.ID);
      this.userOrganizationName = primaryOrg?.Org?.Name;

      this.apiLocationService
        .getLocation(this.loggedInUser.CurrentLocation.ID)
        .pipe(take(1))
        .subscribe((locationObj) => {
          if (locationObj && locationObj.Timezone) moment.tz.setDefault(locationObj.Timezone);
        });
    }
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  logout(): void {
    this.authenticationService.logout().subscribe((response) => {
      const origin = this.utilWindowRefService.nativeWindow.location.origin;
      this.utilWindowRefService.nativeWindow.location.replace(`${origin}/#/login`);
    });
  }

  public hidePhoto() {
    this.showPhoto = false;
  }

  public navigateToAdmin(): void {
    this.appNavigationService.navigateToApp(AppList.Admin);
  }

  public navigateToClient(): void {
    this.appNavigationService.navigateToApp(AppList.Client);
  }

  public toggleSwitchOrg(newVal: boolean): void {
    this.itemSettingsDisplayState.next(newVal);
  }

  public openUserForm(): void {
    this.loggedInUser = this.utilLocalStorageService.getItem('user');

    if (!this.loggedInUser) {
      this.notificationService.show(NotificationType.error, 'Error', 'Can not get current user data', 2500);
      return;
    }
    this.apiUserService
      .getUsersByOrganization(this.loggedInUser.CurrentOrg.ID)
      .pipe(
        takeUntil(this.unsubscribe),
        map((response: User[]) => {
          const foundUser = response.find((u) => u.ID === this.loggedInUser.CurrentUser.ID);
          this.loggedInUser.CurrentUser.RoleLocations = foundUser?.RoleLocations;
          return foundUser;
        }),
        mergeMap((u) => this.apiRoleService.getRolesByOrg(this.loggedInUser.CurrentOrg.ID)),
        catchError((err) => {
          return throwError(err);
        })
      )
      .subscribe((response: GetRolesByOrgResponse) => {
        const formData = {
          user: this.loggedInUser.CurrentUser,
          refresh: this.refreshData.bind(this),
          roles: response.OrgRoleList,
          locations: this.orgLocations,
        };
        this.dialog.open(UIUserFormComponent, {
          data: formData,
          panelClass: 'full-page-dialog',
        });
      });
  }

  public logoLink(): string {
    const url = this.loggedInUser.CurrentUser.Orgs.find((org) => org.OrgID === this.loggedInUser.CurrentOrg.ID).Org.LogoURL;
    return url.replace(/\s/g, '').length > 0 ? url : 'assets/logo-transparent.png';
  }

  private refreshData(): void {
    this.apiUserService.getUsersByOrganization(this.loggedInUser.CurrentOrg.ID).subscribe((response: User[]) => {
      this.apiUserService.setCachedUsers(response);
    });
  }

  getOrganizationName(): string {
    if (this.loggedInUser.CurrentUser?.Orgs)
      return this.loggedInUser.CurrentUser.Orgs.filter((org) => org.OrgID === this.loggedInUser.CurrentOrg.ID)[0].Org.Name;
    else return null;
  }
}
