import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { ApiAuthenticationService, ApiOrganizationService, BaseResponse, User, ApiLocationService, LoggedInUser } from '@xenon/api';
import { MatDialog } from '@angular/material/dialog';

import { UtilLocalStorageService } from '@xenon/utils';
import { NotificationType, UiNotificationService, UiConfirmationComponent } from '@xenon/ui';
import { AppNavigationService } from '../../services/app-navigation.service';
import { AppList } from '../../enums/app-list';
import { CheckYourEmailComponent } from '../check-your-email-dialog/check-your-email.component';
import { ResetPasswordComponent, ResetPasswordData } from '../reset-password/reset-password.component';
import { NewAccountComponent } from '../new-account/new-account.component';
import { take } from 'rxjs/operators';
import { TermsAndServicesComponent } from '../terms-and-services/terms-and-services.component';
import { PickOrgLocationComponent } from '../pick-org-location/pick-org-location.component';

@Component({
  selector: 'xenon-page-login',
  styleUrls: ['./page-login.component.scss'],
  templateUrl: './page-login.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class PageLoginComponent implements OnInit, OnDestroy {
  public form: FormGroup = Object.create(null);
  private subs: Subscription[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private authenticationService: ApiAuthenticationService,
    private localStorage: UtilLocalStorageService,
    private notificationService: UiNotificationService,
    private appNavigationService: AppNavigationService,
    public dialog: MatDialog,
    public apiOrganizationService: ApiOrganizationService,
    public apiLocationService: ApiLocationService
  ) {}

  ngOnDestroy(): void {
    this.subs.forEach((s) => s.unsubscribe());
  }

  ngOnInit() {
    this.form = this.formBuilder.group({
      email: ['', Validators.compose([Validators.required])],
      password: ['', Validators.compose([Validators.required])],
    });
  }
  onSubmit(id = 0) {
    const login = this.authenticationService
      .login(this.form.controls.email.value, this.form.controls.password.value, id)
      .subscribe((loggedInUser: LoggedInUser) => {
        if (loggedInUser) {
          let incompleteUser = false;
          incompleteUser = this.checkTermsAccepted(loggedInUser);
          if (incompleteUser) return;
          incompleteUser = this.checkPrimaryOrgLocation(loggedInUser);
          if (incompleteUser) return;
          this.setAndNavigate(loggedInUser);
        } else {
          this.notificationService.show(NotificationType.error, 'Login Failed', 'Invalid credentials, please try again', 2500);
        }
      });
    this.subs.push(login);
  }

  public openResetPassword(): void {
    const dialogRef = this.dialog.open(ResetPasswordComponent, {
      panelClass: 'full-page-dialog',
      data: {
        email: '',
      },
    });

    dialogRef.afterClosed().subscribe((data: ResetPasswordData) => {
      if (data) {
        this.authenticationService
          .resetPassword(data.email)
          .then((response: BaseResponse) => {
            console.log(response);
            if (response.Status === 'success') {
              this.dialog.open(CheckYourEmailComponent);
            } else this.handleResetPasswordError();
          })
          .catch((err) => {
            console.log(err);
            this.handleResetPasswordError();
          });
      }
    });
  }

  public openNewAccount(): void {
    const dialogRef = this.dialog.open(NewAccountComponent, {
      panelClass: 'full-page-dialog',
      data: {
        firstName: '',
        lastName: '',
        email: '',
        password: '',
      },
    });

    dialogRef.afterClosed().subscribe((data) => {
      if (data) {
        this.apiOrganizationService.scaffoldOrg(data).subscribe((result) => {
          if (result.Status === 'error') {
            this.dialog.open(UiConfirmationComponent, {
              data: {
                title: 'Error',
                description: result.Message,
                acceptMessage: 'Close',
                showDecline: false,
              },
            });
          }
          if (result.Status === 'success') {
            this.notificationService.show(NotificationType.error, 'OK', 'User Created', 5000);
            this.form.controls.email.setValue(data.email);
            this.form.controls.password.setValue(data.password);
            this.onSubmit();
          }
        });
      }
    });
  }

  private setAndNavigate(loggedInUser: LoggedInUser) {
    this.localStorage.setItem('user', loggedInUser);
    this.appNavigationService.navigateToApp(AppList.Client);
  }

  private handleResetPasswordError() {
    this.notificationService.show(NotificationType.error, 'Error', 'Invalid email, please try again', 5000);
  }

  private checkTermsAccepted(loggedInUser: LoggedInUser): boolean {
    if (loggedInUser.CurrentUser.TermsAccepted === 0) {
      const dialogRef = this.dialog.open(TermsAndServicesComponent, {
        panelClass: 'full-page-dialog',
      });

      dialogRef
        .afterClosed()
        .pipe(take(1))
        .subscribe((accepted) => {
          if (accepted) {
            this.authenticationService.acceptUserTerms(loggedInUser.CurrentUser.ID).then((_) => {});
            this.checkPrimaryOrgLocation(loggedInUser);
            this.setAndNavigate(loggedInUser);
          }
        });
      return true;
    }
  }

  private checkPrimaryOrgLocation(loginInfo: LoggedInUser): boolean {
    if (loginInfo.Message && loginInfo.Message.includes('no location')) {
      const dialogRef = this.dialog.open(PickOrgLocationComponent, {
        panelClass: 'full-page-dialog',
        data: loginInfo.Locations,
      });

      dialogRef
        .afterClosed()
        .pipe(take(1))
        .subscribe((newLocationID) => {
          if (newLocationID) {
            this.onSubmit(newLocationID);
          }
        });
      return true;
    }
  }
}
