import { Component, OnDestroy, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, combineLatest, forkJoin, Observable, of } from 'rxjs';
import { AppBase } from '../../../app-base';
import { switchMap } from 'rxjs/operators';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import * as _ from 'lodash-es';
import { UtilCloneService, UtilLocalStorageService, UtilMomentService } from '@xenon/utils';
import { TasksByDate, Task, ApiTaskService, ApiFormService, CreateTaskResponse } from '@xenon/api';
import { UiScrollService } from '@xenon/ui';
import { TaskFilterItem } from '../../../shared/components/tasks-filter/tasks-filter.component';
import { TaskFormComponent } from '../../../shared/modules/task-form/task-form.component';
import * as moment from 'moment';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'xenon-tasks',
  styleUrls: ['./tasks.component.scss'],
  templateUrl: './tasks.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class TasksComponent extends AppBase implements OnDestroy {
  filterItems = [
    {
      active: true,
      label: 'All Tasks',
      value: 'all-tasks',
    },
    {
      active: true,
      label: 'Scheduled',
      value: 'scheduled',
    },
    {
      active: true,
      label: 'Open',
      value: 'open',
    },
    {
      active: true,
      label: 'Overdue',
      value: 'overdue',
    },
    {
      active: true,
      label: 'Complete',
      value: 'complete',
    },
    {
      active: true,
      label: 'Expired',
      value: 'expired',
    },
    {
      active: false,
      label: 'Unassigned Tasks',
      value: 'unassigned-tasks',
    },
    {
      active: false,
      label: 'My Tasks',
      value: 'my-tasks',
    },
    {
      active: false,
      label: 'Team Assignments',
      value: 'team-assignments',
    },
    {
      active: false,
      label: 'Role Assignments',
      value: 'role-assignments',
    },
  ];

  public date$: BehaviorSubject<NgbDateStruct> = new BehaviorSubject(null);
  public filterItems$: BehaviorSubject<TaskFilterItem[]> = new BehaviorSubject(this.filterItems);
  public search$: BehaviorSubject<string> = new BehaviorSubject('');
  public tasksByDate$: BehaviorSubject<TasksByDate[]> = new BehaviorSubject<TasksByDate[]>([]);
  public infoForChecklist$: BehaviorSubject<Task> = new BehaviorSubject<Task>(null);
  public data$: Observable<TasksByDate[]> = combineLatest([this.tasksByDate$, this.date$, this.filterItems$, this.search$]).pipe(
    switchMap(([tbd, date, filter, search]) => {
      // build tasks by date array
      const data = tbd ? tbd : this.localStorage.getItem('tasks-data');
      let newTbd: TasksByDate[] = this.buildTasksByDateArray(data);
      // filter by search
      if (search && search.length) {
        newTbd = this.filterBySearch(newTbd, search);
      }

      // filter by filterItems
      if (filter) {
        newTbd = this.filterByFilters(newTbd, filter);
      }

      // filter by date
      if (date) {
        newTbd = this.filterByDate(newTbd, date);
      }

      // if currently selected not in newTbd re-add it
      if (this.setSelectedTaskID.value > 0 && newTbd.every((newTbdItem) => newTbdItem.tasks.every((task) => task.ID !== this.setSelectedTaskID.value))) {
        let selectedTask;
        this.tasksByDate$.value.forEach((taskByDate) => {
          if (taskByDate.dateIsToday) {
            selectedTask = taskByDate.tasks.find((task) => task.ID === this.setSelectedTaskID.value);
          }
        });
        newTbd.map((tbdItem) => (tbdItem.dateIsToday ? tbdItem.tasks.push(selectedTask) : newTbd));
        this.taskHiddenSnackbar = true;
      }
      return of(newTbd);
    })
  );

  public setSelectedTaskID: BehaviorSubject<number> = new BehaviorSubject(null);
  public closeChecklistPanel: BehaviorSubject<boolean> = new BehaviorSubject(null);
  public taskHiddenSnackbar = false;

  private today = moment().toDate();

  constructor(
    private localStorage: UtilLocalStorageService,
    private taskService: ApiTaskService,
    private formService: ApiFormService,
    private bottomSheet: MatBottomSheet,
    private snackBar: MatSnackBar
  ) {
    super(localStorage);

    this.taskService.stringDateRange$.next(this.getTaskDateRange());
    this.taskService.organizationId$.next(this.organizationId);
    this.taskService.locationId$.next(this.locationId);
    this.refreshData();
    this.reset();
    const localFilters = this.localStorage.getItem('tasksFilterItems');
    if (localFilters) {
      this.filterChange(localFilters);
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub) => sub.unsubscribe());
  }

  public reset(date?: Date): void {
    if (!date) date = this.today;

    this.date$.next({
      year: date.getFullYear(),
      month: date.getMonth() + 1,
      day: date.getDate(),
    });
    this.filterItems.forEach((i) => (i.active = i.value === 'all-tasks'));
    this.filterItems$.next(this.filterItems);
    this.search$.next('');
    this.scrollToDate(false, date);
  }

  public dateChange(event: NgbDateStruct): void {
    const date = {
      day: event.day < 10 ? `0${event.day}` : event.day,
      month: event.month < 10 ? `0${event.month}` : event.month,
      year: event.year,
    } as NgbDateStruct;
    const targetDate = UtilMomentService.new(`${date.year}-${date.month}-${date.day}`, 'YYYY-MM-DD').format('YYYY-MM-DD');
    setTimeout(() => UiScrollService.scrollTo(targetDate), 0);
    this.date$.next(date);

    const myDate = new Date(event.year, event.month - 1, event.day);
    const updatedDateRange = this.getTaskDateRange(myDate);
    this.taskService.stringDateRange$.next(updatedDateRange);
    this.reset(myDate);
  }

  filterChange(event: TaskFilterItem[]): void {
    this.filterItems$.next(event);
  }

  searchChange(event: string): void {
    this.search$.next(event);
  }

  public itemClicked(event: Task | { TaskType: number; TaskTypeID: number }): void {
    if (event.TaskType === 1) {
      this.closeChecklistPanel.next(!this.closeChecklistPanel.value);
      const bottomSheet = forkJoin([this.formService.get(event.TaskTypeID), this.formService.getTypes()]).subscribe(([form, formItemTypes]) => {
        const bs = this.bottomSheet
          .open(TaskFormComponent, {
            data: {
              form: form,
              formItemTypes: formItemTypes,
            },
            backdropClass: 'task-form-backdrop',
            panelClass: 'task-form-panel',
          })
          .afterDismissed()
          .subscribe(() => {
            this.deselectTask();
            this.refreshData();
          });
        this.subscriptions.push(bs);
      });
      this.subscriptions.push(bottomSheet);
    } else {
      // cloning so ngOnChanges is triggered when the user opens the same task twice.
      const clone = JSON.parse(JSON.stringify(event));
      this.infoForChecklist$.next(clone);
    }
  }

  public createTask(): void {
    const data = this.localStorage.getItem('user');
    const availTime = moment().hours() + ':00';
    const dueTime = moment().hours() + 5 + ':00';
    const expireTime = moment().hours() + 10 + ':00';
    const newTask = {
      CreatedBy: this.loggedInUser.CurrentUser.ID,
      OrgID: data.CurrentOrg.ID,
      LocationID: data.CurrentLocation.ID,
      TaskType: 2,
      TaskTypeID: 0,
      DriveID: 0,
      Name: 'Untitled Task',
      Description: 'Description',
      AvailableDate: moment.utc(moment().toDate()).format(moment.HTML5_FMT.DATE),
      AvailableTime: availTime.length === 4 ? `0${availTime}` : availTime,
      DueDate: moment.utc(moment().toDate()).format(moment.HTML5_FMT.DATE),
      DueTime: dueTime.length === 4 ? `0${dueTime}` : dueTime,
      ExpireDate: moment.utc(moment().toDate()).add(1, 'd').format(moment.HTML5_FMT.DATE),
      ExpireTime: expireTime.length === 4 ? `0${expireTime}` : expireTime,
    };
    this.taskService
      .addTask(newTask)
      .then((response: CreateTaskResponse) => {
        this.itemClicked({ ...newTask, ID: response.NewTask.ID, AssignedUsers: [], CurrentStatus: 'open' });
        if (this.tasksByDate$.value.some((date) => date.dateIsToday)) {
          const newTasksByDateAdd = this.tasksByDate$.value.map((date) => {
            if (date.dateIsToday) {
              return { ...date, tasks: [...date.tasks, response.NewTask] };
            } else {
              return date;
            }
          });
          this.tasksByDate$.next(newTasksByDateAdd);
        } else {
          const newTasksByDateAdd = [
            ...this.tasksByDate$.value,
            { active: true, dateIsToday: true, dateString: moment().format('YYYY-MM-DD'), tasks: [response.NewTask] },
          ] as TasksByDate[];
          this.tasksByDate$.next(newTasksByDateAdd);
        }

        this.scrollToDate();

        this.setSelectedTaskID.next(response.NewTask.ID);
        const snackBarRef = this.snackBar.open('Task Created', 'Undo', {
          duration: 4000,
        });
        snackBarRef.afterDismissed().subscribe((info) => {
          if (info.dismissedByAction) {
            this.setSelectedTaskID.next(-1);
            this.hideTask(response.NewTask);
            this.closeChecklistPanel.next(!this.closeChecklistPanel.value);
            this.taskService.deleteTask(response.NewTask.ID).subscribe(() => {
              const newTasksByDateDelete = this.tasksByDate$.value.map((date) => {
                if (date.dateIsToday) {
                  return { ...date, tasks: date.tasks.filter((task) => task.ID !== response.NewTask.ID) };
                } else {
                  return date;
                }
              });

              this.tasksByDate$.next(newTasksByDateDelete);
            });
          }
        });
      })
      .catch((err) => {
        this.snackBar.open('Error created Task', 'Close', {
          duration: 4000,
        });
      });
  }

  public dayClicked(event: string): void {
    const frag = event.split('-');
    const date = {
      day: parseInt(frag[2], 10),
      month: parseInt(frag[1], 10),
      year: parseInt(frag[0], 10),
    } as NgbDateStruct;
    const targetDate = UtilMomentService.new(`${date.year}-${date.month}-${date.day}`, 'YYYY-MM-DD').format('YYYY-MM-DD');
    setTimeout(() => UiScrollService.scrollTo(targetDate), 0);
    this.date$.next(date);
  }

  public refreshData(): void {
    const sub = this.taskService.getTasksListByDate$.subscribe((res) => {
      this.localStorage.setItem('tasks-data', UtilCloneService.deepClone(res));
      this.tasksByDate$.next(res);
    });
    this.subscriptions.push(sub);
  }

  public hideTask(taskToHide: Task): void {
    if (!taskToHide) {
      this.refreshData();
      return;
    }
    this.tasksByDate$.next(
      this.tasksByDate$.getValue().map((byDate) => {
        if (byDate.dateString === taskToHide.AvailableDate) {
          return { ...byDate, tasks: byDate.tasks.filter((byDateFilter) => byDateFilter.ID !== taskToHide.ID) };
        } else {
          return byDate;
        }
      })
    );
  }

  public deselectTask(): void {
    if (this.taskHiddenSnackbar) {
      const snackBarRef = this.snackBar.open('New task hidden by filters', 'Clear Filters', {
        duration: 5000,
      });
      snackBarRef.afterDismissed().subscribe((info) => {
        if (info.dismissedByAction) {
          const tempDate = this.date$.value;
          const tempFilterItems = this.filterItems$.value;
          const tempSearch = this.search$.value;
          this.reset();
          const snackBarRef2 = this.snackBar.open('Filters cleared', 'Undo', {
            duration: 5000,
          });
          snackBarRef2.afterDismissed().subscribe((info2) => {
            if (info2.dismissedByAction) {
              this.date$.next(tempDate);
              this.filterItems$.next(tempFilterItems);
              this.search$.next(tempSearch);
              this.scrollToDate();
            }
          });
        }
      });
      this.taskHiddenSnackbar = false;
    }
    this.setSelectedTaskID.next(this.setSelectedTaskID.value <= 0 ? this.setSelectedTaskID.value - 1 : 0);
  }

  public scrollToDate(hard: boolean = false, date: Date = this.today): void {
    const targetDate = UtilMomentService.new(date).format('YYYY-MM-DD');
    setTimeout(() => UiScrollService.scrollTo(targetDate, hard), 0);
  }

  private filterByDate(data: TasksByDate[], date: NgbDateStruct): TasksByDate[] {
    const targetDate = UtilMomentService.new(`${date.year}-${date.month}-${date.day}`, 'YYYY-MM-D').format('YYYY-MM-DD');
    data.forEach((item: TasksByDate) => {
      item.active = item.dateString === targetDate;
    });
    return data;
  }

  private filterBySearch(data: TasksByDate[], search: string): TasksByDate[] {
    data.forEach((item: TasksByDate) => {
      const filteredTasks = [];
      item.tasks.forEach((task: Task) => {
        if (task.Name.toLowerCase().indexOf(search.toLowerCase()) > -1) {
          filteredTasks.push(task);
        }
      });
      item.tasks = filteredTasks;
    });
    return data;
  }

  private filterByFilters(data: TasksByDate[], filter: TaskFilterItem[]): TasksByDate[] {
    const activeItems = filter.filter((i) => i.active);
    data.forEach((item: TasksByDate) => {
      const filteredTasks = [];
      item.tasks.forEach((task: Task) => {
        let shouldInclude = false;
        shouldInclude = this.checkFilterByStatus(activeItems, task);
        if (shouldInclude) {
          shouldInclude = this.checkFilterByMyAssignments(activeItems, task);
        }
        if (shouldInclude) {
          shouldInclude = this.checkFilterByTeamAssignments(activeItems, task);
        }
        if (shouldInclude) {
          shouldInclude = this.checkFilterByRoleAssignments(activeItems, task);
        }
        if (shouldInclude) {
          filteredTasks.push(task);
        }
      });
      item.tasks = filteredTasks;
    });
    return data;
  }

  private buildTasksByDateArray(tbd: TasksByDate[]): TasksByDate[] {
    const newTbd = [];
    const dateRange = this.getTaskDateRange();
    let dateBuild = dateRange.start;

    let tbdItem = null;
    do {
      const index = _.findIndex(tbd, { dateString: dateBuild });
      if (index > -1) {
        tbdItem = tbd[index];
      } else {
        const dateIsToday = UtilMomentService.new(this.today).format('YYYY-MM-DD') === dateBuild;
        tbdItem = {
          active: false,
          dateString: dateBuild,
          tasks: [],
          dateIsToday: dateIsToday,
        };
      }
      newTbd.push(tbdItem);
      dateBuild = UtilMomentService.new(dateBuild, 'YYYY-MM-DD').add(1, 'day').format('YYYY-MM-DD');
    } while (dateBuild !== dateRange.end);
    return newTbd;
  }

  private checkFilterByStatus(activeItems: TaskFilterItem[], task: Task): boolean {
    if (activeItems.some((checkItem) => checkItem.value.toLowerCase() === 'all-tasks') || activeItems.length === 0) {
      return true;
    } else {
      return activeItems.some((checkItem) => checkItem.value.toLowerCase() === task.CurrentStatus.toLowerCase());
    }
  }

  private checkFilterByMyAssignments(activeItems: TaskFilterItem[], task: Task): boolean {
    const filterByUnassigned = activeItems.some((checkItem) => checkItem.value === 'unassigned-tasks');
    const filterByMyTasks = activeItems.some((checkItem) => checkItem.value === 'my-tasks');
    if (!filterByUnassigned && !filterByMyTasks) {
      return true;
    }
    if (task.AssignedUsers.length === 0) {
      return filterByUnassigned;
    } else if (task.AssignedUsers.some((checkUser) => checkUser.ID === this.loggedInUser.CurrentUser.ID)) {
      return filterByMyTasks;
    }
  }

  private checkFilterByTeamAssignments(activeItems: TaskFilterItem[], task: Task): boolean {
    const allTeamItems = activeItems.filter((item) => item.value.includes('_teamID'));
    if (allTeamItems.length === 0) {
      return true;
    }

    return allTeamItems.some((checkItem) => task.AssignedUsers.some((assignedUser) => assignedUser.ID.toString() === checkItem.value.substring(7, 8)));
  }

  private checkFilterByRoleAssignments(activeItems: TaskFilterItem[], task: Task): boolean {
    const allRoleItems = activeItems.filter((item) => item.value.includes('_roleID'));
    if (task.AssignedRoles.length === 0 && activeItems.some((item) => item.value === 'none')) {
      return true;
    } else if (allRoleItems.length === 0) {
      return true;
    }
    return allRoleItems.some((checkItem) => task.AssignedRoles.some((assignedRole) => assignedRole.ID.toString() === checkItem.value.substring(7, 8)));
  }

  private getTaskDateRange(date?: Date): { start: string; end: string } {
    if (!date) date = this.today;

    return {
      start: UtilMomentService.new(date).format('YYYY-MM-DD'),
      end: UtilMomentService.new(date).add(7, 'days').format('YYYY-MM-DD'),
    };
  }
}
