import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'inXDays',
})
export class InXDaysPipe implements PipeTransform {
  public transform(value: string) {
    if (!isNaN(+value)) {
      const days = +value === 1 ? 'day' : 'days';
      return `in ${value} ${days}`;
    }

    const arr = value?.split('') || [];
    const noNumbers = arr
      .filter((e) => e !== ' ')
      .map(Number)
      .every(isNaN);
    const doesNotEndInS = arr[arr.length - 1] !== 's';
    if (noNumbers || doesNotEndInS) {
      return '';
    }

    return value;
  }
}
