import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, take } from 'rxjs/operators';
import { ApiAuthenticationService } from '../services/api-authentication.service';
import { UtilWindowRefService, UtilLocalStorageService } from '@xenon/utils';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(
    private apiAuth: ApiAuthenticationService,
    private utilWindowRefService: UtilWindowRefService,
    private snackBar: MatSnackBar,
    private localStorage: UtilLocalStorageService
  ) {}

  private accessToken: string;
  private refreshToken: string;

  private attemptingRefresh = false;

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    this.accessToken = this.localStorage.getItem('user').AccessToken;
    this.refreshToken = this.localStorage.getItem('user').RefreshToken;
    if (this.accessToken) {
      const updatedRequest = req.clone({ headers: req.headers.set('Authorization', `Bearer ${this.accessToken}`) });
      return next.handle(updatedRequest).pipe(
        catchError((error) => {
          const message = error.error.Message.toLowerCase();
          if (message.includes('noauth')) {
            this.attemptRefreshToken();
          }
          return throwError(error);
        })
      );
    } else {
      return next.handle(req);
    }
  }

  private attemptRefreshToken(): void {
    if (!this.attemptingRefresh) {
      this.attemptingRefresh = true;

      const request = {
        RefreshToken: this.refreshToken,
        AccessToken: this.accessToken,
      };
      this.apiAuth
        .refreshToken(request)
        .then((result) => {
          const loggedInUser = this.localStorage.getItem('user');
          this.localStorage.setItem('user', { ...loggedInUser, AccessToken: result.AccessToken, RefreshToken: result.RefreshToken });
          this.attemptingRefresh = false;
        })
        .catch((_) => {
          this.attemptingRefresh = false;
          this.forceLogOut();
        });
    }
  }

  private forceLogOut(): void {
    this.apiAuth
      .logout()
      .pipe(take(1))
      .subscribe((_) => {
        this.snackBar.open('Session revoked please login again.', `Close`, { duration: 5000 });
        const origin = this.utilWindowRefService.nativeWindow.location.origin;
        this.utilWindowRefService.nativeWindow.location.replace(`${origin}/#/login`);
      });
  }
}
