import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatSidenav } from '@angular/material/sidenav';
import {
  AddTaskRecurrenceRequest,
  AddTaskRecurrenceResponse,
  ApiTaskRecurrenceService,
  GetTaskRecurrenceResponse,
  RecurrenceDay,
  TaskTemplate,
  User,
  TaskRecurrence,
} from '@xenon/api';
import * as moment from 'moment';
import { DAYS_OF_MONTH, DAYS_OF_WEEK, MONTH_RELATIVE_DAYS, REPEAT_OPTIONS, TASK_RECURRENCE, TIME_UNITS } from './recurrence.const';
import { InXDaysPipe } from './in-x-days.pipe';
import { MatSnackBar } from '@angular/material/snack-bar';
import { BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'xenon-recurrence',
  styleUrls: ['./recurrence.component.scss'],
  templateUrl: './recurrence.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class RecurrenceComponent implements OnInit {
  @ViewChild('drawer') sideNav: MatSidenav;
  @Input() isNewTask: boolean;
  @Input() user: User;
  @Input() selectedTask: TaskTemplate;
  @Input() set callUpdateRecurrence(event) {
    if (!event) return;
    this.updateRecurrence();
  }
  @Output() recurrenceSave = new EventEmitter<boolean>();
  public taskRecurrence = TASK_RECURRENCE;
  public daysOfWeek = DAYS_OF_WEEK;
  public daysOfMonth = DAYS_OF_MONTH;
  public monthRelativeDays = MONTH_RELATIVE_DAYS;
  // public startTime = START_TIME;
  public startTime = '12:00';
  public dueTime;
  public endTime;
  public expireDays;
  public dueDays;
  public repeatOptions = REPEAT_OPTIONS;
  public timeUnits = TIME_UNITS;
  public daysOfMonthView = true;
  public startDateValue = moment().toDate();
  public endDateValue = moment().add(1, 'M').toDate();
  public listOfTaskRecurrences: BehaviorSubject<TaskRecurrence[]> = new BehaviorSubject(null);
  public newRule: boolean;

  private inXDaysPipe: InXDaysPipe;

  constructor(private apiTaskRecurrenceService: ApiTaskRecurrenceService, private snackBar: MatSnackBar) {}

  public ngOnInit(): void {
    this.inXDaysPipe = new InXDaysPipe();

    if (this.selectedTask.ID) {
      this.apiTaskRecurrenceService
        .getTaskRecurrenceByTaskTemplateId(this.selectedTask.ID)
        .pipe(take(1))
        .subscribe((response: GetTaskRecurrenceResponse) => {
          if (response.Status === 'success') {
            this.listOfTaskRecurrences.next(response.TaskRecurrenceList);
          } else if (response.Status === 'error') {
            this.snackBar.open(response.Message, 'Close', {
              duration: 5000,
            });
          }
        });
    }
  }

  public deleteRecurrence(ID: number, event: Event): void {
    event.stopPropagation();
    this.apiTaskRecurrenceService
      .deleteTaskRecurrence(ID)
      .pipe(take(1))
      .subscribe((result) => {
        if (result.Status === 'success') {
          this.snackBar.open('Recurrence Deleted', 'Close', { duration: 5000 });
        } else {
          this.snackBar.open(result.Message, 'Close', { duration: 5000 });
        }
      });
  }

  public getRecurrenceString(recurrence: TaskRecurrence): string {
    let unitString = '';
    let dayOfWeek = '';
    switch (recurrence.RepeatUnit) {
      case 3:
        unitString = recurrence.RepeatValue > 1 ? 'days' : 'day';
        break;
      case 4:
        unitString = recurrence.RepeatValue > 1 ? 'weeks' : 'week';
        break;
      case 5:
        unitString = recurrence.RepeatValue > 1 ? 'months' : 'month';
        break;
    }

    if (recurrence.DaysOfWeek !== '0') {
      switch (recurrence.DaysOfWeek) {
        case '1':
          dayOfWeek = 'on Monday';
          break;
        case '2':
          dayOfWeek = 'on Tuesday';
          break;
        case '3':
          dayOfWeek = 'on Wednesday';
          break;
        case '4':
          dayOfWeek = 'on Thursday';
          break;
        case '5':
          dayOfWeek = 'on Friday';
          break;
        case '6':
          dayOfWeek = 'on Saturday';
          break;
        case '7':
          dayOfWeek = 'on Sunday';
          break;
      }
    }

    return `${recurrence.RepeatValue > 1 ? recurrence.RepeatValue : ''} ${unitString} ${dayOfWeek}`;
  }

  public editRecurrence(recurrence: TaskRecurrence): void {
    this.newRule = false;
    this.taskRecurrence = recurrence;
    this.togglePanel();
  }

  public newRecurrence(): void {
    this.newRule = true;
    this.sideNav.toggle();
  }

  public getStartTimeString(time: string): string {
    let startTime = time + ' AM';
    const intHour = parseInt(time.substr(0, 2), 10);
    if (intHour < 10) {
      startTime = `${time.substr(1, time.length)} AM`;
    } else if (intHour > 12) {
      startTime = `${intHour - 12}:${time.substr(3, 5)} PM`;
    }
    return startTime;
  }

  public togglePanel() {
    this.sideNav.toggle();
  }

  public updateRecurrenceItem(): void {
    this.formatDates();
    this.taskRecurrence.TaskTemplateID = this.selectedTask.ID;

    const taskRecurrence: AddTaskRecurrenceRequest = {
      ...this.taskRecurrence,
      AvailableTime: this.startTime,
      DueTime: this.dueTime,
      DueDays: this.getOnlyDays(this.dueDays),
      ExpireTime: this.endTime,
      ExpireDays: this.getOnlyDays(this.expireDays),
    };

    const isoDate = new Date(taskRecurrence.StartDate);
    taskRecurrence.StartDate = isoDate.toISOString();

    this.apiTaskRecurrenceService
      .updateTaskRecurrence(taskRecurrence)
      .pipe(take(1))
      .subscribe((response) => {
        this.taskRecurrence.ID = response.ID;
        this.setSelectedDays();
        this.initDates();
        this.togglePanel();
      });
  }

  public addRecurrence() {
    this.formatDates();
    this.taskRecurrence.TaskTemplateID = this.selectedTask.ID;

    if (this.user) {
      this.taskRecurrence.CreatedBy = this.user.ID;
    }

    const taskRecurrence: AddTaskRecurrenceRequest = {
      ...this.taskRecurrence,
      AvailableTime: this.startTime,
      DueTime: this.dueTime,
      DueDays: this.getOnlyDays(this.dueDays),
      ExpireTime: this.endTime,
      ExpireDays: this.getOnlyDays(this.expireDays),
    };

    const isoDate = new Date(taskRecurrence.StartDate);
    taskRecurrence.StartDate = isoDate.toISOString();

    this.apiTaskRecurrenceService
      .addTaskRecurrence(taskRecurrence)
      .then((response: AddTaskRecurrenceResponse) => {
        this.taskRecurrence.ID = response.NewTask;
        this.setSelectedDays();
        this.initDates();
        this.togglePanel();
      })
      .catch((err) => {
        // TODO may want to show error here.
      });
  }

  public getOnlyDays(days: string): number {
    return days ? +days.replace(/\D/g, '') : 0;
  }

  public updateDays(toUpdate: 'dueDays' | 'expireDays') {
    if (toUpdate === 'dueDays') {
      this.dueDays = this.inXDaysPipe.transform(this.dueDays);
    } else if (toUpdate === 'expireDays') {
      this.expireDays = this.inXDaysPipe.transform(this.expireDays);
    }
  }

  public selectWeekday(weekday: RecurrenceDay) {
    const day = this.daysOfWeek.find((w) => weekday === w);

    if (day === this.daysOfWeek[0]) {
      this.daysOfWeek.forEach((w) => (w.selected = false));
      this.daysOfWeek[0].selected = true;
    } else {
      day.selected = !day.selected;
      this.daysOfWeek[0].selected = false;
    }

    this.taskRecurrence.DaysOfWeek = this.updateRecurrenceValues(this.daysOfWeek);
  }

  public selectDaysTypeView(viewType: string) {
    this.daysOfMonthView = viewType === 'm';
  }

  private updateRecurrenceValues(selectedValues: RecurrenceDay[]) {
    let recurrenceValues = '';
    selectedValues.forEach((wd) => {
      if (wd.selected) {
        recurrenceValues += wd.value + ',';
      }
    });
    return recurrenceValues;
  }

  private formatDates() {
    const format = (date: Date) => {
      const d = new Date(date);
      let month = '' + (d.getMonth() + 1);
      let day = '' + d.getDate();
      const year = d.getFullYear();

      if (month.length < 2) month = '0' + month;
      if (day.length < 2) day = '0' + day;

      return [year, month, day].join('-');
    };

    this.taskRecurrence.StartDate = format(this.startDateValue);
    this.taskRecurrence.EndDate = format(this.endDateValue);
  }

  private setSelectedDays() {
    this.daysOfWeek.forEach((weekday) => {
      const daysOfWeek = this.taskRecurrence.DaysOfWeek.split(',');
      if (daysOfWeek.includes(String(weekday.value))) {
        weekday.selected = true;
      }
    });

    this.daysOfMonth.forEach((day) => {
      const monthDays = this.taskRecurrence.DaysOfMonth.split(',');
      if (monthDays.includes(String(day.value))) {
        day.selected = true;
      }
    });

    this.monthRelativeDays.forEach((day) => {
      const monthDays = this.taskRecurrence.MonthRelativeDays.split(',');
      if (monthDays.includes(String(day.value))) {
        day.selected = true;
      }
    });

    if (this.taskRecurrence.RepeatUnit === 5) {
      if (this.taskRecurrence.DaysOfMonth) {
        this.daysOfMonthView = true;
      } else {
        this.daysOfMonthView = false;
      }
    }
  }

  private initDates() {
    this.startDateValue = moment(this.taskRecurrence.StartDate).toDate();
    this.endDateValue = moment(this.taskRecurrence.EndDate).toDate();
  }

  private updateRecurrence() {
    if (!this.taskRecurrence) {
      return;
    }

    this.formatDates();
    this.clearPreviouslySelectedDays();

    this.apiTaskRecurrenceService.updateTaskRecurrence(this.taskRecurrence).subscribe((response) => {
      this.recurrenceSave.emit(true);
    });
  }

  private clearPreviouslySelectedDays() {
    switch (this.taskRecurrence.RepeatUnit) {
      case 3: //Days
        this.taskRecurrence.DaysOfMonth = '';
        this.taskRecurrence.DaysOfWeek = '';
        this.taskRecurrence.MonthRelativeDays = '';
        break;
      case 4: //Weekdays
        this.taskRecurrence.DaysOfMonth = '';
        this.taskRecurrence.MonthRelativeDays = '';
        break;
      case 5: //Month
        this.taskRecurrence.DaysOfWeek = '';

        if (this.daysOfMonthView) {
          this.taskRecurrence.MonthRelativeDays = '';
        } else {
          this.taskRecurrence.DaysOfMonth = '';
        }
        break;
    }
  }
}
