import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Task } from '@xenon/api';

@Component({
  selector: 'xenon-task-list',
  styleUrls: ['./task-list.component.scss'],
  template: `
    <table mat-table [dataSource]="tasks" class="mat-elevation-z0" class="task-list-table">
      <ng-container *ngFor="let colName of displayedColumns" matColumnDef="{{ colName }}">
        <th mat-header-cell *matHeaderCellDef>{{ columnTitles[colName] }}</th>
        <td class="clickable" mat-cell *matCellDef="let element" (click)="clickedItem(element)">
          {{ element[colName] }}
        </td>
      </ng-container>
      <tr mat-header-row *matHeaderRowDef="displayedColumns"></tr>
      <tr mat-row *matRowDef="let row; columns: displayedColumns"></tr>
    </table>
  `,
})
export class TaskListComponent {
  displayedColumns: string[] = ['Name', 'ItemsComplete', 'ItemsCompletedLate', 'ItemsRemaining', 'ItemsExpired'];
  columnTitles = {
    Name: 'Name',
    ItemsComplete: 'Items Completed',
    ItemsCompletedLate: 'Items Completed Late',
    ItemsRemaining: 'Remaining Items',
    ItemsExpired: 'Missed Items',
  };

  @Input() tasks: Task[] = [];
  @Output() itemClicked = new EventEmitter<Task>();

  clickedItem(item: Task) {
    this.itemClicked.emit(item);
  }
}
