import { Component, Inject, OnInit, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AddContentRequest, ApiDriveService, DriveItem } from '@xenon/api';
import { BehaviorSubject } from 'rxjs';
import { filter, take } from 'rxjs/operators';

@Component({
  selector: 'xenon-text-document',
  template: ` <div *ngIf="driveItem | async as driveItem">
    <div class="w-100 px-3 space-title" fxLayoutAlign="space-between center">
      <h6 class="m-0 w-100" fxLayoutAlign="start center">
        <button class="mr-2" mat-icon-button (click)="close()">
          <mat-icon>arrow_back</mat-icon>
        </button>
        <xenon-ui-expanding-input
          [startValue]="data.driveItem.details.Name"
          [minWidth]="200"
          [formFieldClasses]="['mat-form-field-alternate', 'mb--7']"
          [disabled]="viewMode | async"
          placeholder="Untitled Document"
          floatLabel="never"
          (fieldValue)="data.driveItem.details.Name = $event"
        ></xenon-ui-expanding-input>
        <!-- <mat-form-field floatLabel="never" class="mat-form-field-alternate mb--7" fxFlex="50%">
          <input autocomplete="off" [disabled]="viewMode | async" matInput [(ngModel)]="driveItem.details.Name" placeholder="Untitled Document" />
        </mat-form-field> -->
      </h6>
      <span fxLayout="row">
        <button *ngIf="viewMode | async; else edit" color="primary" class="p-2 mx-2" mat-stroked-button (click)="editClicked()">Edit</button>
        <ng-template #edit> <button color="primary" class="p-2 mx-2" mat-stroked-button (click)="saveClicked()">Save</button></ng-template>
        <button mat-icon-button [matMenuTriggerFor]="menu"><mat-icon>more_vert</mat-icon></button>
        <mat-menu #menu="matMenu">
          <xenon-drive-vert-menu [item]="driveItem" [hideItems]="['edit', 'rename']" (refresh)="handleRefresh($event)"></xenon-drive-vert-menu>
        </mat-menu>
      </span>
    </div>
    <mat-divider></mat-divider>
    <div class="task-container page-height" fxLayout="row">
      <div class="sidebar overflow-hidden"></div>
      <div class="main-form-container text-center w-100 pt-5 h-100">
        <mat-card class="main-form background-white h-100">
          <mat-form-field class="task-name text-subtitle1 mat-form-field-alternate w-100 h-100 px-3 form-line-gray" floatLabel="never">
            <textarea autocomplete="off" [disabled]="viewMode | async" class="h-100 text-body1" matInput [(ngModel)]="driveItem.details.Content"></textarea>
          </mat-form-field>
        </mat-card>
      </div>
      <div class="sidebar overflow-hidden"></div>
    </div>
  </div>`,
  styleUrls: ['./text-file.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TextFileComponent implements OnInit {
  public driveItem: BehaviorSubject<DriveItem> = new BehaviorSubject(null);
  public viewMode: BehaviorSubject<boolean> = new BehaviorSubject(true);

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { driveItem?: DriveItem; addContentRequest?: AddContentRequest; editFile?: boolean },
    public dialogRef: MatDialogRef<TextFileComponent>,
    private apiDriveService: ApiDriveService
  ) {
    if (data.driveItem) {
      data.driveItem.details.Name = data.driveItem.details.Name === 'Untitled Document' ? '' : data.driveItem.details.Name;
    }
  }

  public ngOnInit(): void {
    this.viewMode.next(!this.data.editFile);
    if (this.data.addContentRequest) {
      this.apiDriveService.saveContentInfo(this.data.addContentRequest).then((newDriveFileID) => {
        this.apiDriveService
          .getDriveFileByID(newDriveFileID.NewDrive)
          .pipe(
            filter((drive) => !!drive),
            take(1)
          )
          .subscribe((drive) => {
            console.log(drive);
            const driveItem: DriveItem = {
              id: drive.ID,
              name: drive.Name,
              active: false,
              icon: null,
              starred: drive.StarredInd,
              details: drive,
            };
            this.data.driveItem = driveItem;
            this.driveItem.next(driveItem);
          });
      });
    } else if (this.data.driveItem) {
      this.driveItem.next(this.data.driveItem);
    }
  }

  public handleRefresh(event: string): void {
    if (event === 'delete') {
      this.close();
    }
  }

  public close(save = false): void {
    if ((save && !this.driveItem.value.details.Content && !this.driveItem.value.details.Name) || (!save && this.data.addContentRequest)) {
      this.apiDriveService.deleteDriveItem(this.driveItem.value.id).then((_) => {
        this.dialogRef.close();
      });
    }
    this.dialogRef.close();
  }

  public saveClicked(): void {
    console.log(this.driveItem);
    this.apiDriveService.updateDriveItem(this.driveItem.value.id, {
      Name: !!this.driveItem.value.details.Name ? this.driveItem.value.details.Name : 'Untitled Document',
      Content: this.driveItem.value.details.Content,
    });
    this.close(true);
  }

  public editClicked(): void {
    this.viewMode.next(false);
  }
}
