import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Role, TaskTemplate, User, Location, SubTask } from '@xenon/api';
import { BehaviorSubject } from 'rxjs';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-complete-checklist-template',
  templateUrl: './complete-checklist-template.component.html',
  styleUrls: ['./complete-checklist-template.component.scss'],
})
export class CompleteChecklistTemplateComponent {
  @Input() selectedTask: TaskTemplate;
  @Input() data: any;
  @Input() set saveData(save: boolean) {
    if (save) {
      this.saveAndClose();
    }
  }
  @Output() closeDialog = new EventEmitter<TaskTemplate>();
  user: User;
  callUpdateRecurrence: BehaviorSubject<Boolean> = new BehaviorSubject(null);
  assignedRoles: Role[];
  assignedUsers: User[];
  subTaskList: SubTask[];
  locationID: number;

  fieldExpanded = true;

  constructor(private localStorage: UtilLocalStorageService) {
    const savedUser = this.localStorage.getItem('user').CurrentUser;
    this.user = savedUser;
  }

  saveAndClose() {
    this.callUpdateRecurrence.next(!this.callUpdateRecurrence.value);
    let newTaskTemplate = this.selectedTask;
    if (this.locationID) {
      newTaskTemplate = { ...newTaskTemplate, LocationID: this.locationID };
    }
    if (this.assignedUsers) {
      newTaskTemplate = { ...newTaskTemplate, AssignedUsers: this.assignedUsers };
    }
    if (this.assignedRoles) {
      newTaskTemplate = { ...newTaskTemplate, AssignedRoles: this.assignedRoles };
    }
    if (this.subTaskList) {
      newTaskTemplate = { ...newTaskTemplate, SubTaskList: this.subTaskList };
    }
    this.closeDialog.emit(newTaskTemplate);
  }
}
