import { Component } from '@angular/core';
import { BehaviorSubject, combineLatest, forkJoin, Observable, of } from 'rxjs';
import { AppBase } from '../../../app-base';
import { map, switchMap } from 'rxjs/operators';
import * as _ from 'lodash-es';
import { ApiOrganizationService, ApiUserService, Organization, User } from '@xenon/api';
import { UtilCloneService, UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-team',
  template: `
    <div class="page-header" fxLayout="row" fxLayoutAlign="space-between center">
      <h6 class="title">Team</h6>
      <xenon-ui-string-search [searchString]="search$ | async" (searchChange)="searchChange($event)"> </xenon-ui-string-search>
    </div>

    <div class="page-body">
      <xenon-team-list [users]="usersData$ | async"></xenon-team-list>
    </div>
  `,
})
export class TeamComponent extends AppBase {
  queue = [];
  users: User[];
  users$ = this.userService.getUsersByOrganization(this.organizationId).pipe(
    switchMap((response: User[]) => {
      this.users = response;
      const orgIds = _.uniq(response.map((r) => r.PrimaryOrgID));
      orgIds.forEach((id) => this.queue.push(this.organizationService.getOrg(id)));
      return forkJoin(this.queue);
    }),
    map((organizations: Organization[]) => {
      this.users.forEach((u) => {
        const orgIndex = _.findIndex(organizations, { ID: u.PrimaryOrgID });
        if (orgIndex > -1) {
          u.Org = organizations[orgIndex];
        }
      });
      return this.users;
    })
  );

  search$: BehaviorSubject<string> = new BehaviorSubject(null);

  usersData$: Observable<User[]> = combineLatest([this.users$, this.search$]).pipe(
    switchMap(([users, search]) => {
      let newUserList: User[] = UtilCloneService.deepClone(users);
      if (search) {
        newUserList = newUserList.filter((u: User) => {
          const searchStr = `${u.FirstName} ${u.LastName} ${u.JobTitle} ${u.Email} ${u.Phone}`.toLowerCase();
          const searchIndex = searchStr.indexOf(search.toLowerCase());
          return searchIndex > -1;
        });
      }
      return of(newUserList);
    })
  );

  constructor(private localStorage: UtilLocalStorageService, private userService: ApiUserService, private organizationService: ApiOrganizationService) {
    super(localStorage);
  }

  searchChange(event: string) {
    this.search$.next(event);
  }
}
