import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiOrganizationService, Organization } from '@xenon/api';
import { NotificationType, UiNotificationService, UiFormFieldType, UiFormFieldImageUpload } from '@xenon/ui';
import { take } from 'rxjs/operators';
import { UtilLocalStorageService } from '@xenon/utils';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'xenon-edit-organization-logo-dialog',
  styleUrls: ['./edit-organization-logo-dialog.component.scss'],
  template: `<h6>Organization Logo</h6>
    <xenon-ui-form-field-image-upload
      [fieldOptions]="fieldOptions"
      [fieldFormGroup]="form"
      [buttonText]="'Select Profile Pic'"
      (pictureChanged)="onSelectProfilePic($event)"
    >
    </xenon-ui-form-field-image-upload>
    <div fxLayoutAlign="end center">
      <button mat-button color="primary" (click)="close()">Cancel</button>
      <button mat-button color="primary" (click)="update()">Update</button>
    </div> `,
})
export class EditOrganizationLogoDialogComponent {
  public fieldOptions = {
    name: 'PhotoUpload',
    label: 'Logo Pic',
    type: UiFormFieldType.File,
    validators: [],
  } as UiFormFieldImageUpload;
  public form = new FormGroup({
    PhotoUpload: new FormControl(),
  });

  private photoToSave: File;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: Organization,
    public dialogRef: MatDialogRef<EditOrganizationLogoDialogComponent>,
    private apiOrganizationService: ApiOrganizationService,
    private notificationService: UiNotificationService,
    private localStorage: UtilLocalStorageService
  ) {}

  public close(): void {
    this.dialogRef.close();
  }

  public onSelectProfilePic(event): void {
    this.photoToSave = event.target.files[0];
  }

  public update(): void {
    this.apiOrganizationService
      .updateOrgLogo(this.data.OrgID, this.photoToSave)
      .pipe(take(1))
      .subscribe((result) => {
        if (result.FileURL !== '') {
          const currentUser = this.localStorage.getItem('user').CurrentUser;
          const newLocalItem = {
            ...currentUser,
            Orgs: currentUser.Orgs.map((userOrg: Organization) => {
              if (userOrg.OrgID === this.data.OrgID) {
                return { ...userOrg, LogoURL: result.FileURL };
              } else {
                return userOrg;
              }
            }),
          };
          let newLoggedInUser = this.localStorage.getItem('user');
          newLoggedInUser = { ...newLoggedInUser, CurrentUser: newLocalItem };
          this.localStorage.setItem('user', newLoggedInUser);
          this.dialogRef.close(result.FileURL);
        } else {
          this.notificationService.show(NotificationType.error, 'ERROR', '', 2500);
        }
      });
  }
}
