import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TaskFormBase } from '../task-form-base';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { debounceTime } from 'rxjs/operators';
import { ApiFormService, Form, FormItem } from '@xenon/api';
import { UtilLocalStorageService, UtilMomentService } from '@xenon/utils';

@Component({
  selector: 'xenon-task-form-field-date',
  styleUrls: ['./task-form-field-date.component.scss'],
  template: `
    <form [formGroup]="form">
      <mat-form-field appearance="fill">
        <mat-label>{{ formItem.Label }}</mat-label>
        <input matInput [matDatepicker]="picker" formControlName="inputItem" />
        <mat-datepicker-toggle matSuffix [for]="picker"></mat-datepicker-toggle>
        <mat-datepicker #picker></mat-datepicker>
      </mat-form-field>
    </form>
  `,
})
export class TaskFormFieldDateComponent extends TaskFormBase implements OnInit {
  @Input() formItem: FormItem;

  constructor(formService: ApiFormService, localStorageService: UtilLocalStorageService, private cd: ChangeDetectorRef) {
    super(formService, localStorageService);
  }

  ngOnInit(): void {
    const initDate = UtilMomentService.new(this.formItem.Response, 'YYYY-MM-DD').toDate();
    this.form = new FormGroup({
      inputItem: new FormControl(initDate, Validators.required),
    });

    this.form.valueChanges.pipe(debounceTime(500)).subscribe((v) => {
      const date = UtilMomentService.new(v.inputItem).format('YYYY-MM-DD');
      this.submitPatch(date, this.formItem).subscribe((response: { Message: string; formItem: FormItem }) => {
        this.formItem = response.formItem;
        this.cd.detectChanges();
      });
    });
  }
}
