import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface ResetPasswordData {
  email: string;
}

@Component({
  selector: 'xenon-reset-password',
  styleUrls: ['./reset-password.component.scss'],
  template: `<mat-card>
    <div fxLayout="column">
      <h5 class="text-center">Reset Your Password</h5>
      <mat-form-field>
        <mat-label>Email</mat-label>
        <input matInput type="email" placeholder="Email" [(ngModel)]="data.email" />
      </mat-form-field>
      <button mat-flat-button color="primary" class="py-2" [disabled]="data.email === ''" (click)="onSubmit()">
        RESET PASSWORD
      </button>
    </div>
  </mat-card>`,
})
export class ResetPasswordComponent {
  constructor(public dialogRef: MatDialogRef<ResetPasswordComponent>, @Inject(MAT_DIALOG_DATA) public data: ResetPasswordData) {}

  public onNoClick(): void {
    this.dialogRef.close();
  }

  public onSubmit(): void {
    this.dialogRef.close(this.data);
  }
}
