import { Injectable } from '@angular/core';
import { UtilWindowRefService } from './util-window-ref.service';

@Injectable({
  providedIn: 'root',
})
export class UtilLocalStorageService {
  private storage;

  constructor(private windowRefService: UtilWindowRefService) {
    this.storage = this.windowRefService.nativeWindow.localStorage;
  }

  setItem(index: string, value: any): void {
    this.storage.setItem(index, JSON.stringify(value));
  }

  removeItem(index: string): void {
    this.storage.removeItem(index);
  }

  clear(): void {
    this.storage.clear();
  }

  getItem(index: string): any {
    const item = this.storage.getItem(index);
    return JSON.parse(item);
  }
}
