import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { UiFormFieldImageUpload } from '../../models/ui-form-field-image-upload';

@Component({
  selector: 'xenon-ui-form-field-image-upload',
  styleUrls: ['./ui-form-field-image-upload.component.scss'],
  template: `
    <mat-form-field appearance="outline" [formGroup]="fieldFormGroup">
      <mat-label>{{ fieldOptions.label }}</mat-label>
      <ngx-mat-file-input
        formControlName="{{ fieldOptions.name }}"
        (change)="uploadChanged($event)"
      ></ngx-mat-file-input>
      <mat-icon matSuffix>folder</mat-icon>
    </mat-form-field>
  `,
})
export class UiFormFieldImageUploadComponent {
  @Input() fieldFormGroup: FormGroup;
  @Input() fieldOptions: UiFormFieldImageUpload;
  @Input() buttonText = 'Upload Image';
  @Output() pictureChanged = new EventEmitter<HTMLInputElement>();

  public uploadChanged($event): void {
    this.pictureChanged.emit($event);
  }
}
