import { Injectable } from '@angular/core';
import { UtilLocalStorageService } from '@xenon/utils';

@Injectable({
  providedIn: 'root',
})
export class PermissionsHelperService {
  constructor(private localStorage: UtilLocalStorageService) {}

  public hasEveryPermissionInArray(permissions: string[]): boolean {
    return permissions.every((permission) => {
      return this.hasPermissionByName(permission);
    });
  }

  public hasAnyPermissionInArray(permissions: string[]): boolean {
    return permissions.some((permission) => {
      return this.hasPermissionByName(permission);
    });
  }

  public hasPermissionByName(permissionName: string): boolean {
    const aggregatePermissions = this.localStorage.getItem('user') ? this.localStorage.getItem('user').AggregatePermissions : false;
    if (!aggregatePermissions) {
      return false;
    }
    return aggregatePermissions.some((permission) => permission === permissionName);
  }
}
