import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { UtilLocalStorageService } from '@xenon/utils';
import { AppNavigationService } from '../services/app-navigation.service';

@Injectable({
  providedIn: 'root',
})
export class PermissionsAuthGuard implements CanActivate {
  constructor(private localStorageService: UtilLocalStorageService, private navigationService: AppNavigationService, private router: Router) {}
  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    const menu = this.localStorageService.getItem('menu');
    if (menu.some((item) => item.name === state.url.substring(1) && !item.hasAccess)) return this.router.navigate(['login']);
    return true;
  }
}
