import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'xenon-ui-prompt',
  styleUrls: ['./ui-prompt.component.scss'],
  template: `
    <div mat-dialog-title>Prompt</div>
    <div mat-dialog-content>{{ question }}</div>
    <div mat-dialog-actions fxLayout="row" fxLayoutAlign="end center">
      <button mat-flat-button (click)="onNoClick()">No Thanks</button>
      <button mat-flat-button color="primary" (click)="onOkClick()">Ok</button>
    </div>
  `,
})
export class UiPromptComponent {
  question: string;

  constructor(
    public dialogRef: MatDialogRef<UiPromptComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { ok: boolean; question: string }
  ) {
    this.question = this.data.question;
  }

  onNoClick(): void {
    this.dialogRef.close({ ok: false });
  }

  onOkClick() {
    this.dialogRef.close({ ok: true });
  }
}
