import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ApiFormTemplateService, FormTemplate, Role, TaskTemplate, User, LoggedInUser } from '@xenon/api';
import { BehaviorSubject } from 'rxjs';
import { UtilLocalStorageService } from '@xenon/utils';

@Component({
  selector: 'xenon-complete-form-template',
  templateUrl: './complete-form-template.component.html',
  styleUrls: ['./complete-form-template.component.scss'],
})
export class CompleteFormTemplateComponent {
  @Input() selectedTask: TaskTemplate;
  @Input() data: any;
  @Input() set saveData(save: boolean) {
    if (save) {
      this.saveAndClose();
    }
  }
  @Output() closeDialog = new EventEmitter<TaskTemplate>();
  user: LoggedInUser;
  callUpdateRecurrence: BehaviorSubject<Boolean> = new BehaviorSubject(null);
  assignedRoles: Role[];
  assignedUsers: User[];
  locationID: number;
  assignedTemplate: FormTemplate;
  templateList: BehaviorSubject<FormTemplate[]> = new BehaviorSubject(null);

  fieldExpanded = true;

  constructor(apiFormTemplateService: ApiFormTemplateService, private localStorage: UtilLocalStorageService) {
    const savedUser = this.localStorage.getItem('user');
    this.user = savedUser;
    apiFormTemplateService.getFormTemplatesByOrg(this.user.CurrentOrg.ID).subscribe((templates) => {
      this.templateList.next(templates);
    });
  }

  saveAndClose() {
    this.callUpdateRecurrence.next(!this.callUpdateRecurrence.value);
    let newTaskTemplate = this.selectedTask;
    if (this.locationID) {
      newTaskTemplate = { ...newTaskTemplate, LocationID: this.locationID };
    }
    if (this.assignedUsers) {
      newTaskTemplate = { ...newTaskTemplate, AssignedUsers: this.assignedUsers };
    }
    if (this.assignedRoles) {
      newTaskTemplate = { ...newTaskTemplate, AssignedRoles: this.assignedRoles };
    }
    this.closeDialog.emit(newTaskTemplate);
  }
}
