import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  CreateLocationRequest,
  CreateLocationResponse,
  GetAllLocationsResponse,
  GetLocationListResponse,
  GetLocationResponse,
  Location,
  UpdateLocationRequest,
  BaseResponse,
} from '@xenon/api';

import { BaseApi } from '../environments/base-api';
import { UtilLocalStorageService } from '@xenon/utils';
import { LoggedInUser } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class ApiLocationService extends BaseApi {
  constructor(private http: HttpClient, private localStorageService: UtilLocalStorageService) {
    super();
  }

  /**
   * Used to get all locations.
   * @returns An observable containing an array of locations.
   */
  public getAllLocations(): Observable<Location[]> {
    const url = `${this.baseUrl}/location`;
    return this.http.get<GetAllLocationsResponse>(url).pipe(map((response: GetAllLocationsResponse) => response.LocationList));
  }

  /**
   * Used to get a single location by id.
   * @param id The organization's ID.
   */
  public getLocation(id: number): Observable<Location> {
    const url = `${this.baseUrl}/location/${id}`;
    return this.http.get<GetLocationResponse>(url).pipe(map((response: GetLocationResponse) => response.Location));
  }

  /**
   * Used to get all locations in an organization.
   * @param id The organization's ID.
   */
  public getLocationsByOrgId(id: number): Observable<Location[]> {
    const url = `${this.baseUrl}/locationbyorg/${id}`;
    return this.http.get<GetLocationListResponse>(url).pipe(map((response: GetLocationListResponse) => response.LocationList));
  }

  /**
   * Used to add a location.
   * @param location The new location to create.
   * @returns A promise containing the CreateLocationResponse.
   */
  public createLocation(location: CreateLocationRequest): Promise<CreateLocationResponse> {
    const url = `${this.baseUrl}/location`;
    return this.http.post<CreateLocationResponse>(url, location).toPromise();
  }

  /**
   * @param location The location to update.
   * @returns A promise with the BaseResponse.
   */
  public updateLocation(location: UpdateLocationRequest): Promise<BaseResponse> {
    const locationID = location.ID;
    const url = `${this.baseUrl}/location/${locationID}`;
    return this.http.patch<BaseResponse>(url, location).toPromise();
  }

  /**
   * @param id The location ID.
   * @returns An observable with the DeleteLocationResponse.
   */
  public deleteLocation(id: number): Observable<BaseResponse> {
    const url = `${this.baseUrl}/location/${id}`;
    return this.http.delete<BaseResponse>(url);
  }

  /**
   * @returns An observable with the timezone.
   */
  public getTimeZone(): Observable<any> {
    const url = `${this.baseUrl}/timezones`;
    return this.http.get<any>(url);
  }

  /**
   * @param locationID The location ID.
   * @returns An observable with the new token.
   */
  public switchLocation(locationID: number): Observable<LoggedInUser> {
    const url = `${this.baseUrl}/location/switch`;
    return this.http.post<LoggedInUser>(url, { LocationID: locationID });
  }
}
