import { Injectable } from '@angular/core';
import { DriveFile, FormTemplate, User } from '@xenon/api';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class FormBuilderService {
  newForm: FormTemplate = {
    OrgID: 0,
    LocationID: 0,
    Name: 'New Form',
    NamePlural: '',
    Description: '',
    DriveID: 0,
    FormTemplateItemList: [],
    AssignedRoles: [],
  };

  private currentFormSource = new BehaviorSubject<FormTemplate>(this.newForm);
  currentForm = this.currentFormSource.asObservable();

  // private lastSavedFormSource = new Subject<FormTemplate>();
  // lastSavedForm = this.currentFormSource.asObservable();

  private currentDriveListSource = new Subject<DriveFile[]>();
  currentDriveList = this.currentDriveListSource.asObservable();

  private user = new Subject<User>();
  currentUser = this.user.asObservable();

  constructor() {}

  setFormTemplate(form: any) {
    this.currentFormSource.next({
      AssignedRoles: form.AssignedRoles,
      Description: form.Description,
      DriveID: form.DriveID,
      FormCategoryID: form.FormCategoryID,
      FormTemplateItemList: form.FormTemplateItemList,
      ID: form.ID,
      LocationID: form.LocationID,
      Name: form.Name,
      NamePlural: form.NamePlural,
      OrgID: form.OrgID,
    });
  }

  setCurrentUser(user: User) {
    this.user.next(user);
  }

  setCurrentDriveList(driveList: DriveFile[]) {
    this.currentDriveListSource.next(driveList);
  }

  // setLastSavedForm(form: FormTemplate) {
  //   this.lastSavedFormSource.next(form);
  // }
}
