import { Component } from '@angular/core';
import { ApiUserService } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { take, filter } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
import { CurrentUserStorageService } from '../services/current-user-storage.service';

@Component({
  selector: 'xenon-main-layout',
  styles: [
    '.app-container { height: 100%; }',
    '.main-container { height: calc(100vh - 57px); }',
    'mat-drawer-content { overflow: hidden; margin-left: 72px !important; }',
  ],
  template: `
    <div class="app-container" *ngIf="loadedCurrentUser | async">
      <xenon-header></xenon-header>

      <mat-drawer-container class="main-container">
        <mat-drawer id="main-side-bar" mode="side" opened>
          <xenon-sidebar></xenon-sidebar>
        </mat-drawer>

        <mat-drawer-content>
          <router-outlet></router-outlet>
        </mat-drawer-content>
      </mat-drawer-container>
    </div>
  `,
})
export class MainLayoutComponent {
  public loadedCurrentUser: BehaviorSubject<boolean> = new BehaviorSubject(false);

  constructor(currentUserStorage: CurrentUserStorageService) {
    currentUserStorage
      .setCurrentUser()
      .pipe(
        filter((result) => result),
        take(1)
      )
      .subscribe((_) => {
        this.loadedCurrentUser.next(true);
      });
  }
}
