import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiTaskService, TaskTemplate, User, LoggedInUser } from '@xenon/api';
import { UtilLocalStorageService } from '@xenon/utils';
import { BehaviorSubject } from 'rxjs';

export interface DialogOptions {
  task: TaskTemplate;
  newTask: boolean;
  forceTemplate: 'Form' | 'Checklist';
}

export interface TemplateInfo {
  Name: string;
  ID: number;
}

const DISPLAY_TEMPLATES = [
  { Name: 'Form', ID: 1 },
  { Name: 'Checklist', ID: 2 },
];

@Component({
  selector: 'xenon-edit-task-template',
  templateUrl: './edit-task-template.component.html',
  styleUrls: ['./edit-task-template.component.scss'],
})
export class EditTaskTemplateComponent {
  selectedTask: TaskTemplate;
  taskType: BehaviorSubject<TemplateInfo> = new BehaviorSubject(null);
  taskTypeList: TemplateInfo[];

  saveData = false;
  private loggedInUser: LoggedInUser;

  public layoutOptions = {
    sideNavMenuOptions: [
      {
        selector: 'locations',
        title: 'Locations',
      },
      {
        selector: 'recurrence',
        title: 'Recurrence',
      },
      {
        selector: 'permissions',
        title: 'Permissions',
      },
    ],
    pageTitle: 'Edit Task Template',
    saveButton: true,
    navIsAnchor: true,
  };

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: DialogOptions,
    public dialogRef: MatDialogRef<EditTaskTemplateComponent>,
    private apiTaskService: ApiTaskService,
    private localStorageService: UtilLocalStorageService
  ) {
    this.loggedInUser = this.localStorageService.getItem('user');
    this.taskTypeList = data.forceTemplate ? [DISPLAY_TEMPLATES.find((template) => template.Name === data.forceTemplate)] : DISPLAY_TEMPLATES;
    if (data.newTask) {
      this.selectedTask = {
        OrgID: this.loggedInUser.CurrentOrg.ID,
        LocationID: null,
        TaskType: null,
        TaskTypeID: null,
        Name: null,
        Description: null,
        CreatedBy: this.loggedInUser.CurrentUser.ID,
      };
    } else {
      this.selectedTask = data.task;
      this.setTaskTemplateType(DISPLAY_TEMPLATES.find((template) => template.ID === this.selectedTask.TaskType));
    }
  }

  public createTaskTemplate(taskType: number): void {
    const newTask: TaskTemplate = {
      OrgID: this.loggedInUser.CurrentOrg.ID,
      LocationID: this.loggedInUser.CurrentLocation.ID,
      TaskType: taskType,
      TaskTypeID: 2,
      Name: this.selectedTask.Name,
      Description: this.selectedTask.Description,
      CreatedBy: this.loggedInUser.CurrentUser.ID,
    };

    this.apiTaskService.addTaskTemplate(newTask).subscribe((response) => {
      this.selectedTask = newTask;
      this.setTaskTemplateType(DISPLAY_TEMPLATES.find((template) => template.ID === this.selectedTask.TaskType));
    });
  }

  public closeDialog(newTaskTemplate: TaskTemplate): void {
    this.dialogRef.close(newTaskTemplate);
  }

  public goToFormField(location: string): void {
    const element = document.getElementById(location);
    element.scrollIntoView();
  }

  public setTaskTemplateType(template: TemplateInfo): void {
    if (template) {
      this.taskTypeList = [template];
      this.taskType.next(template);
      if (template.ID === 1) {
        this.layoutOptions.sideNavMenuOptions.push({
          selector: 'form-options',
          title: 'Form Options',
        });
      } else {
        this.layoutOptions.sideNavMenuOptions.push(
          {
            selector: 'assignments',
            title: 'Assignments',
          },
          {
            selector: 'subtask',
            title: 'Subtask',
          }
        );
        const deletedItem = this.layoutOptions.sideNavMenuOptions.splice(2, 1)[0];
        this.layoutOptions.sideNavMenuOptions.splice(3, 0, deletedItem);
      }
    }
  }

  public closeClicked(): void {
    this.dialogRef.close();
    this.saveData = false;
  }

  public saveClicked(): void {
    if (!this.taskType.value) {
      this.dialogRef.close();
    }
    this.saveData = true;
  }
}
