import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { Form, FormItemType } from '@xenon/api';
import { TaskFormItemType } from './task-form-item-type';

@Component({
  selector: 'xenon-task-form',
  styleUrls: ['./task-form.component.scss'],
  template: `
    <div *ngIf="data?.form?.FormItemList?.length">
      <h6>{{ data.form.Name }}</h6>
      <div *ngFor="let formItem of data.form.FormItemList">
        <xenon-task-form-field-checkbox [formItem]="formItem" *ngIf="formItem.FormItemType === types.Checkbox"></xenon-task-form-field-checkbox>
        <xenon-task-form-field-date [formItem]="formItem" *ngIf="formItem.FormItemType === types.Date"></xenon-task-form-field-date>
        <xenon-task-form-field-date-range [formItem]="formItem" *ngIf="formItem.FormItemType === types.DateRange"></xenon-task-form-field-date-range>
        <!--        <xenon-task-form-field-datetime-->
        <!--          [formItem]="formItem"-->
        <!--          *ngIf="formItem.FormItemType === types.DateTime"-->
        <!--        ></xenon-task-form-field-datetime>-->
        <!--      <xenon-task-form-field-datetime-range-->
        <!--        [formItem]="formItem"-->
        <!--        *ngIf="formItem.FormItemType === types.DateTimeRange"-->
        <!--      ></xenon-task-form-field-datetime-range>-->
        <xenon-task-form-field-file [formItem]="formItem" *ngIf="formItem.FormItemType === types.File"></xenon-task-form-field-file>
        <xenon-task-form-field-input [formItem]="formItem" *ngIf="formItem.FormItemType === types.Input"></xenon-task-form-field-input>
        <xenon-task-form-field-number [formItem]="formItem" *ngIf="formItem.FormItemType === types.Number"></xenon-task-form-field-number>
        <xenon-task-form-field-radio [formItem]="formItem" *ngIf="formItem.FormItemType === types.Radio"></xenon-task-form-field-radio>
        <xenon-task-form-field-textbox [formItem]="formItem" *ngIf="formItem.FormItemType === types.TextBox"></xenon-task-form-field-textbox>
        <!--      <xenon-task-form-field-time-->
        <!--        [formItem]="formItem"-->
        <!--        *ngIf="formItem.FormItemType === types.Time"-->
        <!--      ></xenon-task-form-field-time>-->
        <!--      <xenon-task-form-field-time-range-->
        <!--        [formItem]="formItem"-->
        <!--        *ngIf="formItem.FormItemType === types.TimeRange"-->
        <!--      ></xenon-task-form-field-time-range>-->
      </div>
    </div>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class TaskFormComponent {
  types = TaskFormItemType;
  formData: Form;

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA)
    public data: { form: Form; formItemTypes: FormItemType[] }
  ) {
    this.formData = data.form;
  }
}
