import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'daysOfWeek',
})
export class DaysOfWeekPipe implements PipeTransform {
  transform(value: unknown, ...args: unknown[]): unknown {
    let dayOfWeekName = '';
    switch (value) {
      case 0:
        dayOfWeekName = 'All';
        break;

      case 1:
        dayOfWeekName = 'Sun';
        break;

      case 2:
        dayOfWeekName = 'Mon';
        break;

      case 3:
        dayOfWeekName = 'Tues';
        break;

      case 4:
        dayOfWeekName = 'Wed';
        break;

      case 5:
        dayOfWeekName = 'Thurs';
        break;

      case 6:
        dayOfWeekName = 'Fri';
        break;

      case 7:
        dayOfWeekName = 'Sat';
        break;

      default:
        break;
    }
    return dayOfWeekName;
  }
}
