import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

export interface MenuOption {
  selector: string;
  title: string;
  icon?: string;
}

export interface LayoutOptions {
  sideNavMenuOptions?: MenuOption[];
  pageTitle?: string;
  pageTitleInput?: boolean;
  saveButton?: boolean;
  moreVertMenuOptions?: MenuOption[];
  navIsAnchor?: boolean;
}

@Component({
  selector: 'xenon-ui-side-nav-layout',
  styleUrls: ['./ui-side-nav-layout.component.scss'],
  template: ` <div *ngIf="layoutOptions" fxLayoutAlign="space-between center" class="background-primary color-white w-100 py-2">
      <span fxLayoutAlign="start center">
        <button mat-icon-button class="mx-2" (click)="backClick.emit()"><mat-icon>arrow_back</mat-icon></button>
        <mat-form-field *ngIf="layoutOptions.pageTitleInput; else display">
          <input matInput [value]="layoutOptions.pageTitle" (change)="titleInputChange.emit($event.value)" />
        </mat-form-field>
        <ng-template #display>
          <h6 class="mb-0">{{ layoutOptions.pageTitle }}</h6>
        </ng-template>
      </span>
      <span fxLayout="row">
        <button *ngIf="layoutOptions.saveButton" mat-button class="mx-2" (click)="saveClick.emit()">SAVE</button>
        <div *ngIf="layoutOptions.moreVertMenuOptions">
          <mat-icon [matMenuTriggerFor]="menu" class="mx-3 clickable">more_vert</mat-icon>
          <mat-menu #menu="matMenu">
            <button *ngFor="let item of layoutOptions.moreVertMenuOptions" mat-menu-item (click)="vertMenuClick.emit(item.selector)">
              <mat-icon *ngIf="item.icon" class="mr-2 material-icons-outlined">{{ item.icon }}</mat-icon
              >{{ item.title }}
            </button>
          </mat-menu>
        </div>
      </span>
    </div>

    <div fxLayout="row" class="page-height background-white">
      <div *ngIf="layoutOptions.sideNavMenuOptions" fxLayout="column" fxFlex="15%" class="h-100 side-nav">
        <mat-action-list class="pr-5" *ngIf="layoutOptions.navIsAnchor; else links">
          <button *ngFor="let option of layoutOptions.sideNavMenuOptions" mat-list-item class="nav-button" (click)="goToFormField(option.selector)">
            <mat-icon *ngIf="option.icon" class="mr-2 material-icons-outlined">{{ option.icon }}</mat-icon
            >{{ option.title }}
          </button>
        </mat-action-list>
        <ng-template #links>
          <mat-action-list class="pr-5">
            <button
              *ngFor="let option of layoutOptions.sideNavMenuOptions"
              class="nav-button"
              [ngClass]="{ 'selected-nav': currentTab.value === option.selector }"
              mat-list-item
              (click)="sideNavClicked(option.selector)"
            >
              <mat-icon *ngIf="option.icon" class="mr-2 material-icons-outlined">{{ option.icon }}</mat-icon
              >{{ option.title }}
            </button>
          </mat-action-list>
        </ng-template>
      </div>

      <div class="w-100 overflow-y-auto">
        <ng-content></ng-content>
      </div>
    </div>`,
})
export class UiSideNavLayoutComponent implements OnInit {
  @Input() layoutOptions: LayoutOptions = {
    sideNavMenuOptions: [],
    pageTitle: 'empty',
    pageTitleInput: false,
    saveButton: false,
    moreVertMenuOptions: [],
    navIsAnchor: false,
  };
  @Output() sideNavClick = new EventEmitter<string>();
  @Output() vertMenuClick = new EventEmitter<string>();
  @Output() saveClick = new EventEmitter();
  @Output() backClick = new EventEmitter();
  @Output() titleInputChange = new EventEmitter<string>();

  public currentTab: BehaviorSubject<string> = new BehaviorSubject(null);

  constructor() {}

  public ngOnInit(): void {
    if (this.layoutOptions.sideNavMenuOptions) {
      this.currentTab.next(this.layoutOptions.sideNavMenuOptions[0].selector);
    }
  }

  public sideNavClicked(value: string): void {
    this.currentTab.next(value);
    this.sideNavClick.emit(value);
  }

  public goToFormField(location: string): void {
    const element = document.getElementById(location);
    element.scrollIntoView();
  }
}
