import { Component, OnDestroy, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, forkJoin } from 'rxjs';
import { AppBase } from '../../../app-base';
import { ApiFormService, ApiKpiService, ApiTaskService, KPI, Task } from '@xenon/api';
import { Chart } from '@xenon/ui';
import { UtilDateToolsService, UtilLocalStorageService } from '@xenon/utils';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { TaskFormComponent } from '../../../shared/modules/task-form/task-form.component';
import * as Chartist from 'chartist';

@Component({
  selector: 'xenon-home',
  styleUrls: ['home.component.scss'],
  template: `
    <div class="page-header" fxLayout="row" fxLayoutAlign="space-between center">
      <h5 class="title">Task Completion</h5>
      <div>
        <xenon-ui-date-range-picker [dateRange]="dateRange" (rangeChange)="dateRangeChanged($event)"></xenon-ui-date-range-picker>
      </div>
    </div>

    <div class="page-body">
      <div class="my-5">
        <div *ngIf="kpi$ | async as kpi" class="ml-3" fxLayoutAlign="center center">
          <div class="ct-chart ct-perfect-fourth text-body1 set-size">
            <div class="complete-label text-center">
              <h4 class="mb-0">{{ kpi.PercentComplete + '%' }}</h4>
              <p class="mb-0 text-body1">Complete</p>
            </div>
          </div>
          <div fxLayout="column" class="ml-5">
            <div class="key-row my-1" fxLayoutAlign="start center">
              <div class="color-square-open"></div>
              <p class="text-body1 mx-3 mb-0">{{ kpi.PercentOpen + '%' }}</p>
              <p class="text-body1 mb-0">Open</p>
            </div>
            <div class="key-row my-1" fxLayoutAlign="start center">
              <div class="color-square-overdue"></div>
              <p class="text-body1 mx-3 mb-0">{{ kpi.PercentOverdue + '%' }}</p>
              <p class="text-body1 mb-0">Overdue</p>
            </div>
            <div class="key-row my-1" fxLayoutAlign="start center">
              <div class="color-square-complete"></div>
              <p class="text-body1 mx-3 mb-0">{{ kpi.PercentComplete + '%' }}</p>
              <p class="text-body1 mb-0">Complete</p>
            </div>
            <div class="key-row my-1" fxLayoutAlign="start center">
              <div class="color-square-expired"></div>
              <p class="text-body1 mx-3 mb-0">{{ kpi.PercentExpired + '%' }}</p>
              <p class="text-body1 mb-0">Expired</p>
            </div>
          </div>
        </div>
      </div>

      <xenon-task-list (itemClicked)="itemClicked($event)" [tasks]="tasks$ | async"></xenon-task-list>
      <xenon-checklist-panel [checkListData]="infoForChecklist$ | async" [currentUserID]="loggedInUser.CurrentUser?.ID" (refresh)="refreshData()">
      </xenon-checklist-panel>
    </div>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class HomeComponent extends AppBase implements OnDestroy {
  public tasks$: BehaviorSubject<Task[]> = new BehaviorSubject<Task[]>([]);
  public infoForChecklist$: BehaviorSubject<Task> = new BehaviorSubject<Task>(null);
  public kpi$: BehaviorSubject<KPI> = new BehaviorSubject<KPI>({
    PercentComplete: 0,
    PercentExpired: 0,
    PercentOverdue: 0,
    PercentOpen: 0,
  });

  public charts$: BehaviorSubject<{
    complete: Chart;
    overdue: Chart;
    expired: Chart;
    open: Chart;
  }> = new BehaviorSubject<{ complete: Chart; overdue: Chart; expired: Chart; open: Chart }>({
    complete: null,
    overdue: null,
    expired: null,
    open: null,
  });

  constructor(
    private localStorage: UtilLocalStorageService,
    private taskService: ApiTaskService,
    private kpiService: ApiKpiService,
    private formService: ApiFormService,
    private bottomSheet: MatBottomSheet
  ) {
    super(localStorage);

    this.loggedInUser = this.localStorage.getItem('user');
    this.refreshData();

    this.taskService.stringDateRange$.next(this.stringDateRange);
    this.taskService.organizationId$.next(this.organizationId);
    this.taskService.locationId$.next(this.locationId);

    this.kpiService.stringDateRange$.next(this.stringDateRange);
    this.kpiService.organizationId$.next(this.organizationId);
    this.kpiService.locationId$.next(this.locationId);
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub) => sub.unsubscribe());
  }

  public locationChanged(event: number): void {
    this.locationId = event;
    this.taskService.locationId$.next(event);
  }

  public dateRangeChanged(event: { start: Date; end: Date }): void {
    const startStringDate = UtilDateToolsService.convertToDateString(event.start);
    const endStringDate = UtilDateToolsService.convertToDateString(event.end);
    const stringDateRange = { start: startStringDate, end: endStringDate };
    this.kpiService.stringDateRange$.next(stringDateRange);
    this.taskService.stringDateRange$.next(stringDateRange);
  }

  public itemClicked(event: Task) {
    if (event.TaskType === 1) {
      const bottomSheet = forkJoin([this.formService.get(event.TaskTypeID), this.formService.getTypes()]).subscribe(([form, formItemTypes]) => {
        const bs = this.bottomSheet
          .open(TaskFormComponent, {
            data: { form: form, formItemTypes: formItemTypes },
            backdropClass: 'task-form-backdrop',
            panelClass: 'task-form-panel',
          })
          .afterDismissed()
          .subscribe(() => this.refreshData());
        this.subscriptions.push(bs);
      });
      this.subscriptions.push(bottomSheet);
    } else {
      // cloning so ngOnChanges is triggered when the user opens the same task twice.
      const clone = JSON.parse(JSON.stringify(event));
      this.infoForChecklist$.next(clone);
    }
  }

  public refreshData(): void {
    const taskServiceSub = this.taskService.search$.subscribe((r) => this.tasks$.next(r));
    this.subscriptions.push(taskServiceSub);

    const kpiServiceSub = this.kpiService.get$.subscribe((r) => {
      this.kpi$.next(r);

      // tslint:disable-next-line: no-unused-expression
      new Chartist.Pie(
        '.ct-chart',
        {
          labels: [' ', ' ', ' ', ' '],
          series: [r.PercentOpen, r.PercentOverdue, r.PercentComplete, r.PercentExpired],
        },
        {
          donut: true,
          donutWidth: 30,
          height: 200,
          donutSolid: true,
          showLabel: true,
        }
      );
    });
    this.subscriptions.push(kpiServiceSub);
  }

  private buildChart(percent: number): Chart {
    return {
      type: 'Pie',
      data: {
        series: [percent, 100 - percent],
      },
      options: {
        donut: true,
        height: 400,
        showLabel: false,
      },
    } as Chart;
  }
}
