import { Component, Input, ViewEncapsulation } from '@angular/core';
import { KPI } from '@xenon/api';
import { Chart } from '@xenon/ui';

@Component({
  selector: 'xenon-tasks-summary-donuts',
  styleUrls: ['./tasks-summary-donuts.component.scss'],
  template: `
    <div fxLayout="row" fxLayoutAlign="space-around" *ngIf="kpi && charts" class="summary-donuts">
      <div class="donut open">
        <div class="legend text-center">
          <div class="percent">{{ kpi.PercentOpen }}%</div>
          <div class="title">Open</div>
        </div>
        <xenon-ui-chart-donut [chart]="charts.open"> </xenon-ui-chart-donut>
      </div>
      <div class="donut complete">
        <div class="legend text-center">
          <div class="percent">{{ kpi.PercentComplete }}%</div>
          <div class="title">Complete</div>
        </div>
        <xenon-ui-chart-donut [chart]="charts.complete"> </xenon-ui-chart-donut>
      </div>
      <div class="donut overdue">
        <div class="legend text-center">
          <div class="percent">{{ kpi.PercentOverdue }}%</div>
          <div class="title">Overdue</div>
        </div>
        <xenon-ui-chart-donut [chart]="charts.overdue"> </xenon-ui-chart-donut>
      </div>
      <div class="donut missed">
        <div class="legend text-center">
          <div class="percent">{{ kpi.PercentExpired }}%</div>
          <div class="title">Expired</div>
        </div>
        <xenon-ui-chart-donut [chart]="charts.expired"> </xenon-ui-chart-donut>
      </div>
    </div>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class TasksSummaryDonutsComponent {
  @Input() kpi: KPI;
  @Input() charts: { complete: Chart; overdue: Chart; expired: Chart; open: Chart };
}
