import { Component, Input, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { UiFormFieldSelect } from '../../models/ui-form-field-select';

@Component({
  selector: 'xenon-ui-form-field-select',
  styleUrls: ['./ui-form-field-select.component.scss'],
  template: `
    <mat-form-field [formGroup]="fieldFormGroup">
      <mat-label>{{ fieldOptions.label }}</mat-label>
      <mat-select formControlName="{{ fieldOptions.name }}">
        <mat-option *ngFor="let o of fieldOptions.options" [value]="o.value">{{ o.label }}</mat-option>
      </mat-select>
    </mat-form-field>
  `,
})
export class UiFormFieldSelectComponent implements OnInit {
  @Input() fieldFormGroup: FormGroup;
  @Input() fieldOptions: UiFormFieldSelect;

  ngOnInit(): void {
    const selectedQuery = this.fieldOptions.options.filter((o) => o.selected);
    const selected = selectedQuery.length ? selectedQuery[0] : null;
    if (selected) {
      this.fieldFormGroup.controls[this.fieldOptions.name].setValue(selected.value);
    }

    this.fieldFormGroup.controls[this.fieldOptions.name].valueChanges.subscribe((v) => {
      this.fieldOptions.value = v;
    });
  }
}
