import { Component } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'xenon-unsaved-changes-modal',
  template: `
    <div id="unsavedChangesModal">
      <p mat-dialog-title class="text-subtitle1">There are unsaved changes</p>
      <div mat-dialog-content>
        <p class="text-body2">Would you like to save your changes before leaving this page?</p>
      </div>
      <div class="submit-buttons" mat-dialog-actions fxLayoutAlign="space-between">
        <button type="submit" (click)="onSave()" class="submit-button" color="primary" mat-flat-button cdkFocusInitial>
          Save
        </button>
        <button mat-stroked-button (click)="onCancel()">
          Don't save
        </button>
      </div>
    </div>
  `,
  styleUrls: ['./unsaved-changes-modal.component.scss'],
})
export class UnsavedChangesModalComponent {
  constructor(public dialog: MatDialog, public dialogRef: MatDialogRef<UnsavedChangesModalComponent>) {}

  public onSave() {
    this.dialogRef.close(true);
  }

  public onCancel() {
    this.dialogRef.close(false);
  }
}
