import { Component, ElementRef, EventEmitter, Input, Output, Renderer2, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { UtilWindowRefService } from '@xenon/utils';
import { ApiDriveService, DriveItem } from '@xenon/api';
import { TextFileComponent } from '../../../../shared/components/text-file/text-file.component';
import { LeaveAppDialogComponent } from '../../../../shared/components/leave-app-dialog/leave-app-dialog.component';

@Component({
  selector: 'xenon-drive-explorer',
  styleUrls: ['./drive-explorer.component.scss'],
  template: `
    <div class="drive-list" *ngIf="driveItems">
      <div style="width: {{ driveItems.length * 336 }}px; display: inline-block;">
        <div class="folder" *ngFor="let driveItem of driveItems" (drop)="drop($event)" (dragover)="allowDrop($event)">
          <div
            class="folder-item clickable"
            [class.active]="selectedDriveItem?.id === item.id"
            fxLayout="row"
            *ngFor="let item of driveItem"
            (drop)="drop($event, item)"
            (dragover)="allowDrop($event)"
            draggable="true"
            (dragstart)="drag(item)"
            (click)="setSelected(item)"
            [class.selected]="selectedIndex === item.id"
          >
            <div class="type" (click)="clickDriveItem(item)" fxFlex="72px" fxLayoutAlign="center center">
              <mat-icon color="primary" class="circle">{{ item.icon }}</mat-icon>
            </div>

            <span (click)="clickDriveItem(item)" fxFlex fxLayoutAlign="space-between center" class="handle-overflow w-100">
              {{ item.name }}
            </span>

            <div fxFlex="72px" fxLayoutAlign="center center" mat-icon-button [matMenuTriggerFor]="menu">
              <mat-icon>more_vert</mat-icon>
            </div>

            <mat-menu #menu="matMenu">
              <xenon-drive-vert-menu [item]="item" (refresh)="refreshDrive.emit()"></xenon-drive-vert-menu>
            </mat-menu>
          </div>
        </div>
      </div>

      <div #content id="drive-content"></div>
    </div>
  `,
})
export class DriveExplorerComponent {
  @Input() driveItems: DriveItem[][] = [];
  @Output() driveItemClicked = new EventEmitter();
  @Output() moveDriveItem = new EventEmitter();
  @Output() showError = new EventEmitter();
  @Output() refreshDrive = new EventEmitter();

  @ViewChild('content') content: ElementRef;
  public selectedDriveItem: DriveItem;
  public agreed = false;

  private draggedItem: DriveItem;

  constructor(private renderer: Renderer2, private windowRefService: UtilWindowRefService, private driveService: ApiDriveService, public dialog: MatDialog) {}

  public drop(event: DragEvent, moveTo: DriveItem) {
    event.preventDefault();

    if (moveTo) {
      // if item is not already in folder, then move it
      if (this.draggedItem.details.ParentID !== moveTo.id) {
        this.moveDriveItem.emit({
          id: this.draggedItem.id,
          parentId: moveTo.id,
          parentItem: moveTo,
        });
      }

      this.selectedDriveItem = null; // prevents multiple moves from happening
    } else if (this.selectedDriveItem) {
      // if item is not already in folder, then move it
      if (this.draggedItem.details.ParentID !== this.selectedDriveItem.id) {
        this.moveDriveItem.emit({
          id: this.draggedItem.id,
          parentId: this.selectedDriveItem.id,
          parentItem: this.selectedDriveItem,
        });
      } else {
        this.moveDriveItem.emit({ id: this.draggedItem.id, parentId: 0 });
      }
    }
  }

  public allowDrop(event: DragEvent) {
    event.preventDefault();
  }

  public drag(item) {
    this.draggedItem = item;
  }

  public setSelected(driveItem: DriveItem) {
    this.selectedDriveItem = driveItem;
  }

  public clickDriveItem(item: DriveItem) {
    if (!item.active) {
      this.clearContent();
      switch (item.details.ContentType) {
        case 1:
          // folder
          break;
        case 2:
          // text
          const textFileDialogRef = this.dialog.open(TextFileComponent, {
            data: { driveItem: item },
            panelClass: 'full-page-dialog',
          });

          textFileDialogRef.afterClosed().subscribe(() => {
            this.refreshDrive.emit();
          });

          // const p: HTMLParagraphElement = this.renderer.createElement('p');
          // p.innerHTML = item.details.Content;
          // this.renderer.appendChild(this.content.nativeElement, p);
          break;
        case 3:
          // // Note: We cannot use iframes
          // // https://stackoverflow.com/questions/60494390/refused-to-display-url-in-a-frame-because-it-set-x-frame-options-to-sameori
          // const iframe: HTMLIFrameElement = this.renderer.createElement('iframe');
          // iframe.src = item.details.ContentPath;
          // this.renderer.appendChild(this.content.nativeElement, iframe);
          const dialogRef = this.dialog.open(LeaveAppDialogComponent, {
            width: '250px',
            data: { agreed: this.agreed },
          });

          dialogRef.afterClosed().subscribe((result) => {
            this.agreed = result;
            if (this.agreed) {
              this.windowRefService.nativeWindow.open(item.details.ContentPath, '_blank');
            }
          });
          break;
        case 4:
          // file
          break;
      }
      this.driveItemClicked.emit(item);
    }
  }

  private clearContent(): void {
    this.content.nativeElement.childNodes.forEach((child) => this.renderer.removeChild(this.content.nativeElement, child));
  }
}
