import { Subscription } from 'rxjs';
import { UtilLocalStorageService, UtilMomentService } from '@xenon/utils';
import { User, LoggedInUser } from '@xenon/api';
import * as moment from 'moment';

const today = moment().toDate();
export const DEFAULT_STRING_DATE_RANGE = {
  start: UtilMomentService.new(today).format('YYYY-MM-DD'),
  end: UtilMomentService.new(today).add(1, 'months').format('YYYY-MM-DD'),
};

export const DEFAULT_DATE_RANGE = {
  start: UtilMomentService.new(today).toDate(),
  end: UtilMomentService.new(today).add(1, 'months').toDate(),
};

export class AppBase {
  loggedInUser: LoggedInUser;
  organizationId: number;
  locationId: number;
  stringDateRange: { start: string; end: string } = DEFAULT_STRING_DATE_RANGE;
  dateRange: { start: Date; end: Date } = DEFAULT_DATE_RANGE;

  subscriptions: Subscription[] = [];

  constructor(localStorage: UtilLocalStorageService) {
    this.loggedInUser = localStorage.getItem('user');
    this.organizationId = this.loggedInUser.CurrentOrg.ID;
    this.locationId = this.loggedInUser.CurrentLocation.ID;
  }
}
