import { EventEmitter, Output, SimpleChanges } from '@angular/core';
import { Component, Input, OnChanges, ViewChild } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Form } from '@xenon/api';
import * as moment from 'moment';

@Component({
  selector: 'xenon-forms-list',
  styleUrls: ['./forms-list.component.scss'],
  templateUrl: './forms-list.component.html',
})
export class FormsListComponent implements OnChanges {
  public hideTable = true;
  @Input() forms: Form[] = [];
  @Output() openEditForm = new EventEmitter<Form>();
  @ViewChild(MatSort, { static: false }) sort: MatSort;

  public displayedColumns: string[] = ['Organization', 'Name', 'CreatedAt', 'ItemsComplete', 'ItemsExpired', 'ItemsRemaining', 'ItemsTotal'];
  public dataSource: MatTableDataSource<any> = new MatTableDataSource([]);

  constructor() {}

  ngOnChanges(changes: SimpleChanges) {
    if (!changes.firstChange && this.forms?.length > 0) {
      this.dataSource = new MatTableDataSource(this.forms);
      this.dataSource.sort = this.sort;
      this.hideTable = false;
    } else this.hideTable = true;
  }

  public editForm(form: Form) {
    this.openEditForm.emit(form);
  }

  public sortData(sortBy: { active: string; direction: string }) {
    const isAscending = sortBy.direction === 'asc';

    switch (sortBy.active) {
      case 'Name':
      case 'Organization':
        this.sortByString(sortBy.active, isAscending);
        break;
      case 'ItemsComplete':
      case 'ItemsExpired':
      case 'ItemsRemaining':
      case 'ItemsTotal':
        this.sortByNumber(sortBy.active, isAscending);
        break;
      case 'CreatedAt':
        this.sortByDate(sortBy.active, isAscending);
        break;
    }

    this.dataSource = new MatTableDataSource(this.forms);
  }

  private sortByString(property: string, isAscending: boolean) {
    this.forms = this.forms.sort((a, b) => (a[property]?.toLowerCase() < b[property]?.toLowerCase() ? -1 : 1) * (isAscending ? 1 : -1));
  }

  private sortByNumber(property: string, isAscending: boolean) {
    this.forms = this.forms.sort((a, b) => {
      if (isAscending) return a[property] - b[property];
      else return b[property] - a[property];
    });
  }

  private sortByDate(property: string, isAscending: boolean) {
    this.forms = this.forms.sort((a, b) => {
      const c = moment(a[property]).toDate().getTime();
      const d = moment(b[property]).toDate().getTime();
      return isAscending ? c - d : d - c;
    });
  }
}
