import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface ModalData {
  folderName: string;
}

@Component({
  selector: 'xenon-add-folder-modal',
  template: `
    <h6 mat-dialog-title>New Folder</h6>
    <div mat-dialog-content>
      <mat-form-field>
        <mat-label>Untitled Folder</mat-label>
        <input matInput [(ngModel)]="data.folderName" />
      </mat-form-field>
    </div>
    <div mat-dialog-actions>
      <button mat-button (click)="onNoClick()">CANCEL</button>
      <button mat-button [mat-dialog-close]="data.folderName" cdkFocusInitial>
        CREATE
      </button>
    </div>
  `,
})
export class AddFolderModalComponent {
  constructor(public dialogRef: MatDialogRef<AddFolderModalComponent>, @Inject(MAT_DIALOG_DATA) public data: ModalData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
}
