import { Injectable } from '@angular/core';
import { BaseApi } from '../environments/base-api';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AddContentRequest, Drive, DriveFile, AddContentResponse, UploadFileResponse, DriveItem } from '../models/drive';
import { DriveContentType } from '../models/drive-content-type';
import { BaseResponse } from '../models/base-response';

@Injectable({
  providedIn: 'root',
})
export class ApiDriveService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  public getDrivesByOrg(orgId: number): Observable<DriveItem[][]> {
    const url = `${this.baseUrl}/drive/org/${orgId}`;
    return this.http.get<{ DriveList: Drive[] }>(url).pipe(
      map((response: { DriveList: Drive[] }) => response.DriveList),
      map((response: Drive[]) => {
        const drives = [];
        const icons = {
          '1': 'folder',
          '2': 'text_snippet',
          '3': 'insert_link',
          '4': 'insert_drive',
        };
        if (response) {
          response.forEach((drive: Drive) => {
            const driveItem: DriveItem = {
              id: drive.ID,
              name: drive.Name,
              active: false,
              icon: icons[drive.ContentType],
              starred: drive.StarredInd,
              details: drive,
            };
            drives.push(driveItem);
          });
        }
        return [drives];
      })
    );
  }

  public getDriveFolderContent(driveId: number, current: DriveItem[][]): Observable<DriveItem[][]> {
    const url = `${this.baseUrl}/drive/folder/${driveId}`;
    return this.http.get<{ DriveList: Drive[] }>(url).pipe(
      map((response: { DriveList: Drive[] }) => response.DriveList),
      map((response: Drive[]) => {
        const drives = [];
        const icons = {
          '1': 'folder',
          '2': 'text_snippet',
          '3': 'insert_link',
          '4': 'insert_drive',
        };
        if (response) {
          response.forEach((drive: Drive) => {
            const driveItem: DriveItem = {
              id: drive.ID,
              name: drive.Name,
              active: false,
              icon: icons[drive.ContentType],
              starred: drive.StarredInd,
              details: drive,
            };
            drives.push(driveItem);
          });
        }
        current.push(drives);
        return current;
      })
    );
  }

  public getDriveContentTypes(): Observable<DriveContentType[]> {
    const url = `${this.baseUrl}/drivecontenttypelist`;
    return this.http
      .get<{ DriveContentTypes: DriveContentType[] }>(url)
      .pipe(map((response: { DriveContentTypes: DriveContentType[] }) => response.DriveContentTypes));
  }

  public getDrivesByLocation(locationId: number): Observable<Drive[]> {
    const url = `${this.baseUrl}/drive/location/${locationId}`;
    return this.http.get<{ DriveList: Drive[] }>(url).pipe(map((response: { DriveList: Drive[] }) => response.DriveList));
  }

  public getDriveFilesByOrg(orgId: number): Observable<DriveFile[]> {
    const url = `${this.baseUrl}/drive/org/${orgId}`;
    return this.http.get<{ DriveList: DriveFile[] }>(url).pipe(map((response: { DriveList: DriveFile[] }) => response.DriveList));
  }

  public getDriveFileByID(ID: number): Observable<Drive> {
    const url = `${this.baseUrl}/drive/file/${ID}`;
    return this.http.get<{ Drive: Drive }>(url).pipe(map((response) => response.Drive));
  }

  public getDriveFolderFiles(driveId: number, current: DriveItem[][]): Observable<DriveItem[][]> {
    const url = `${this.baseUrl}/drive/folder/${driveId}`;
    return this.http.get<{ DriveList: Drive[] }>(url).pipe(
      map((response: { DriveList: Drive[] }) => response.DriveList),
      map((response: Drive[]) => {
        const drives = [];
        const icons = {
          '1': 'folder',
          '2': 'text_snippet',
          '3': 'insert_link',
          '4': 'insert_drive',
        };
        if (response) {
          response.forEach((drive: Drive) => {
            const driveItem: DriveItem = {
              id: drive.ID,
              name: drive.Name,
              active: false,
              icon: icons[drive.ContentType],
              starred: drive.StarredInd,
              details: drive,
            };
            drives.push(driveItem);
          });
        }
        current.push(drives);
        return current;
      })
    );
  }

  /**
   * This methods completes both steps required to upload a photo.
   * 1. Saves the file information.
   * 2. Uploads the actual photo.
   *
   * Note: If you want to add a file to a specific folder,
   *       include the "ParentID" property (id of the folder)
   *       in the request object.
   * @param fileInfo Information on the file to upload.
   * @returns A promise that resolves true if success, false if failure.
   */
  public addFile(fileInfo: AddContentRequest, file: File): Promise<boolean> {
    const success = true;
    console.log('hit add');
    return new Promise((resolve, reject) => {
      this.saveContentInfo(fileInfo)
        .then((response: AddContentResponse) => {
          if (response.Status === 'error') {
            reject(!success);
          } else {
            this.uploadFile(response.NewDrive, file)
              .then((result: UploadFileResponse) => {
                if (result.Status === 'error') reject(!success);
                else resolve(success);
              })
              .catch((err) => {
                reject(!success);
              });
          }
        })
        .catch((err) => {
          reject(!success);
        });
    });
  }

  /**
   * Add the file/folder name and information.
   *   Note: Doesn't add the actual file, just the file info.
   * @param info The information needed to add the content.
   * @returns A promise containing the AddContentResponse.
   */
  public saveContentInfo(info: AddContentRequest): Promise<AddContentResponse> {
    const url = `${this.baseUrl}/drive`;
    return this.http.post<AddContentResponse>(url, info).toPromise();
  }

  /**
   * Gets the blob information so a user can download a file.
   * @param urlToFile The url where that file is stored on our servers.
   * @returns A promise containing the Blob response.
   */
  public downloadFile(urlToFile: string): Promise<Blob> {
    const httpOptions = {
      responseType: 'blob' as 'json',
    };

    return this.http.get<Blob>(urlToFile, httpOptions).toPromise();
  }

  /**
   * Used to delete a file or folder.
   * @param id The id of the file or folder to delete.
   * @returns A promise containing the base response.
   */
  public deleteDriveItem(id: number): Promise<BaseResponse> {
    const url = `${this.baseUrl}/drive/${id}`;
    return this.http.delete<BaseResponse>(url).toPromise();
  }

  /**
   * Rename a drive item (file/folder).
   * @param id The id of the file or folder to rename.
   * @param name: The new name.
   * @returns A promise containing the base response.
   */
  public renameDriveItem(id: number, name: string): Promise<BaseResponse> {
    const url = `${this.baseUrl}/drive/${id}`;
    const request = { ID: id, Name: name };
    return this.http.patch<BaseResponse>(url, request).toPromise();
  }

  /**
   * Move drive item to different folder.
   * @param id The id of the file or folder to move.
   * @param parentId The id of the folder to move the drive item to (0 = root folder).
   * @returns A promise containing the base response.
   */
  public moveDriveItem(id: number, parentId: number): Promise<BaseResponse> {
    const url = `${this.baseUrl}/drive/${id}`;
    const request = { ID: id, ParentID: parentId };
    return this.http.patch<BaseResponse>(url, request).toPromise();
  }

  /**
   * update a drive item file.
   * @param id The id of the file or folder to rename.
   * @param newDriveItem: The changes of drive to be made.
   * @returns A promise containing the base response.
   */
  public updateDriveItem(id: number, newDriveItem: {}): Promise<BaseResponse> {
    const url = `${this.baseUrl}/drive/${id}`;
    const request = { ...newDriveItem, ID: id };
    return this.http.patch<BaseResponse>(url, request).toPromise();
  }

  private uploadFile(driveID: number, file: File): Promise<UploadFileResponse> {
    const url = `${this.baseUrl}/uploaddrivefile/${driveID}`;

    const formData = new FormData();
    formData.append('driveFile', file, file.name);

    return this.http.post<UploadFileResponse>(url, formData).toPromise();
  }
}
